<template>
  <div class="channel-item" @click="toDetail(item['_id'])">
    <div class="channel-item-img">
      <van-image :src="getCover()" fit="fill">
        <template v-slot:loading>
          <van-loading type="spinner" size="20" />
        </template>
        <template v-slot:error>
          <img :src="require('_nc/assets/styles/themes/img/file-read-18688.png')" style="width: 100%; height: 100%" />
        </template>
      </van-image>
    </div>
    <div class="channel-item-content">
      <div class="van-multi-ellipsis--l3 channel-title">{{ item["nc_pd_pddata.title"] }}</div>
      <div class="channel-label-box">
        <div class="channel-label" v-for="(one, index) in label" :key="index">{{ one }}</div>
      </div>
      <div class="channel-icon-box">
        <div class="channel-icon"><dsf-icon name="yanjing"></dsf-icon>{{ item["nc_pd_pddata.djs"] || 0 }}</div>
        <div class="channel-icon"><dsf-icon name="tuijianweixuanzhong"></dsf-icon>{{ item["nc_pd_pddata.dys"] || 0 }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "channelItem",
  data() {
    return {
      label: [],
    };
  },
  props: {
    detailUrl: {
      type: String,
      default: "",
    },
    item: {
      type: Object,
      default: function () {
        return {};
      },
    },
  },
  computed: {},
  components: {},
  created: function () {
    if (this.item["nc_pd_pddata.label_text"]) {
      this.label = this.item["nc_pd_pddata.label_text"].split("^");
    }
  },
  mounted: function () {},
  methods: {
    getCover: function () {
      if (!this.item["nc_pd_pddata.cover"]) {
        return "_nc/assets/styles/themes/img/file-read-18688.png";
      }
      let files = dsf.getUploadFileInfo(this.item["nc_pd_pddata.cover"]);
      return dsf.url.getWebPath(files[0].relativePath, $$webRoot.nc);
    },
    toDetail(id) {
      this.$router.push({
        path: this.detailUrl,
        query: {
          id: id,
        },
      });
    },
  },
};
</script>

<style scoped></style>
