//注入按钮
import "./sysButton";
import "../api";
// nc推荐组件
import { DsfMobileNcRecommend } from "./recommend";
import { DsfMobileNcPlayer, DsfMobileNcCoursePlayer, DsfMobileNcCoursePlayerList } from "./player";
import { DsfMobileNcSpecilaDetail } from "./recommend/special";
import { DsfMobileNcTeacherDetail } from "./recommend/teacher";
import { DsfMobileNcNewsDetail } from "./recommend/news";
import { DsfMobileNcChannelDetail } from "./recommend/channel";
import { DsfMobileNcLiveSign } from "./recommend/live";
import { DsfChoiceSidePopup, DsfTreeSelectPopup } from "./recommend/dsfChoiceSidePopup";
import { DsfMobileNcDataGrid } from "./recommend/dsfDataGrid";
import { DsfSidePopup } from "./recommend/dsfSidePopup";
import { DsfMobileSort } from "./recommend/dsfFilter";
import { DsfMobileNcGlobalSearch } from "./recommend/globalSearch";
import baseComponents from "./base";
import exam from "./components/exam";
import learn from "./components/learn";
import { coursePlayer } from "./components/course";
import { mineInfo, userBack } from "./components/mine";
import { livePlay, liveList } from "./components/live";
import { teacherCard, teacherDetail } from "./components/teacher";
import { channelDetail } from "./components/channel";
import { specialDetail } from "./components/special";
import globalSearch from "./components/gloabalSearch";
const components = [
  DsfMobileNcRecommend,
  DsfMobileNcPlayer,
  DsfMobileNcCoursePlayer,
  DsfMobileNcCoursePlayerList,
  DsfMobileNcSpecilaDetail,
  DsfMobileNcTeacherDetail,
  DsfMobileNcNewsDetail,
  DsfMobileNcChannelDetail,
  DsfMobileNcLiveSign,
  DsfChoiceSidePopup,
  DsfTreeSelectPopup,
  DsfMobileNcDataGrid,
  DsfSidePopup,
  DsfMobileSort,
  DsfMobileNcGlobalSearch,
  ...baseComponents,
  ...exam,
  ...learn,
  ...globalSearch,
  coursePlayer,
  mineInfo,
  userBack, //用户反馈
  livePlay,
  liveList,
  teacherCard,
  teacherDetail,
  channelDetail,
  specialDetail,
];

function install(Vue) {
  components.forEach(it => {
    Vue.component(it.name, it);
    if (!window.$components["nc"]) {
      window.$components["nc"] = {};
    }
    window.$components["nc"][it.name] = it;
  });
}

export default install;

export { install };