<template>
  <dsf-player-mini
    ref="player"
    :is-show="isShow"
    :auto-play="autoPlay"
    :cover-img="coverImage"
    :title="courseTitle"
    :sub-title="userName"
    v-if="$store.state.ncMedia.isUseMini && videoSrc() && seekTime !== null"
    :seek-time="seekTime"
    :videoSource="videoSrc()"
    @playerPlay="playerPlay"
    @playerPause="playerPause"
    @playerPlayingTime="playerPlayingTime"
    @playerEnded="playerEnded"
    @playerWaiting="playerWaiting"
    @close="close"
    @hide="hide"
    @show="show"
    @go="toDetail"
  >
  </dsf-player-mini>
</template>
<script>
import { dsfPlayerMini } from "../dsfPlayer";
import * as storeKey from "_nc/store/funName";
export default {
  name: "App",
  components: { dsfPlayerMini },
  data() {
    return {
      timer: null,
    };
  },
  props: {
    isShow: {
      type: Boolean,
      default: false,
    },
    autoPlay: {
      type: Boolean,
      default: true,
    },
  },
  computed: {
    player() {
      return this.$refs.player;
    },
    course() {
      return this.$store.state.ncMedia.course;
    },
    //课件ID
    playVideoId() {
      return this.$store.state.ncMedia.playVideoId || "";
    },
    playVideoTime() {
      return this.$store.state.ncMedia.playVideoTime || "00:00:00";
    },
    //视频地址信息包含高清、标清、超清
    videoSource() {
      return this.$store.state.ncMedia.src;
    },
    //视频地址
    videoSrc() {
      return () => {
        if (dsf.isUnDef(this.videoSource)) {
          return "";
        } else {
          let url = "";
          let mp3 = this.videoSource.mp3;
          url = mp3.relative;
          if (!url) {
            let sd = this.videoSource.sd;
            url = dsf.url.getWebPath(sd.relative);
          } else {
            url = dsf.url.getWebPath(url);
          }
          return url;
        }
      };
    },
    //当前播放时间
    playingTime() {
      return this.$store.state.ncMedia.playingTime;
    },
    //当前视频进度情况
    progressInfo() {
      return this.$store.state.ncMedia.progressInfo;
    },
    //课程课件数据
    courseData() {
      return this.$store.state.ncMedia.course;
    },
    //视频初始化播放定位时间
    seekTime() {
      return this.$store.state.ncMedia.playingTime;
    },
    courseTitle() {
      if (dsf.isUnDef(this.$store.state.ncMedia.course) || dsf.isUnDef(this.$store.state.ncMedia.course.title)) {
        return "";
      }
      return this.$store.state.ncMedia.course.title;
    },
    coverImage() {
      let cover = dsf.getUploadFileInfo(this.$store.state.ncMedia.course.cover);
      if (dsf.type(cover) == "array") {
        return dsf.url.getWebPath(cover[0].relativePath);
      } else {
        return cover.relativePath ? dsf.url.getWebPath(cover.relativePath) : "";
      }
    },
    userName() {
      if (!this.$store.state.ncMedia.course) {
        return "";
      } else {
        let js = this.$store.state.ncMedia.course.skjsnow[0];
        if (js) {
          return js.jsname;
        }
        return "";
      }
    },
  },
  methods: {
    formatSeconds(value) {
      let time = dsf.date.secondsToTime(value / 1000);
      console.log(time.hours + ":" + time.minutes + ":" + time.seconds);
      return time.hours + ":" + time.minutes + ":" + time.seconds;
    },
    //格式化进度时间
    formatSeek(t) {
      if (!t) t = "00:00:00";
      t = t.split(":");
      var s = t[0] * 60 * 60 + t[1] * 60 + t[2] * 1;
      return s;
    },
    playerPlay(player) {
      this.clearTimer();
      this.timer = window.setInterval(() => {
        this.addSpeedProgress();
      }, 1000 * 10);
    },
    playerPause(player) {
      this.$store.commit('media/Media_MiniPlaying', false);
      this.clearTimer();
    },
    playerPlayingTime(t) {
      this.$store.commit('media/Media_MiniPlaying', true);
      this.$store.commit('media/Media_UpdatePlayTime', Math.ceil(t));
    },
    playerEnded() {
      this.$store.commit('media/Media_MiniPlaying', false);
      this.clearTimer();
      this.addSpeedProgress();
      // this.$store.commit(storeKey.Media_UseMini, false);
    },
    playerWaiting() {
      // alert('ddd')
      // console.log("等待")
    },
    //添加播放记录
    addSpeedProgress() {
      let that = this;
      let params = {};
      this.$store
        .dispatch('media/Media_PostProgress', params)
        .then(function (res) {
          if (res.success) {
            console.log(res);
            if (res.state == "61101") {
              dsf.layer.message(res.message);
            }
          } else {
            dsf.layer.message(res.message, false);
          }
        })
        .catch(function (err) {
          dsf.layer.message("添加播放记录服务器异常", false);
        });
    },
    clearTimer() {
      if (this.timer) {
        window.clearInterval(this.timer);
        this.timer = null;
      }
    },
    close() {
      this.$store.commit('media/Media_UseMini', false);
    },
    hide() {
      this.$store.commit('media/Media_MiniHide', true);
    },
    show() {
      this.$store.commit('media/Media_MiniHide', false);
    },
    toDetail() {
      this.$router.push({
        path: "/player/" + this.course.kcid + "/2",
      });
    },
  },
  destroyed() {
    this.clearTimer();
    this.$store.commit('media/Media_SetSeekTime', null);
  },
  deactivated() {
    this.clearTimer();
    this.$store.commit('media/Media_SetSeekTime', null);
  },
};
</script>
