<template>
  <div class="dsf_cardItemByExam" v-if="visible">
    <div class="content" v-if="isDesign">
      <div class="item-wrap">
        <!-- <div class="img-box" :style="{ width: imgWidth, height: imgHeight, margin: imgMargin }">
          <van-image src="https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg" fit="cover"></van-image>
           <el-image :src="data['cover'] | imgFormat" fit="cover"></el-image>
        </div> -->
        <p class="title ell2 margin-bottom-5">《XXXX》考试</p>
        <p class="time margin-bottom-5">{{ formatterDate("2020.3.4", "yyyy-mm-dd HH:ii") }}~{{ formatterDate("2020.3.15", "yyyy-mm-dd HH:ii") }}</p>
        <div class="detail-cell-1">
          <span>考试总分：0分</span>
          <span>及格分数：0分</span>
        </div>
        <div class="detail-cell-2 margin-bottom-5">
          <span>考试次数：0次</span>
          <span>已考次数：0次</span>
          <span>剩余次数：0次</span>
        </div>
      </div>
      <van-button class="start" :disabled="false" block @click="goDetail">开始考试</van-button>
      <div class="score">
        <p class="score-num">0分</p>
        <p class="txt">最高成绩</p>
      </div>
    </div>

    <div class="content" v-else>
      <div class="item-wrap">
        <p class="title ell2 margin-bottom-5">《{{ data["title"] }}》考试</p>
        <p class="time margin-bottom-5" v-if="data['startTime']">{{ formatterDate(data["startTime"], "yyyy-mm-dd HH:ii") }}~{{ formatterDate(data["endTime"], "yyyy-mm-dd HH:ii") }}</p>
        <div class="detail-cell-1">
          <span>考试总分：{{ data["total"] || 0 }}分</span>
          <span>及格分数：{{ data["pass"] || 0 }}分</span>
        </div>
        <div class="detail-cell-2 margin-bottom-5">
          <span>考试次数：{{ data["examNum"].includes("无限") ? data["examNum"] : (data["examNum"] || 0) + "次" }}</span>
          <span>已考次数：{{ data["already"] || 0 }}次</span>
          <span>剩余次数：{{ data["residue"].includes("无限") ? data["residue"] : (data["residue"] || 0) + "次" }}</span>
        </div>
      </div>
      <van-button class="start" :disabled="data['residue'].includes('无限') || data['residue'] != 0 || data['isDo'] == 1 ? false : true" block @click="goDetail(data)">开始考试</van-button>
      <div class="score" v-if="data['heightScore']">
        <p class="score-num">{{ data["heightScore"] || 0 }}分</p>
        <p class="txt">最高成绩</p>
      </div>
    </div>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobileNcCardItemByExamOne",
  ctrlCaption: "考试卡片",
  mixins: [$mixins.card],
  props: {
    visible: {
      type: Boolean,
      default: true,
    },
    detailUrl: {
      type: String,
      default: "",
    },
    examAppendUrl: {
      type: String,
      default: "exam/api/ksgl/kssz/append",
    },
    examUrl: {
      type: String,
      default: "exam/api/sjgl/url",
    },
    keysMap: {
      type: Array,
      default: () => {
        return [
          { from: "cover", to: "cover", type: "text", desc: "封面" },
          { from: "title", to: "title", type: "text", desc: "考试名称" },
          { from: "startTime", to: "startTime", type: "text", desc: "开始时间" },
          { from: "endTime", to: "endTime", type: "text", desc: "结束时间" },
          { from: "total", to: "total", type: "text", desc: "总分" },
          { from: "pass", to: "pass", type: "text", desc: "及格分数" },
          { from: "examNum", to: "examNum", type: "text", desc: "考试次数" },
          { from: "already", to: "already", type: "text", desc: "已考次数" },
          { from: "residue", to: "residue", type: "text", desc: "剩余次数" },
          { from: "heightScore", to: "heightScore", type: "text", desc: "最高成绩" },
          { from: "isDo", to: "isDo", type: "text", desc: "考试是否可用" },
          { from: "businessId", to: "businessId", type: "text", desc: "获取考试businessId" },
          { from: "id", to: "id", type: "text", desc: "id" },
        ];
      },
    },
  },
  data() {
    return {};
  },
  components: {},
  methods: {
    formatterDate(time, format) {
      return time ? dsf.date.format(time, !format ? "yyyy-mm-dd HH:ii" : format) : "";
    },
    async goDetail(item) {
      let url = this.$replace(this.detailUrl, item);
      console.log(url);
      if (item["residue"].includes("无限")) {
        let appendExam = await this.appendExam(this.data);
        if (appendExam.success) {
          let examUrl = await this.getExamUrl(this.data);
          dsf.layer.openWindow(url + examUrl);
        }
      } else if (this.data["residue"] != 0) {
        let appendExam = await this.appendExam(this.data);
        if (appendExam.success) {
          let examUrl = await this.getExamUrl(this.data);
          dsf.layer.openWindow(url + examUrl);
        }
      } else if (item["residue"] == 0) {
        this.dsf.layer.message("考试次数已用完", false);
      } else if (item["isDo"] == 0) {
        this.dsf.layer.message("该考试不可用", false);
      } else {
        this.dsf.layer.message("进入考试异常", false);
      }
    },
    appendExam(data) {
      return new Promise((resolve, reject) => {
        const loading = dsf.layer.loading();
        this.dsf.http
          .post(this.examAppendUrl, { examId: data["id"] })
          .done(res => {
            if (res.success) {
              resolve(res);
            } else {
              this.dsf.layer.message(res.message || "获取考试异常", false);
              reject();
            }
          })
          .error(response => {
            this.dsf.layer.message(response.message || "获取考试异常", false);
            reject();
          })
          .always(function () {
            dsf.layer.closeLoading(loading);
          });
      });
    },
    getExamUrl(data) {
      return new Promise((resolve, reject) => {
        const loading = dsf.layer.loading();
        this.dsf.http
          .get(this.examUrl, { examId: data["id"], deviceType: "mobile", businessId: data["businessId"], entryType: 2 })
          .done(res => {
            if (res.success) {
              resolve(res.data);
            } else {
              reject();
              this.dsf.layer.mobile.message(res.message || "获取考试异常", false);
            }
          })
          .error(response => {
            reject();
            this.dsf.layer.mobile.message(response.message || "获取考试异常", false);
          })
          .always(function () {
            dsf.layer.closeLoading(loading);
          });
      });
    },
  },
});
</script>
<style lang="scss" scoped>
@import "../../../assets/styles/mobile/index.scss";
.dsf_cardItemByExam {
  background: #ffffff;
  border-radius: 4.5px;
  padding: 10px 10px 0 10px;
  box-sizing: border-box;
  color: #666;
  font-size: 13px;
  // .img-box {
  //   width: 120px;
  //   height: 90px;
  // }
  .content {
    position: relative;
  }
  .item-wrap {
    @include border-bottom-theme(1px, solid, "normal");
  }
  .margin-bottom-5 {
    margin-bottom: 5px;
  }
  .title {
    color: #333;
    font-size: 15px;
  }
  .detail-cell-1 {
    display: flex;
    align-items: center;
    margin-bottom: 2.5px;
    span {
      display: inline-block;
      width: 115px;
      flex-shrink: 0;
    }
  }
  .detail-cell-2 {
    display: flex;
    align-items: center;
    span {
      display: inline-block;
      width: 115px;
      flex-shrink: 0;
    }
  }
  .start {
    height: 35px;
    line-height: 35px;
    text-align: center;
    @include font-theme("normal");
    border: none;
  }
  .score {
    padding: 4px 4px;
    box-sizing: border-box;
    background: #ffffff;
    box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.2);
    border-radius: 2px;
    position: absolute;
    right: 2px;
    top: 10%;
    transform: translateY(-10%);
    text-align: center;
    .score-num {
      @include font-theme("normal");
      font-size: 14px;
      margin-bottom: 5px;
    }
    .txt {
      font-size: 12px;
    }
  }
}
</style>
