<template>
  <div class="dsf_nc_special_detail">
    <div class="special_top">
      <!-- setImage(info.cover)  -->
      <van-image width="100%" height="191px" style="width: 100%; height: 191px" fit="fill" :src="info.cover | imgFormat">
        <template v-slot:loading>
          <van-loading type="spinner" size="20" />
        </template>
        <template v-slot:error>
          <img :src="dsf.config.setting_error_img" style="width: 100%; height: 191px" />
        </template>
      </van-image>
      <div class="special_top_mask">
        <p>{{ info.name }}</p>
      </div>
    </div>
    <div class="special_detail_info">
      <div class="info_left info_items">
        <div class="item">
          <span class="date">{{ setDate(info.pxsjSdate, info.pxsjEdate, " ～ ") }}</span>
          <span class="font">培训时间</span>
        </div>
      </div>
      <div class="info_right info_items">
        <div class="item">
          <span class="rscount">{{ info.studentCount || 0 }}人</span>
          <span class="font">学员人数</span>
        </div>
      </div>
    </div>
    <ul class="special_detail_label_wrap" v-if="labelLists.length">
      <li class="label_items" v-for="(item, index) in labelLists" :key="index">{{ item }}</li>
    </ul>
    <!-- 培训介绍 -->
    <special-title :title="titleNames[0].name" ref="title0">
      <template slot="content">
        <div class="work_wrap">
          <p>
            <span class="label">主办单位：</span>
            <span class="font">{{ info.fzbmText || "暂无" }}</span>
          </p>
          <p>
            <span class="label">协办单位：</span>
            <span class="font">{{ info.bm || "暂无" }}</span>
          </p>
          <p>
            <span class="label">培训范围：</span>
            <span class="font">{{ info.txrs ? info.txrs : "无限制" }}</span>
          </p>
        </div>
        <div class="intro_des" v-html="info.content || '暂无数据'"></div>
      </template>
    </special-title>
    <!-- 培训课程 -->
    <special-title :title="titleNames[1].name" ref="title1">
      <template slot="content">
        <ul class="course_wrap" v-if="courseLists.length">
          <li class="course_items" v-for="(item, index) in courseLists" :key="item.unitId + index">
            <div class="course_item_top_title">
              <span class="ell">{{ item.unitName }}</span>
            </div>
            <div class="course_notice">
              <span>必修：{{ item.compulsory || 0 }}门</span>
              <span>{{ item.compulsoryPeriod || 0 }}学时</span>
              <span>选修：{{ item.electives || 0 }}门</span>
              <span>{{ item.electivesPeriod || 0 }}学时</span>
            </div>
            <div class="course_sub_items" v-for="(items, idx) in item.subList" :key="items.businessId + idx" @click="clickCourseItem(items, info)">
              <div class="item_type">
                <div class="type_top">
                  <!-- <i :class="items.typeValue"></i> -->
                  <i :class="`icon iconfont ${setIcon(items.typeValue)}`"></i>
                  <div class="item_name ell">{{ items.name || "暂无" }}</div>
                </div>
                <div class="type_bottom">
                  <div class="period">
                    <span>{{ items.period || 0 }} 学时</span>
                    <span class="isbx">{{ items.categoryText || "" }}</span>
                  </div>
                  <div class="duration">{{ items.duration || "00:00:00" }}</div>
                </div>
              </div>
              <div class="item_circle">
                <van-circle :value="recharNum(items.progress || 0)" :rate="100" :speed="50" size="35px" :stroke-width="80" :text="items.progress || 0" layer-color="#ebedf0" />
              </div>
            </div>
          </li>
        </ul>
        <div class="no_data" v-else>
          <!-- 暂无数据 -->
          <!-- <van-image :src="'/'" fit="fill"></van-image> -->
          暂无数据
        </div>
      </template>
    </special-title>
    <!-- 结业考核 -->
    <special-title :title="titleNames[2].name" v-if="isShowCheck" ref="title2" class="special_check">
      <template slot="content">
        <div class="complete_wrap">
          <div class="complete_progress">
            <div class="complete_title">进度</div>
            <div class="complete_progress_wrap" v-for="(item, index) in unitAssess" :key="index">
              <div class="progress_circle_compulsory">
                <van-circle
                  :value="recharNum(item.compulsoryper || 0)"
                  :rate="100"
                  :speed="50"
                  size="100px"
                  :stroke-width="100"
                  color="#F66C6C"
                  :stroke-linecap="'butt'"
                  layer-color="#CDCDCD"
                >
                  <template slot="default">
                    <div class="proess">
                      <p>{{ item.compulsoryper || 0 }}%</p>
                      <p class="bx">必修</p>
                    </div>
                  </template>
                </van-circle>
                <div class="period">{{ item.compulsoryHursed || 0 }}/{{ item.compulsoryPeriod || 0 }}学时</div>
              </div>
              <div class="progress_circle_electives">
                <van-circle
                  :value="recharNum(item.electiveper || 0)"
                  :rate="100"
                  :speed="50"
                  size="100px"
                  :stroke-width="100"
                  color="#0E8CEE"
                  :stroke-linecap="'butt'"
                  layer-color="#CDCDCD"
                >
                  <template slot="default">
                    <div class="proess">
                      <p>{{ item.electiveper || 0 }}%</p>
                      <p class="xx">选修</p>
                    </div>
                  </template>
                </van-circle>
                <div class="period">{{ item.electiveHursed || 0 }}/{{ item.electivesPeriod || 0 }}学时</div>
              </div>
            </div>
          </div>
          <!-- <div class="complete_certificate">
            <div class="complete_title">证书</div>
            <div class="complete_certificate_wrap">

            </div>
          </div> -->
        </div>
      </template>
    </special-title>
    <div class="btn_wrap">
      <div
        class="btn"
        @click="clickEnter(info)"
        v-if="setStatus(info)"
        :style="`${
          setStatus(info) == '待审核' || setStatus(info) == '已结业' || (info.state == 0 && info.signStatus != 1) || (info.state == 2 && info.trainingStatus != 1)
            ? 'background: #aaa; cursor: default'
            : ''
        }`"
      >
        {{ setStatus(info) }}
      </div>
    </div>
    <div class="fixed_title_wrap" v-if="showFixed">
      <div :class="`fixed_title ${item.isactive ? 'active' : ''}`" v-for="(item, index) in titleNames" :key="index" @click="clickTitle(item, index)">
        <span class="icon"></span>
        <span>{{ item.name }}</span>
      </div>
    </div>
  </div>
</template>
<script>
import { specialTitle } from "../../base/special";

export default dsf.component({
  name: "DsfMobileNcSpecialDetail",
  ctrlCaption: "专题班详情",
  mixins: [$mixins.control],
  components: {
    specialTitle,
  },
  props: {
    // slots: {
    //   type: Array,
    //   default() {
    //     return [{ name: "default", controls: [] }];
    //   },
    // },
    infoUrl: {
      type: String,
      default: "/nc/class/info",
    },
    courseUrl: {
      type: String,
      default: "/nc/class/course/list",
    },
    isShowCheck: {
      type: Boolean,
      default: true,
    },
    openUrl: {
      type: String,
      default: "#/mobile/nc/page/app/course/mobile-coursePlayer",
    },
  },
  data() {
    return {
      classId: "",
      info: {},
      labelLists: [],
      courseLists: [],
      titleNames: [
        {
          name: "培训介绍",
          isactive: true,
        },
        {
          name: "培训课程",
          isactive: false,
        },
        {
          name: "结业考核",
          isactive: false,
        },
      ],
      showFixed: false,
      scheduleData: {}, // 考核进度
      unitAssess: [],
    };
  },
  created() {
    this.classId = this.queryString["id"] || "c9cd45271f374737b973ff461e68d3ad";
  },
  mounted() {
    if (!this.isDesign) {
      this.classInfo(); // 简介
      this.classCourse(); // 课程
      this.scheduleInit(); // 考核进度
    }

    window.addEventListener("scroll", this.handleScroll, true);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    classInfo() {
      this.dsf.http
        .get(this.infoUrl, { id: this.classId })
        .done(res => {
          if (res.success) {
            this.info = res.data;
            this.labelLists = this.info.labelsText ? this.info.labelsText.split("^") : [];
          } else {
            this.dsf.layer.mobile.message(res.message || "获取数据异常", false);
          }
        })
        .error(response => {
          this.dsf.layer.mobile.message(response.message || "获取数据异常", false);
        });
    },
    classCourse() {
      this.dsf.http
        .get(this.courseUrl, { id: this.classId })
        .done(res => {
          if (res.success) {
            this.courseLists = res.data || [];
          } else {
            this.dsf.layer.mobile.message(res.message || "获取数据异常", false);
          }
        })
        .error(response => {
          this.dsf.layer.mobile.message(response.message || "获取数据异常", false);
        });
    },
    scheduleInit() {
      this.dsf.http
        .get("/nc/class/learning/assess", { id: this.classId })
        .done(res => {
          if (res.success) {
            this.scheduleData = res.data;
            this.unitAssess = res.data.unitAssess.length ? res.data.unitAssess : [];
          } else {
            this.dsf.layer.mobile.message(res.message || "获取数据异常", false);
          }
        })
        .error(response => {
          this.dsf.layer.mobile.message(response.message || "获取数据异常", false);
        });
    },
    clickCourseItem(item, info) {
      let courseId = item.businessId; // 课程id
      if (info.trainingStatus == 1 && info.state == 2) {
        //trainingStatus 1能学课 按钮状态 ； state 2 开始学习 专题班状态
        this.$open({ url: `${this.openUrl}?id=${courseId}&type=ztb` });
        // return;
      }
    },
    setImage(item) {
      const data = item;
      let src = "";
      if (dsf.isDef(data) && data[0]) {
        src = data[0].photo ? dsf.url.getWebPath(JSON.parse(data[0].photo)[0].relativePath) : "";
      }
      return src;
    },
    setDate(startDate, endDate, interval) {
      let start = startDate ? startDate.slice(5, 10) : "";
      let end = endDate ? endDate.slice(5, 10) : "";
      return start && end ? `${start}  ${interval}  ${end}` : "暂无";
    },
    recharNum(data) {
      let wcjd = data;
      wcjd = wcjd * 1;
      return wcjd;
    },
    setStatus(data) {
      // 设置状态
      let status = data.state; // 状态
      let studentCount = data.studentCount; // 学员人数
      let sfxzrs = data.txrs; //是否有人数限制
      let html = "";
      switch (status) {
        case 0:
          if (sfxzrs && studentCount >= sfxzrs) {
            html = "人数已满";
            return;
          }
          html = "立即报名";
          break;
        case 1:
          html = "待审核";
          break;
        case 2:
          html = "开始学习";
          break;
        case 3:
          html = "已结业";
          break;
        default:
          html = "";
      }
      return html;
    },
    setIcon(status) {
      // 设置图标
      let iconText = "";
      switch (status) {
        case "course" || "video" || "audio":
          iconText = "icon-shipin1";
          break;
        case "draft":
          iconText = "icon-wengao-1";
          break;
        case "live":
          iconText = "icon-a-zhibobofangshexiangjitianxianmianxing";
          break;
        case "exam":
          iconText = "icon-kaoshi";
          break;
        case "media":
          iconText = "icon-fumeitixiaoxi";
          break;
        default:
          iconText = "";
      }
      return iconText;
    },
    clickEnter(data) {
      let status = data.state; // 状态
      console.log("点击报名按钮===》", data);
      let courseId = data.lastCourseId; //课程id
      // if (this.info.signStatus != 1 || this.info.trainingStatus != 1) return;
      if (status == 0 && this.info.signStatus == 1) {
        let id = data.classId; //专题班id
        this.dsf.http
          .get("nc/class/enroll", { id })
          .done(res => {
            if (res.success) {
              this.dsf.layer.mobile.message(res.message);
              this.classInfo();
            } else {
              this.dsf.layer.mobile.message(res.message || "获取数据异常", false);
            }
          })
          .error(response => {
            this.dsf.layer.mobile.message(response.message || "获取数据异常", false);
          });
      } else if (status == 2 && this.info.trainingStatus == 1) {
        this.$open({ url: `${this.openUrl}?id=${courseId} ` });
      }
    },
    clickTitle(item, index) {
      // this.titleNames.forEach(element => {
      //   if (item.name == element.name) {
      //     element.isactive = true;
      //   } else {
      //     element.isactive = false;
      //   }
      // });

      this.$nextTick(() => {
        document.querySelector(".ds-page-center").scrollTo({
          top: this.$refs[`title${index}`].$el.offsetTop - document.querySelector(".fixed_title_wrap").clientHeight + 10, // +10 表示进入了此模块
          behavior: "smooth", // 平滑滚动
        });
      });
    },
    handleScroll() {
      this.$nextTick(() => {
        let scrollTop = document.querySelector(".ds-page-center").scrollTop;
        let offsetTitle0Top = this.$refs.title0.$el.offsetTop - 45; // special_title的padding-top20+行高22 = 44
        let offsetTitle1Top = this.$refs.title1.$el.offsetTop - 45;
        let offsetTitle2Top = this.$refs.title2.$el.offsetTop - 45;
        let infoOffsetTop = document.querySelector(".special_detail_info").offsetTop;

        // console.log("scrollTop===>", scrollTop, "infoOffsetTop===>", infoOffsetTop, "offsetTitle1Top===>", offsetTitle1Top);

        this.showFixed = scrollTop > infoOffsetTop ? true : false;

        this.titleNames.forEach((element, index) => {
          if (scrollTop > offsetTitle0Top && offsetTitle0Top < offsetTitle1Top) {
            if (index == 0) {
              element.isactive = true;
            } else {
              element.isactive = false;
            }
          }

          if (scrollTop > offsetTitle1Top && offsetTitle0Top < offsetTitle2Top) {
            if (index == 1) {
              element.isactive = true;
            } else {
              element.isactive = false;
            }
          }

          if (scrollTop > offsetTitle2Top) {
            if (index == 2) {
              element.isactive = true;
            } else {
              element.isactive = false;
            }
          }
        });
      });
    },
  },
});
</script>
<style lang="scss" scoped>
@import "../../../assets/styles/mobile/index.scss";
.dsf_nc_special_detail {
  background: #ffffff;
  // padding-bottom: 20px;
  .special_check {
    margin-bottom: 60px;
  }
  .special_top {
    position: relative;
    height: 192px;
    .special_top_mask {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0, 0, 0, 0.5);
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      padding: 0 15px 10px;
      p {
        // font-size: 16px;
        @include font_5;
        color: #ffffff;
        line-height: 24px;
      }
    }
  }
  .special_detail_info {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px 15px;
    .info_items {
      width: 50%;
      .item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        span {
          font-size: 15px;
          color: #333333;
          line-height: 30px;
          &.font {
            font-size: 13px;
            color: #666666;
            line-height: 20px;
          }
        }
      }
      &.info_right {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        &::before {
          position: absolute;
          left: 0;
          content: "";
          display: inline-block;
          width: 1px;
          height: calc(100% - 10px);
          background: #c5c5c5;
        }
      }
    }
  }
  .special_detail_label_wrap {
    display: flex;
    padding: 0 15px;
    flex-wrap: wrap;
    .label_items {
      font-size: 12px;
      // color: #0E8CEE;
      @include font_theme("normal");
      padding: 2px 18px;
      background: #e7f4ff;
      border-radius: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 10px;
      margin-bottom: 5px;
      &:last-child {
        margin-right: 0;
      }
    }
  }

  .work_wrap {
    width: 100%;
    box-sizing: border-box;
    background: #f7f7f7;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 15px;
    p {
      // font-size: 16px;
      @include font_5;
      color: #666666;
      line-height: 22px;
      display: flex;
      .label {
        width: 80px;
      }
      .font {
        width: calc(100% - 80px);
      }
      &:nth-child(2) {
        padding: 10px 0;
      }
    }
  }
  .course_wrap {
    box-sizing: border-box;
    border-radius: 5px;
    width: 100%;
    .course_items {
      background: #f7f7f7;
      margin-bottom: 10px;
      padding: 10px;
      .course_item_top_title {
        line-height: 40px;
        height: 40px;
        font-size: 19px;
        display: flex;
        align-items: center;
        width: 100%;
        span {
          width: calc(100% - 20px);
        }
        &::before {
          content: "";
          height: 20px;
          width: 5px;
          margin-right: 5px;
          @include background_theme("main");
        }
      }
      .course_notice {
        display: flex;
        height: 20px;
        align-items: center;
        margin-bottom: 5px;
        span {
          margin-right: 5px;
          font-size: 17px;
          line-height: 20px;
        }
      }
      .course_sub_items {
        border-bottom: 1px solid #e3e3e3;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px 0;
        &:last-child {
          border-bottom: none;
        }
      }
      .item_type {
        width: calc(100% - 1rem);
        .type_top {
          display: flex;
          align-items: center;
          i {
            margin-right: 5px;
            @include font_5;
            @include font_theme("normal");
          }
          .item_name {
            // font-size: 16px;
            @include font_5;
            color: #333333;
            line-height: 22px;
            width: calc(100% - 20px);
          }
        }
        .type_bottom {
          margin-left: 20px;
          height: 24px;
          // font-size: 16px;
          @include font_5;
          line-height: 24px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          .isbx {
            height: 100%;
            border: 1px solid #fa5151;
            color: #fa5151;
            margin-left: 10px;
          }
          .duration {
            margin-right: 20px;
          }
        }
      }
      .item_circle {
        width: 50px;
        justify-content: center;
        align-items: center;
        display: flex;
        .van-circle {
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
    }
  }
  .complete_wrap {
    .complete_title {
      // font-size: 16px;
      @include font_5;
      color: #333333;
      line-height: 23px;
      margin-bottom: 10px;
    }
    .complete_progress {
      margin-bottom: 19px;
      .complete_progress_wrap {
        display: flex;
        justify-content: space-around;
        align-items: center;
      }
      .progress_circle_compulsory {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .van-circle {
          display: flex;
          justify-content: center;
          align-items: center;
          p {
            font-size: 15px;
            color: #333333;
            line-height: 22px;
            &.bx {
              font-size: 13px;
              color: #f66c6c;
            }
          }
        }
        .period {
          font-size: 15px;
          color: #f66c6c;
          line-height: 22px;
          margin-top: 5px;
        }
      }
      .progress_circle_electives {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .van-circle {
          display: flex;
          justify-content: center;
          align-items: center;
          p {
            font-size: 15px;
            color: #333333;
            line-height: 22px;
            &.xx {
              font-size: 13px;
              // color: #0E8CEE;
              @include font_theme("normal");
            }
          }
        }
        .period {
          font-size: 15px;
          // color: #0E8CEE;
          @include font_theme("normal");
          line-height: 22px;
          margin-top: 5px;
        }
      }
    }
    .complete_certificate_wrap {
      margin-bottom: 15px;
    }
  }
  .btn_wrap {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 20;
    box-sizing: border-box;
    height: 60px;
    padding: 10px 15px;
    background: #ffffff;
    border-top: 1px solid #f1f1f1;
    box-shadow: 0 -2px 4px 0 rgba(238, 238, 238, 0.5);
    .btn {
      width: 100%;
      height: 40px;
      // background: #1682F8;
      // font-size: 16px;
      @include font_5;
      @include background_theme("normal");
      border-radius: 22px;
      color: #ffffff;
      text-align: center;
      line-height: 40px;
      cursor: pointer;
    }
  }
  .fixed_title_wrap {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: #ffffff;
    box-shadow: 0 1px 12px 0 rgba(213, 213, 213, 0.5);
    padding: 20px 0 15px;
    .fixed_title {
      display: flex;
      align-items: center;
      span {
        font-size: 13px;
        color: #666666;
        line-height: 19px;
        &.icon {
          display: inline-block;
          width: 11px;
          height: 14px;
          margin-right: 5px;
        }
      }
      &.active {
        .icon {
          background-image: url("../../../assets/imgs/mobile/special/position.png");
          background-repeat: no-repeat;
          background-size: 100% 100%;
        }
