<template>
  <div class="dsf-sort" @click="changeStatus()">
    <span class="sort-name" :style="{'font-size':fontSize}">{{iconName}}</span>
    <div class="sort-icon-g">
      <dsf-icon name="shangjiantou" class="top" :class="status == 'asc'?'on':status == 'desc'?'def':''"></dsf-icon>
      <dsf-icon name="xiajiantou" class="bottom" :class="status == 'desc'?'on':status == 'asc'?'def':''"></dsf-icon>
    </div>
  </div>
</template>

<script>
  export default {
    name: "dsfSort",
    data() {
      return {
        status: this.customSort
      }
    },
    props: {
      fontSize: {
        type: [String],
        default: '17px',
        desc: '字体大小'
      },
      customSort: {
        type: String,
        default: 'desc'
      },
      iconName: {
          type: [String],
          default: '请设置名称',
          desc: '按钮名称'
      },
      code: {
        type: String,
        default: 'ds_create_time'
      }
    },
    methods: {
      changeStatus(type) {
        if (type) {
          // 父组件刷新等功能时会使用代码调整状态
          this.status = type;
        } else {
          this.status == 'desc' ? this.status = 'asc' : this.status = 'desc';
          this.$emit('click', this.status, this.code);
        }
      }
    }
  }
</script>
