<template>
  <div class="dsf_learnHeader" v-if="visible">
    <div class="content">
      <div class="item-wrap" v-if="isDesign">
        <div class="item" v-for="(item, index) in keysMap" :key="index">
          <div class="num">
            <span v-if="item.from.includes(',')">{{ resData[item.from.split(",")[0]] }}/{{ resData[item.from.split(",")[1]] }}</span>
            <span v-else>{{ resData[item.from] }}</span>
          </div>
          <p class="title">{{ item.label }}</p>
        </div>
      </div>
      <div class="item-wrap" v-else>
        <div class="item" v-for="(item, index) in keysMap" :key="index">
          <div class="num">
            <span v-if="item.from.includes(',')">{{ resData[item.from.split(",")[0]] || 0 }}/{{ resData[item.from.split(",")[1]] || 0 }}</span>
            <span v-else>{{ resData[item.from] || 0 }}</span>
          </div>
          <p class="title">{{ item.label }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobileNcLearnHeader",
  ctrlCaption: "在学顶部汇总",
  mixins: [$mixins.control],
  props: {
    visible: {
      type: Boolean,
      default: true,
    },
    reqUrl: { type: String, default: "" },
    keysMap: {
      type: Array,
      default: () => {
        return [
          { from: "year", to: "year", type: "text", desc: "学习年度", label: "学习年度" },
          { from: "special,special2", to: "special", type: "text", desc: "必修专题班", label: "必修专题班" },
          { from: "course,course2", to: "course", type: "text", desc: "必修课程", label: "必修课程" },
          { from: "total,total2", to: "total", type: "text|number", desc: "总学时", label: "总学时" },
        ];
      },
    },
  },
  data() {
    return {
      resData: {},
    };
  },
  components: {},
  mounted() {
    if (this.isDesign) {
      this.resData = {
        year: 0,
        special: 0,
        special2: 0,
        course: 0,
        course2: 0,
        total: 0,
        total2: 0,
      };
    } else {
      this.getData();
    }
  },
  methods: {
    getData() {
      if (this.reqUrl) {
        this.dsf.http
          .get(this.reqUrl, {})
          .done(res => {
            if (res.success) {
              this.resData = res.data;
            } else {
              this.dsf.layer.mobile.message(res.message || "获取数据异常", false);
            }
          })
          .error(response => {
            this.dsf.layer.mobile.message(response.message || "获取数据异常", false);
          });
      }
    },
  },
});
</script>
<style lang="scss" scoped>
@import "../../../assets/styles/mobile/index.scss";
.dsf_learnHeader {
  border-radius: 8px;
  overflow: hidden;
  .item-wrap {
    height: 70px;
    padding: 0 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fff;
    flex-wrap: wrap;
    .item {
      .num {
        text-align: center;
        font-size: 17px;
        margin-bottom: 10px;
      }
      &:nth-child(n + 1) .num {
        color: #ff8f00;
      }
      &:nth-child(2n) .num {
        color: #f66c6c;
      }
      &:nth-child(3n) .num {
        color: #0e8cee;
      }
      &:nth-child(4n) .num {
        color: #1ebdb8;
      }
    }
  }
}
</style>
