export default {
    data() {
        return {
            is_check_refresh: true
        }
    },
    created() {
        //如果页面第一次创建则不许要进行检查
        this.is_check_refresh = false;
    },
    methods: {
        //检查页面加载是是否要刷新
        $checkRefresh: function(to, from, next) {
            next();
            if (to.name != from.name) {
                if (this.is_check_refresh) {
                    let maps = this.$store.state.refresh.maps;
                    if (maps[to.name] && maps[to.name].state) {
                        window.setTimeout(() => {
                            this.$refresh(from.name, maps[to.name].args);
                            this.$removeRefresh(to.name);
                        }, 500);
                    }
                } else {
                    this.is_check_refresh = true;
                    this.$removeRefresh(to.name);
                }
            }
        },
        $pushRefresh: function(name, attachArgs) {
            this.$store.commit("pushRefresh", {
                name: name,
                args: attachArgs || null
            });
        },
        $removeRefresh: function(name) {
            this.$store.commit("removeRefresh", name);
        },
        $refresh: function() {}
    },
    beforeRouteEnter(to, from, next) {
        next(vm => {
            vm.$checkRefresh(to, from, next)
        })
    }
}