<!-- 侧部弹出面板-->
<template>
  <div>
    <van-popup
      ref="popup"
      :class="customClass"
      :get-container="site"
      v-model="isShow"
      :position="position == 'right' ? 'right' : 'left'"
      :style="{ width: '84%', height: '100%' }"
    >
      <slot></slot>
    </van-popup>
  </div>
</template>

<script>
export default {
  name: "DsfSidePopup",
  props: {
    position: {
      type: String,
      default: "right",
      desc: "right/left",
    },
    show: {
      type: Boolean,
      default: false,
      desc: "默认是否显示",
    },
    site: {
      default: "",
      desc: "挂载",
    },
    customClass: {
      type: String,
      default: "",
      desc: "自定义class",
    },
  },
  data() {
    return {
      isShow: this.show,
    };
  },
  methods: {
    changeShow() {
      this.isShow = !this.isShow;
    },
  },
};
</script>

<style></style>
