<template>
  <div class="channel-detail-item" @click="goCourse">
    <div class="channel-item-img">
      <van-image :src="getCover()" fit="fill">
        <template v-slot:loading>
          <van-loading type="spinner" size="20" />
        </template>
        <template v-slot:error><img :src="require('_nc/assets/styles/themes/img/file-read-18688.png')" style="width:100%;height:100%"></template>
      </van-image>
      <div class="channel-item-img-rank"><div class="rank-number">{{item['nc_pd_pdkcdata.average_fs']||0}}</div></div>
      <div v-if="item['nc_pd_pdkcdata.kcsc']" class="channel-item-img-date ellipsis"><dsf-icon name="shijian"></dsf-icon>{{item['nc_pd_pdkcdata.kcsc']}}</div>
    </div>
    <div class="channel-item-content">
      <div class="channel-title">{{item['nc_pd_pdkcdata.title']||'暂无'}}</div>
      <div class="channel-label-box ellipsis">{{item['nc_pd_pdkcdata.skjsunit']||'暂无'}}</div>
      <div class="channel-icon-box">
        <div class="channel-icon">
          <dsf-icon name="yanjing"></dsf-icon>{{item['nc_pd_pdkcdata.djs']||0}}
        </div>
        <div class="channel-icon">
          <dsf-icon name="date"></dsf-icon>{{item['nc_pd_pdkcdata.xs']||0}}学时
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "channelDetail",
    data() {
      return {}
    },
    props:{
      item:{
        type:Object,
        default:function () {
          return {}
        }
      },
      readOnly: {
        type: Boolean,
        default: false
      }
    },
    computed: {},
    components: {},
    created: function () {},
    mounted: function () {

    },
    methods: {
      getCover: function () {
        if(!this.item['nc_pd_pdkcdata.cover']){
          let src=require('_nc/assets/styles/themes/img/file-read-18688.png')
          return src;
        }
        let files = dsf.getUploadFileInfo(this.item['nc_pd_pdkcdata.cover']);
        return dsf.config.webRoot + files[0].relativePath
      },
      goCourse(){
        if(this.readOnly){
          return;
        }
        this.$router.push('/mobile/nc/recommendFile/mobile-courseDetail?id'+this.item['nc_pd_pdkcdata.relid']);
      }
    }
  }
</script>

<style scoped>

</style>
