<!--  -->
<template>
  <div :style="{ height: allHeight }">
    <div id="LiveVideo" :style="{ height: playerHeight }"></div>
    <div id="chatwrap" :style="{ height: `calc(${allHeight} - ${playerHeight})` }"></div>
    <div class="player-signed" v-if="showSign">
      <div class="player-signed-in">
        <div class="signed-head">签到</div>
        <div class="signed-content">
          <div>
            <span class="signed-time">{{ time }}</span>
            秒
          </div>
          <li class="sign-message">各位同学开始签到了</li>
          <span class="btn-signed" @click="submitSigned()">签到</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/**
 * 设置播放器参数
 * @param autoplay 自动播放
 * @param audioMode 讲师画面以音频模式播放(只在移动端生效), 默认false
 * @param controller 是否显示控制栏
 * @param pptNav 是否显示ppt控制控件
 * @param barrage 弹幕 是否开启弹幕 true false
 * @param switchPlayer  切换按钮
 * @param controllerPosition  固定位置 'ppt'|'player'
 */
/**
 * 设置聊天室参数
 * @param enableFlower 送花功能 开启true 关闭false
 * @param enableLike 点赞功能 开启true 关闭false
 * @param customChatColor 消息颜色 '#2495c7'
 * @param enableRewardAnimation  打赏动画效果 true false
 * @param enableUpdatePageviews  热度更新 true false
 * @param tabData  菜单栏 true false
 */
import jq from "jquery";
export default dsf.component({
  name: "DsfMobileNcLivePlayer",
  ctrlCaption: "保利直播移动端",
  mixins: [$mixins.control],
  props: {
    othLang: {
      default: false,
      type: Boolean,
    },
    detailData: {
      default: () => {
        return { channelValue: "" };
      },
      type: Object,
    },
    allHeight: {
      default: "100%",
      type: String,
    },
    playerHeight: {
      default: "4rem",
      type: String,
    },
    sourceUrl: {
      default: "",
      type: String,
    },
    state: {
      default: 5,
      type: [String, Number],
    },
    PolyvParams: {
      type: Object,
      default() {
        return {
          autoplay: true,
          audioMode: false,
          controller: true,
          pptNav: true,
          barrage: true,
          switchPlayer: true,
        };
      },
    },
    charRoomParams: {
      type: Object,
      default() {
        return {
          enableFlower: true,
          enableLike: true,
          customChatColor: "#2495c7",
          enableRewardAnimation: true,
          enableUpdatePageviews: true,
          tabData: [],
        };
      },
    },
  },
  data() {
    return {
      chatroom: null,
      num: 0,
      barrageStatus: true,
      liveSdk: null,
      showSign: false,
      time: 0,
      timer: null,
      checkinId: null, //签到ID
      id: "",
    };
  },

  components: {},

  computed: {},
  created() {
    let script = document.createElement("script");
    script.type = "text/javascript";
    script.src = this.othLang ? dsf.url.getWebPath("/nc/pc/live/scripts/polyv-live.js") : "https://player.polyv.net/livesdk/polyv-live.min.js";
    let script2 = document.createElement("script");
    script2.type = "text/javascript";
    script2.src = this.othLang ? dsf.url.getWebPath("/nc/pc/live/scripts/polyv-chatroom.js") : "https://player.polyv.net/jssdk/polyv-chatroom.min.js";
    let css = document.createElement("link");
    css.rel = "stylesheet";
    css.href = "https://player.polyv.net/jssdk/polyv-chatroom.min.css";
    document.getElementsByTagName("head")[0].appendChild(script);
    document.getElementsByTagName("head")[0].appendChild(script2);
    document.getElementsByTagName("head")[0].appendChild(css);
  },
  mounted() {
    this.id = this.queryString["id"] || "44c8d879bb834d2c9b1b2f8ac1b55fdc";
    if (this.isDesign) {
      console.log("设计器中暂时不调用任何接口");
    } else {
      if (this.sourceUrl) {
        this.getLiveinfo();
      } else {
        this.getliveConfig();
      }
    }
  },
  beforeDestroy() {
    if (this.chatroom) {
      this.chatroom.destroy();
    }
    if (this.liveSdk) {
      this.liveSdk.destroy();
    }
  },
  methods: {
    //获取直播信息
    getLiveinfo() {
      this.dsf.http
        .get(this.sourceUrl, {
          id: this.id,
        })
        .done(res => {
          if (res.success) {
            this.detailData = res.data.ncLiveInfo;
            this.state = res.data.state.value;
            this.getliveConfig();
          } else {
            this.dsf.layer.mobile.message(res.message || "获取直播数据异常", false);
          }
        })
        .error(response => {
          this.dsf.layer.mobile.message(response.message || "获取直播数据异常", false);
        });
    },
    //获取频道信息
    async getliveConfig() {
      let flag = this.state == 2 ? true : false;
      let loader = this.dsf.layer.mobile.loading();
      this.dsf.http
        .get("nc/live/liveConfig", {
          channelId: this.detailData.channelValue || "",
        })
        .done(res => {
          if (res.success) {
            this.chatToken(res.data, flag);
          } else {
            this.dsf.layer.mobile.message(res.message || "获取直播数据异常", false);
          }
        })
        .error(response => {
          this.dsf.layer.mobile.message(response.message || "获取直播数据异常", false);
        })
        .always(res => {
          this.dsf.layer.mobile.closeLoading(loader);
        });
    },
    Polyv(data, flag, chatroom) {
      //以下是js
      let liveSdk = new PolyvLiveSdk({
        channelId: data.channelId, //频道信息
        sign: data.sign, // 频道验证签名
        timestamp: data.timestamp, // 毫秒级时间戳
        appId: data.appId, // polyv 后台的appId
        socket: chatroom.chat.socket,
        user: {
          userId: data.user.userId || "",
          userName: data.user.userName || "",
          pic: data.user.pic || "",
          // roomName: data.user.roomName || "",....千万别加
          userType: data.user.userType || "slice",
        },
      });
      let pptEl = "#LiveVideo";
      let el = "#LiveVideo";
      if (data.scene === "ppt") {
        pptEl = ".tab-ppt";
      }
      let config = {
        pptEl: pptEl,
        el: el,
        type: "auto",
        autoplay: this.PolyvParams.autoplay,
        audioMode: this.PolyvParams.audioMode,
        width: "100%",
        height: "100%",
        pptWidth: "100%",
        pptHeight: "100%",
        controllerPosition: "ppt",
        controller: this.PolyvParams.controller,
        pptNav: this.PolyvParams.pptNav,
        barrage: this.PolyvParams.barrage,
        switchPlayer: this.PolyvParams.switchPlayer,
      };
      if (flag) {
        // 回放模式需要fileId、url、sessionId
        // fileId: undefined, //回放时需要
        // url: undefined, //回放时需要
        // sessionId: undefined, //回放时需要
        config.fileId = JSON.parse(this.detailData.play_back_videos)[0].fileId || undefined;
        config.url = JSON.parse(this.detailData.play_back_videos)[0].url || undefined;
        config.sessionId = JSON.parse(this.detailData.play_back_videos)[0].channelSessionId || undefined;
        // if (data.scene == "ppt") {
        //   config.type = "vod";
        // }
        config.type = "vod";
        config.pptType = "record";
      }

      // 监听频道信息并初始化播放器
      liveSdk.on(PolyvLiveSdk.EVENTS.CHANNEL_DATA_INIT, (event, data) => {
        liveSdk.setupPlayer(config);

        //弹幕
        liveSdk.player.on("barrageStatusChange", line => {
          // console.log(line); // 0/1...
          this.barrageStatus = line;
        });
      });
      this.liveSdk = liveSdk;
    },
    //发送弹幕
    sendText(text, color) {
      if (this.barrageStatus) {
        this.liveSdk.player.sendBarrage(text, color || this.charRoomParams.customChatColor);
      }
    },
    //聊天室
    chatToken(data, flag) {
      this.dsf.http
        .get("nc/live/getLiveSign", {
          channelId: data.channelId,
          userId: data.user.userId,
          appId: data.appId,
          role: "guest",
        })
        .done(res => {
          if (res.state === 20000) {
            jq.post(
              "https://api.polyv.net/live/v3/channel/common/get-chat-token",
              {
                appId: data.appId || "", //string	从API设置中获取，在直播系统登记的appId
                timestamp: res.data.timestamp || "", //long	13位当前时间戳
                sign: res.data.sign || "", //是	string	签名值
                channelId: res.data.channelId || "", //是	int	频道Id
                userId: data.user.userId || "", //是	String	观看者用户ID
                role: "guest", //是	String	角色，值有：teacher admin guest assistant viewer等
                origin: res.data.origin,
              },
              resP => {
                if (resP.code == 200) {
                  let config = {
                    roomId: data.channelId || "268682",
                    userId: data.user.userId || "153075602311",
                    nick: data.user.userName || "游客",
                    pic: this.getPic(data.user.pic) || "https://livestatic.videocc.net/assets/wimages/missing_face.png",
                    token: resP.data.token,
                    mediaChannelKey: resP.data.mediaChannelKey,
                    version: "2.0",
                    container: "#chatwrap",
                    width: "100%",
                    height: "100%",
                    userType: data.user.userType || "slice",
                    enableFlower: this.charRoomParams.enableFlower, //送花
                    enableLike: this.charRoomParams.enableLike, //点赞
                    enableRewardAnimation: this.charRoomParams.enableRewardAnimation, //打赏动画效果
                    enableUpdatePageviews: this.charRoomParams.enableUpdatePageviews, //热度更新
                    roomMessage: data2 => {
                      // TODO
                      // data为聊天室socket消息，当有聊天室消息时会触发此方法
                      switch (data2.EVENT) {
                        case "CLOSEROOM": {
                          //关闭聊天室
                          jq(".polyv-input-ele").attr("disabled", true);
                          break;
                        }
                        case "openRoom": {
                          //开启聊天室
                          jq(".polyv-input-ele").removeAttr("disabled");
                          break;
                        }
                        case "SIGN_IN": {
                          //签到
                          this.time = data2.data.limitTime;
                          this.checkinId = data2.data.checkinId;
                          this.showSign = true;
                          this.timer = setInterval(() => {
                            this.time -= 1;
                            if (this.time <= 0) {
                              clearInterval(this.timer);
                            }
                          }, 1000);

                          break;
                        }
                        //结束签到
                        case "STOP_SIGN_IN": {
                          this.showSign = false;
                          //结束签到
                          clearInterval(this.timer);
                          break;
                        }
                        case "CLOSE_DANMU": {
                          //关闭弹幕
                          console.log(3333);
                          break;
                        }
                        case "SPEAK": {
                          // 进入队列时，自己SPEAK消息不触发
                          if (data2.user.userId !== data.user.userId) sendText(data2.content);
                          break;
                        }
                        case "sendMessage": {
                          sendText(data2.content);
                          break;
                        }
                      }
                    },
                  };
                  if (data.scene === "ppt") {
                    config.tabData = [
                      {
                        name: "文档",
                        type: "ppt",
                      },
                      {
                        name: "聊天",
                        type: "chat",
                      },
                      {
                        name: "提问",
                        type: "ask",
                      },
                    ];
                  } else if (this.charRoomParams.tabData.length) {
                    config.tabData = this.charRoomParams.tabData;
                  }
                  this.chatroom = new PolyvChatRoom(config);
                  this.Polyv(data, flag, this.chatroom);
                }
              }
            );
          } else {
            var p = "<p style='font-size:14px;text-align:center;padding:20px;box-sizing:border-box'>聊天室初始化失败</p>";
            document.getElementById("chatwrap").innerHTML = p;
          }
        })
        .error(response => {
          this.dsf.layer.mobile.closeLoading(loader);
          this.dsf.layer.mobile.message("服务器异常", false);
        });
    },
    //签到函数
    submitSigned() {
      this.liveSdk.toSign(this.checkinId, res => {
        let code = JSON.parse(res).code || null;
        if (code === 200) {
          this.dsf.layer.mobile.message("签到成功", true);
          this.showSign = false;
          clearInterval(this.timer);
        }
      });
    },
    //获取头像函数
    getPic(val) {
      // !val || val.indexOf("http://") !== -1 ? null : val;
      if (!val) return null;
      try {
        if (val.length > 2) {
          let url = location.origin + dsf.url.getWebPath(JSON.parse(val)[0].relativePath);
          return url;
        }
      } catch (err) {
        console.error(err);
        return null;
      }
    },
  },
});
</script>
<style lang="scss" scoped>
@import "../../../assets/styles/mobile/index.scss";
.player-signed {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.65);
  font-size: 14px;
  text-align: center;
}
.player-signed-in {
  background: #fff;
  border-radius: 6px;
  border: 1px solid #ccc;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 24em;
  height: 25em;
  margin-top: -12.5em;
  margin-left: -12em;
}
.player-signed .player-signed-in .signed-head {
  width: 100%;
  height: 42px;
  line-height: 42px;
  background-color: #f6f7fb;
  border-bottom: 1px solid #eceff0;
  border-radius: 6px 6px 0 0;
}
.player-signed .player-signed-in .signed-content div {
  width: 120px;
  height: 120px;
  margin: 20px auto;
  border-radius: 50%;
  text-align: center;
  background-color: #e3f2fd;
  line-height: 120px;
  color: #2296f3;
}
.player-signed .player-signed-in .signed-content .btn-signed {
  width: 200px;
  height: 36px;
  margin-top: 30px;
  line-height: 36px;
  text-align: center;
  border-radius: 6px;
  cursor: pointer;
  color: #fff;
  background-color: #8ac34a;
  display: inline-block;
}
</style>
