<template>
  <div class="ds-control mobile ds-globalsearch">
    <div class="ds-globalsearch-header">
      <van-search
        v-model="keyword"
        :label="label"
        :shape="shape"
        :background="background2"
        :maxlength="maxlength < 0 ? Infinity : maxlength"
        :placeholder="placeholder"
        :clearable="clearable"
        :clear-trigger="clearTrigger"
        :autofocus="autofocus"
        :show-action="showAction"
        :action-text="actionText"
        :disabled="disabled"
        :readonly="readonly"
        :error="error"
        :input-align="inputAlign"
        :left-icon="leftIcon"
        :right-icon="rightIcon"
        @focus="isSearching = true"
        @search="onSearch"
        @input="$onChange"
        @cancel="onCancel"
      >
        <template v-slot:left>
          <slot name="left"></slot>
        </template>
        <template v-slot:action>
          <slot name="action">
            <span @click="onCancel">取消</span>
          </slot>
        </template>
        <template v-slot:label>
          <slot name="label"></slot>
        </template>
        <template v-slot:left-icon>
          <slot name="left-icon"></slot>
        </template>
        <template v-slot:right-icon>
          <slot name="right-icon"></slot>
        </template>
      </van-search>
    </div>
    <div v-if="isSearching" class="ds-globalsearch-body">
      <!--级联词汇-->
      <ul v-if="keyword.length" class="cascade-words">
        <li v-for="(item, index) in cascadeKeywords" :key="index" class="cascade-words-item van-hairline--bottom">
          <i class="iconfont icon-sousuo"></i>
          <div class="cascade-words-item-content">
            <div class="ds-multi-ellipsis-1" v-html="item.html"></div>
          </div>
        </li>
      </ul>
      <template v-else>
        <!--热搜-->
        <div class="hot-words ds-globalsearch-section">
          <div class="ds-globalsearch-section-title">热搜</div>
          <div class="hot-words-box">
            <div class="hot-words-list">
              <div v-for="(hot, index) in hotKeywords" :key="index" class="hot-words-item" @click="hotClick(hot)">
                <span class="index">{{ index }}</span>
                <div class="content">
                  <div class="ds-multi-ellipsis-1">{{ hot.text }}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!--历史搜索-->
        <div class="history-words ds-globalsearch-section">
          <div class="ds-globalsearch-section-title">历史搜索</div>
          <div class="history-words-box">
            <div class="history-words-list">
              <div v-for="(history, index) in historyKeywords" :key="index" class="history-words-item" @click="historyClick(hot)">
                <div>{{ history.text }}</div>
              </div>
            </div>
          </div>
          <div class="history-words-bts">
            <div class="history-words-bt" @click="clearHistory">
              <i class="iconfont icon-shanchu"></i>
              <span>清空搜索记录</span>
            </div>
          </div>
        </div>
      </template>
    </div>
    <div v-if="!isSearching" class="ds-globalsearch-body">
      <!--暂无搜索结果-->
      <van-empty description="暂无搜索结果" v-if="!searchResult.length" />
      <!--todo 有搜索结果-->
    </div>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobileNcSearch",
  ctrlCaption: "全局检索",
  mixins: [$mixins.control],
  props: {
    // 搜索框左侧文本
    label: {
      type: String,
      default: "",
    },
    // 图标形状：方形(square)，圆形(round)
    shape: {
      type: String,
      default: "square",
    },
    // 搜索框背景色
    background: {
      type: String,
      default: "theme",
    },
    // 最大输入长度，设置为 -1 的时候不限制最大长度
    maxlength: {
      type: [Number, String],
      default: -1,
    },
    // 占位提示文字
    placeholder: {
      type: String,
      default: "请输入搜索关键词",
    },
    // 是否启用清除图标
    clearable: {
      type: Boolean,
      default: true,
    },
    // 显示清除图标的时机
    clearTrigger: {
      type: String,
      default: "focus",
    },
    // 是否自动聚焦，iOS 系统不支持该属性
    autofocus: {
      type: Boolean,
      default: false,
    },
    // 是否在搜索框右侧显示取消按钮
    showAction: {
      type: Boolean,
      default: false,
    },
    // 取消按钮文字
    actionText: {
      type: [String, Boolean],
      default: "",
    },
    // 是否禁用输入框
    disabled: {
      type: Boolean,
      default: false,
    },
    // 是否只读
    readonly: {
      type: Boolean,
      default: false,
    },
    // 是否将输入内容标红
    error: {
      type: Boolean,
      default: false,
    },
    // 输入框内容对齐方式
    inputAlign: {
      type: String,
      default: "left",
    },
    // 输入框左侧图标
    leftIcon: {
      type: String,
      default: "search",
    },
    // 输入框右侧图标
    rightIcon: {
      type: String,
      default: "",
    },
    // 配置接口地址
    interfaceUrl: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      // 是否搜索中
      isSearching: true,
      // 所搜词
      keyword: "",
      // 级联词汇
      cascadeKeywords: [
        {
          html: '“<span class="search-text">一带一路</span>”的提出与发展',
        },
        {
          html: '“<span class="search-text">一带一路</span>”提倡：思路、重点、难点',
        },
        {
          html: '“<span class="search-text">一带一路</span>”提倡：思路、重点、难点重点、难点重点、难点',
        },
      ],
      // 热搜词汇
      hotKeywords: [
        {
          text: "“一带一路”的提出与发展",
        },
        {
          text: "“一带一路”提倡：思路、重点、难点",
        },
        {
          text: "“一带一路”提倡：思路、重点、难点",
        },
        {
          text: "“一带一路”提倡：思路、重点、难点思路、重点、难点思路、重点、难点",
        },
      ],
      // 历史词汇
      historyKeywords: [
        {
          text: "重要活动",
        },
        {
          text: "军事",
        },
        {
          text: "一带一路提出",
        },
        {
          text: "新发展理念",
        },
        {
          text: "国有企业党建工作",
        },
      ],
      // 搜索结果
      searchResult: [],
    };
  },
  computed: {
    background2() {
      if (this.background == "theme" || this.background == "") {
        return this.$root.themesConfig[this.$root.setting.theme]["main"];
      }
      return this.background;
    },
  },
  created() {
    // 节流
    this.$onChange = _.throttle(this.onChange, 500, {
      leading: true,
      trailing: true,
    });
  },
  methods: {
    // 匹配关键字正则
    // 高亮替换v-html值
    changeColor(resultsList) {
      if (!this.keyword && !this.keyword.length) {
        this.cascadeKeywords = [];
        return;
      }
      this.cascadeKeywords = resultsList.map((item, index) => {
        item.html = item.name.replace(new RegExp(this.keyword, "g"), `<span class="search-text">${this.keyword}</span>`);
        return item;
      });
    },
    onChange(value) {
      console.log(value);
    },
    onSearch() {
      this.isSearching = false;
      // this.$http.get(this.interfaceUrl, {
      //     searchValue: this.keyword
      // })
      //   .done(({data}) => {
      //     console.log('data', data);
      //   })
      //   .error(err => {
      //     dsf.layer.message("搜索失败", false);
      //     this.finished = true;
      //   })
      //   .always(() => {

      //   })
    },
    onCancel() {
      if (this.isSearching && this.keyword != "") {
        this.isSearching = false;
        this.keyword = "";
      } else {
        this.$router.back();
      }
    },
    // 清空搜索历史
    clearHistory() {},
    // 点击热搜
    hotClick(item) {},
    // 点击历史
    historyClick(item) {},
  },
});
</script>
<style lang="scss" scoped>
@import "../../../assets/styles/mobile/index.scss";
// 高级检索
.ds-globalsearch.mobile {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 0;
  box-sizing: border-box;
  position: relative;
  background: #fff;
  * {
    box-sizing: border-box;
  }
  .van-field__control,
  .van-field__left-icon {
    color: #999;
  }
  .van-search__action {
    color: #fff;
    &:active {
      background-color: transparent;
    }
  }
  .ds-globalsearch-header {
    flex: none;
    position: relative;
    z-index: 100;
  }
  .ds-globalsearch-body {
    flex: auto;
    overflow: auto;
    position: relative;
  }
  // 级联词汇
  .cascade-words {
    background-color: #fff;
    @include font_4();
    color: #666;
    &-item {
      display: flex;
      flex-direction: row;
      align-items: center;
      height: 55px;
      .iconfont {
        display: block;
        flex: none;
        width: 65px;
        text-align: center;
        font-size: inherit;
      }
      &-content {
        flex: auto;
        overflow: hidden;
        padding-right: 15px;
        .search-text {
          color: red;
        }
      }
    }
  }
  // 段落
  .ds-globalsearch-section {
    padding: 0 15px;
    margin-bottom: 10px;
    &-title {
      @include font_4();
      color: #333333;
      line-height: 48px;
      font-weight: bold;
    }
  }
  // 热搜
  .hot-words {
    &-box {
      @include font_4();
    }
    &-item {
      display: flex;
      flex-direction: row;
      height: 45px;
      margin-bottom: 2.5px;
      border-radius: 5px;
      overflow: hidden;
      .index {
        display: flex;
        flex: none;
        justify-content: center;
        align-items: center;
        width: 39px;
        height: 100%;
        margin-right: 1px;
        background-color: #fff;
        color: #999;
        font-weight: bold;
      }
      .content {
        display: flex;
        flex: auto;
        align-items: center;
        height: 100%;
        background-color: #fff;
        padding: 0 10px;
        color: #666;
        overflow: hidden;
      }
      &:first-child {
        span {
          @include font-theme("normal");
        }
      }
      &:nth-child(2) {
        span {
          @include font-theme-opacity("normal", 0.8);
        }
      }
      &:nth-child(3) {
        span {
          @include font-theme-opacity("normal", 0.5);
        }
      }
    }
  }
  // 历史搜索
  .history-words {
    &-box {
      width: 100%;
    }
    &-list {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      width: calc(100% + 10px);
      @include font_4();
    }
    &-item {
      display: flex;
      align-items: center;
      background-color: #e9ecef;
      margin-right: 10px;
      margin-bottom: 13px;
      padding: 0 17px;
      border-radius: 20px;
      height: 33px;
      overflow: hidden;
    }
    &-bts {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 23px 0;
      color: #666;
    }
    &-bt {
      @include font_5();
      i {
        margin-right: 5px;
        font-size: inherit;
      }
    }
  }
}
</style>
