<template>
  <div class="ac-player-list">
    <!-- <van-nav-bar title left-arrow @click-left="$router.go(-1)" /> -->
    <div class="list-wrap">
      <!--      <van-collapse v-model="activeNames" style="width:100%">-->
      <!--  -->
      <div
        v-for="(item, $index) in chapters"
        :key="item + $index"
        :name="$index + 1"
        :disabled="item.type_text !== '课程' ? true : false"
        :class="{ noShowItems: item.type_text !== '课程' }"
      >
        <!--  -->
        <div slot="title" style="margin-bottom: 0.2rem">
          <h3 class="item-title">
            <span class="item-title-icon"></span>
            <p style="width: 200px" class="van-ellipsis">{{ item.name }}</p>
            <div class="item-title-time">
              <span>{{ item.xs }}学时</span>
            </div>
          </h3>
        </div>
        <!--  -->
        <ul class="items" v-show="item.type_text == '课程'">
          <li @click="backToPlayer(ccItem)" v-for="(ccItem, $index) in item.kjParaInfo" :key="ccItem + $index">
            <div class="item-info">
              <h4 class="item-info">
                <i v-if="ccItem.kjlx_value == '3'" class="icon iconfont icon-icon_shiyongwendang" style="color: #ff6a00"></i>
                <i v-else-if="ccItem.type_value == '01'" class="item-info-icon icon-course"></i>
                <dsf-icon v-else :name="getName(ccItem)" class="course-icon"></dsf-icon>
                {{ ccItem.name }}
              </h4>
              <span v-if="ccItem.kjsc">{{ ccItem.kjsc }}</span>
            </div>
          </li>
        </ul>
      </div>
      <!--  -->
      <!--      </van-collapse>-->
    </div>
  </div>
  <!-- <div class="item-circle">
                <van-circle
                  v-model="currentRate"
                  layer-color="#D8D8D8"
                  color="#FF6A00"
                  size="27"
                  :rate="30"
                  :speed="100"
                  text="30"
                  stroke-width="110"
                />
  </div>-->
</template>
<script>
/* eslint-disable vue/html-indent */
export default {
  props: {
    type: {
      type: String,
      default: "2",
    },
    id: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      activeNames: [1],
      currentRate: 80,
      currentRate2: "80",
      loaded: false,
      finished: false,
      refreshing: false,
      chapters: null,
    };
  },
  created() {
    this.loadChapters();
    console.log("列表页面");
  },
  methods: {
    getName(ccItem) {
      return ccItem.type_value == "02"
        ? "lianxi"
        : ccItem.type_value == "04" || ccItem.type_value == "03"
        ? "wenjuan"
        : ccItem.type_value == "05"
        ? "word"
        : "";
    },
    backToPlayer(item) {
      this.$emit("playerListItem", item);
    },
    loadChapters() {
      let that = this;
      if (that.refreshing) that.refreshing = false;
      let loader = dsf.layer.loading();
      const chapterUrl = "nc/mobile/tyzj/kcbf/getKcDetailMenu";
      const params = {
        relid: that.id || "2441a3cad5da43d1b5a23e7a5e96d4db",
        type: that.type || 2, //1专题班 2课程
      };
      let errorMsg = "";
      that.dsf.http
        .get(chapterUrl, params)
        .done(function (res) {
          console.log(res);
          if (res.success) {
            // that.chapters = res.data.dyInfo;
            let chapterArr = [];
            for (let i = 0; i < res.data.dyInfo.length; ++i) {
              chapterArr = chapterArr.concat(res.data.dyInfo[i]["kcParaInfo"]);
            }
            that.chapters = chapterArr;
            console.log(that.chapters);
          } else {
            errorMsg = res.message || "获取列表数据异常";
            // dsf.layer.message(res.message || "获取列表数据异常");
          }
        })
        .error(function (error) {
          errorMsg = "服务器异常";
        })
        .always(function () {
          that.loaded = false;
          that.finished = true;
          dsf.layer.closeLoading(loader);
          if (errorMsg) {
            dsf.layer.message(errorMsg, false);
          }
        });
    },
  },
};
</script>
