<template>
  <div v-if="isDesign" class="ds_teacher_list">
    <div class="detail-top">
      <div class="detail-header">
        <div class="avatar">
          <van-image src="https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg"></van-image>
        </div>
        <div class="desc">
          <h3 class="desc-title">
            <span class="name">张三</span>
          </h3>
          <div class="position ellipsis">中央党校（国家行政学院）</div>
          <div class="tags">
            <dl class="tag">自贸区</dl>
          </div>
        </div>
      </div>
      <div class="teacher-desc">
        <span class="text">谢春涛，男，汉族，1963年生，山东临沭人，1988年7月参加工作，1985年7月加入中国共产党，中国人民大学中共党史系…</span>
        <span class="flod">展开</span>
      </div>
    </div>
  </div>
  <div v-else class="ds_teacher_list">
    <div class="detail-top" v-if="it">
      <div class="detail-header">
        <div class="avatar">
          <van-image :src="it['photo'] | imgFormat" fit="cover"></van-image>
        </div>
        <div class="desc">
          <h3 class="desc-title">
            <span class="name">{{ it.name }}</span>
          </h3>
          <div class="position ellipsis">{{ it.deptText }}</div>
          <div class="tags" v-if="it['adeptText']">
            <dl class="tag" v-for="item in it['adeptText'].split('^')" :key="item">{{ item }}</dl>
          </div>
        </div>
      </div>
      <div class="teacher-desc">
        <div
          class="text ellipsis-2"
          ref="teacherIntr"
          v-html="
            it.abstracts ? (it.abstracts.length > 80 ? it.abstracts.substring(0, showFold ? 10000 : 80) + (showFold ? '' : '...') : it.abstracts) : ''
          "
        ></div>
        <span class="flod" v-if="showFoldBtn && it.abstracts" @click="flodText">{{ showFold ? "收起" : "展开" }}</span>
      </div>
    </div>
    <div class="fixed-button">
      <van-button round class="subcrible-btn" @click="toSubscrible(it)">{{ subText }}</van-button>
    </div>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobileNcTeacherDetailTop",
  ctrlCaption: "师资详情",
  mixins: [$mixins.control],
  computed: {
    subText() {
      let text = "订阅";
      if (this.it) {
        text = this.it.mySubscribed ? "已订阅" : "立即订阅";
      }
      return text;
    },
  },
  data() {
    return {
      itemList: [],
      it: null,
      queryiId: this.$route.query.id || "f445282c9db54cc4b90881f4c5569bcb",
      showFold: false,
      showFoldBtn: false,
    };
  },
  mounted() {
    if (!this.isDesign) {
      this.loadTeacherDetail();
    }
  },
  methods: {
    loadTeacherDetail() {
      const sourceUrl = "nc/teacher/info";
      const params = {
        id: this.queryiId,
      };
      const loading = dsf.layer.mobile.loading();
      dsf.http
        .get(sourceUrl, params)
        .done(({ success, data, message }) => {
          if (success) {
            this.it = data;
            this.$nextTick(() => {
              let offsetHeight = this.$refs.teacherIntr.offsetHeight;
              if (offsetHeight >= 50) this.showFoldBtn = true;
            });
          } else {
            error(message);
          }
        })
        .error(({ message }) => {
          error(message);
        })
        .always(res => {
          dsf.layer.mobile.closeLoading(loading);
        });
      function error(err) {
        dsf.layer.mobile.message("查询失败", false);
      }
    },
    flodText() {
      this.showFold = !this.showFold;
    },
    toSubscrible(it) {
      console.log(it);
      const sourceUrl = "/dsfa/subscribe/saveOrCancel";
      const params = {
        ywlxValue: "teacher",
        ywlxText: "师资",
        ywid: it.teacherId,
      };
      const loading = dsf.layer.mobile.loading("订阅中..");
      dsf.http
        .post(sourceUrl, params)
        .done(({ success, data, message }) => {
          if (success) {
            this.it.mySubscribed = data.isDo;
            dsf.layer.mobile.message(message);
          } else {
            error();
          }
        })
        .error(err => {
          error();
        })
        .always(res => {
          dsf.layer.mobile.closeLoading(loading);
        });
      function error() {
        dsf.layer.mobile.message("订阅失败", false);
      }
    },
  },
});
</script>
<style lang="scss" scoped>
@import "../../../assets/styles/mobile/index.scss";
.ds_teacher_list {
  min-height: 220px;
  box-sizing: border-box;
  .detail-top {
    height: 100%;
    min-height: 220px;
    padding: 10px 15px;
    position: relative;
    background-image: linear-gradient(180deg, #0176f6 0%, #5eabff 57%, rgba(14, 122, 255, 0) 98%, rgba(14, 122, 255, 0) 100%);
    .detail-header {
      display: flex;
      padding-bottom: 15px;
      .avatar {
        width: 60px;
        height: 60px;
        overflow: hidden;
        flex-shrink: 0;
        border-radius: 50%;
        margin-right: 20px;
        .el-image,
        .van-image {
          width: 100%;
          height: 100%;
        }
      }
      .desc {
        flex: 1;
        display: flex;
        flex-direction: column;
        // justify-content: space-between;
        &-title {
          .name {
            font-size: 15px;
            color: #fff;
            line-height: 22px;
          }
        }
        .position {
          font-size: 13px;
          color: #fff;
          line-height: 16px;
          padding: 10px 0 0;
        }
        .tags {
          display: flex;
          align-items: center;
          margin-top: 5px;
          .tag {
            font-size: 12px;
            color: #0e8cee;
            background: rgba(255, 255, 255, 0.64);
            border-radius: 9.5px;
            margin: 0 6px;
            padding: 2px 8.5px;
            &:first-child {
              margin-left: 0;
            }
          }
        }
      }
    }
    .teacher-desc {
      background: #ffffff;
      border-radius: 8px;
      position: relative;
      // height: 94px;
      overflow: hidden;
      .text {
        padding: 10px 10px 20px 10px;
        display: block;
        min-height: 94px;
        font-size: 15px;
        color: #333333;
        line-height: 26px;
        overflow: hidden;
      }
      .flod {
        font-size: 12px;
        @include font-theme("normal");
        position: absolute;
        right: 10px;
        bottom: 5px;
        cursor: pointer;
        overflow: hidden;
      }
    }
  }
  .fixed-button {
    height: 60px;
    background: #ffffff;
    border-top: 1px solid #f1f1f1;
    box-sizing: border-box;
    box-shadow: 0 -2px 4px 0 rgba(238, 238, 238, 0.5);
    z-index: 20;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    .subcrible-btn {
      color: #fff;
      display: block;
      width: calc(100% - 30px);
      height: 40px;
      margin: 10px 15px;
      @include background-theme("normal");
    }
  }
}
</style>
