<template>
  <DsfMobileNcDataGrid
    class="news-list"
    height="100%"
    :max-top-number="maxTopNumber"
    :top-props="'teas_jwgl_bjgl_nowbcsjy.sxrs'"
    ref="dsfDatagrid"
    :http-param="datagrid_httpParam"
    :background-color="'#fff'"
    :padding="padding"
    :margin="margin"
    :top-margin="[0, -15, 0, -15]"
    :col="col"
    :limit="limit"
    :hasLoad="hasLoad"
    :hasRefresh="hasRefresh"
  >
    <template v-slot:top="item" v-if="maxTopNumber > 0">
      <div class="news-swiper-list" @click="toDetail(item.data['nc_news_xxlist.id'])">
        <div class="news-swiper-title">
          <p>{{ item.data["nc_news_xxlist.cname"] }}</p>
        </div>
        <div class="news-swiper-img">
          <img :src="getCover(item.data)" alt />
          <p>
            <span>{{ item.data["nc_news_xxlist.cname"] }}</span>
          </p>
        </div>
      </div>
    </template>
    <template v-slot:default="item">
      <div class="news-item" @click="toDetail(item.data['nc_news_xxlist.id'])">
        <div class="news-list-img" :class="{ 'no-image': !getCover(item.data) }">
          <!-- <van-image :src="getCover(item.data+1)" fit="fill" v-if="getCover(item.data)" @error="this.style.display = 'none'">
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
            <template v-slot:error><img :src="require('_nc/assets/styles/themes/img/file-read-18688.png')" style="width:100%;height:100%"></template>
          </van-image> -->
          <img :src="getCover(item.data)" alt="" :onerror="defHide" class="means-img" />
          <div class="news-item-content">
            <p class="van-multi-ellipsis--l2">{{ item.data["nc_news_xxlist.cname"] }}</p>
            <p>
              <span>{{ item.data["nc_news_xxlist.ds_create_time"] }}</span>
              <span> <dsf-icon class="yanjing-icon" name="yanjing"></dsf-icon>{{ item.data["nc_news_xxlist.djcs"] }} </span>
            </p>
          </div>
        </div>
      </div>
    </template>
  </DsfMobileNcDataGrid>
</template>

<script>
import { dsfRouteView } from "../dsfBase";
export default {
  name: "newsList",
  mixins: [dsfRouteView],

  data() {
    return {
      datagrid_search: "",
      datagrid_httpParam: {
        query: JSON.stringify({
          searchValue: "",
        }),
        order: JSON.stringify([]),
        namespace: "nc.news",
        pageName: "xxlist",
      },
      defHide: "this.style.display = 'none'",
    };
  },
  props: {
    detailUrl: {
      type: String,
      default: "",
    },
    maxTopNumber: {
      type: Number,
      default: 5,
    },
    col: {
      type: [String, Number],
      default: 1,
      desc: "一列显示几个，暂支持1和2",
    },
    hasLoad: {
      type: Boolean,
      default: true,
      desc: "是否有上拉加载",
    },
    hasRefresh: {
      type: Boolean,
      default: true,
      desc: "是否有下拉刷新",
    },
    dataSource: {
      type: String,
      default: "meta/list/data",
      desc: "请求接口，可自定义",
    },
    curr: {
      type: [String, Number],
      default: 1,
      desc: "从哪一页开始",
    },
    limit: {
      type: [String, Number],
      default: 12,
      desc: "每页显示多少",
    },
    httpParam: {
      type: Object,
      default: function () {
        return {};
      },
      desc: "接口参数",
    },
    padding: {
      type: Array,
      default() {
        return [0, 15, 0, 15];
      },
    },
    margin: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
  },
  computed: {},
  created: function () {},
  mounted: function () {},
  methods: {
    getCover: function (item) {
      let files = dsf.getUploadFileInfo(item["nc_news_xxlist.cover"]);
      return dsf.url.getWebPath(files[0].relativePath, $$webRoot.nc);
    },
    query: function () {},
    toDetail(id) {
      this.$router.push({
        path: this.detailUrl,
        query: {
          id: id,
        },
      });
    },
    onscreen(who) {
      let _this = this;
      if (_this[who] == "down") {
        _this[who] = "up";
      } else {
        _this[who] = "down";
      }
    },
  },
};
</script>

<style scoped></style>
