<template>
  <div class="ds-control dsf_nc_mobile_player" :style="{ width: itemWidth }">
    <div :class="['course-player-player', { positionAbs: isDesign }]">
      <dsf-mobile-player
        ref="mobilePlayer"
        :videoSource="videoSourceUrl"
        :seektime="seektime"
        :autoPlay="autoPlay"
        @playerPlay="playerHasPlay"
        @playerPause="playerHasPause"
        @playerPlayingTime="playerPlayingTime"
        @playerEnded="playerEnded"
        v-if="curPlayType == 'video'"
        @playerLoadeddata="playerHasLoaded"
      />
      <div :class="['fixed-top-cover', { positionAbs: isDesign }]" v-else>
        <van-image :src="it && it.cover | imgFormat" fit="cover" />
      </div>
    </div>
    <div class="course-player-fixed-content" style="padding-top: 210px" ref="playerFixedContent">
      <div class="course-content-header">
        <div class="course-title" v-if="it">
          <div class="courseTitle">
            <div class="title">
              <div class="detail ellipsis-2">{{ it.title }}</div>
            </div>
            <div class="more">
              <div class="left-click">
                <i class="iconfont icon-icon_yulan"></i>
                <span>0</span>
              </div>
              <!-- <van-button type="default" size="small" class="join-stud-btn" @click="joinStudy">{{ joinText }}</van-button> -->
              <!-- <div class="course-rate-wrap" :class="{ disabled: 1 }">
              <van-rate
                v-model="rateNum"
                class="course-rate"
                :size="18"
                :readonly="rateReadonly"
                :disabled="isPlayError"
                color="#FD952A"
                void-icon="star"
                void-color="#eee"
                @change="rateChange"
              ></van-rate>
              <span>{{ 0 }}分</span>
            </div> -->
            </div>
          </div>
        </div>
      </div>

      <div class="fixed-title-visible">
        <div class="fixed-content-title-wrap" ref="fixedContentTitle">
          <div :class="`fixed-title ${item.active ? 'active' : ''}`" v-for="(item, index) in fixedMenus" :key="item.id" @click="scrollTo(index)">
            <!-- <i class="icon"></i> -->
            <dsf-icon name="location-solid" :style="`visibility:${item.active ? 'inherit' : 'hidden'}`" />
            <span>{{ item.name }}</span>
          </div>
        </div>
      </div>

      <div class="course-container">
        <course-title-content :title="fixedMenus[0].name" ref="courseContainerIntrduce">
          <template slot="content">
            <div class="desc" v-if="it" v-html="it.content"></div>
          </template>
        </course-title-content>
        <course-title-content :title="fixedMenus[1].name" ref="courseContainerChapter">
          <template slot="content">
            <courseChapter :chapters="chapters" v-if="chapters" @changePlay="changePlayerUrl" />
          </template>
        </course-title-content>
        <course-title-content :title="fixedMenus[2].name" ref="courseContainerTeacher">
          <template slot="content">
            <courseTeachers :list="it && it.teacherList ? it.teacherList : []" @toTeacherDetail="toTeacherDetail" />
          </template>
        </course-title-content>
        <div>
          <course-title-content :title="fixedMenus[3].name" :showMore="true" :moreUrl="moreUrl" ref="courseContainerRecommend"></course-title-content>
          <div :class="['insert', { 'insert-wrap': isDesign }]">
            <div slot-name="recommend" :class="['insert-content', { 'insert-border': isDesign }]">
              <slot name="recommend"></slot>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="fixed-iframe" v-if="showIframe">
      <span class="close-icon" @click="showIframe = false">
        <dsf-icon name="guanbi1" />
      </span>
      <iframe :src="videoSourceUrl" frameborder="0"></iframe>
    </div>
    <div :class="['fixed-bottom', { positionAbs: isDesign }]">
      <div class="handle-wrap">
        <div class="handle-items">
          <dl @click="toHandle('fs')">
            <dsf-icon name="fenxiangfangshi1" />
            <span>分享</span>
          </dl>
          <dl @click="toHandle('sc')">
            <dsf-icon :name="collectIcon" />
            <span>收藏</span>
          </dl>
        </div>
        <div class="note" @click="toNote">笔记</div>
        <div class="join-wrap">
          <van-button round class="join-btn" @click="joinStudy">{{ joinText }}</van-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import courseIntrduce from "../../base/course/courseIntrduce";
import courseTeachers from "../../base/course/courseTeachers";
import courseChapter from "../../base/course/courseChapter";
import courseTitleContent from "../../base/course/courseTitleContent";
export default dsf.component({
  name: "DsfMobileNcMobileCoursePlayer",
  ctrlCaption: "课程播放",
  mixins: [$mixins.layout],
  components: {
    courseIntrduce,
    courseTeachers,
    courseChapter,
    courseTitleContent,
  },
  props: {
    moreUrl: {
      type: String,
      default: "/mobile/nc/page/app/recommend/mobile-recommendPage?active=course",
    },
    teacherDetailUrl: {
      type: String,
      default: "",
    },
    itPadding: {
      type: String,
      default: "220px 0 0 0 ",
    },
    slots: {
      type: Array,
      default() {
        return [{ name: "recommend", controls: [] }];
      },
    },
  },
  computed: {
    videoSourceUrl() {
      let src = "";
      const { curPlayItem } = this;
      if (curPlayItem) {
        // const preUrl = dsf.config.setting_public_file_host;
        const type = curPlayItem.rTypeValue;
        const fileUrl = this.filterFileUrl(curPlayItem);
        switch (type) {
          case "link":
            src = curPlayItem.linkPcUrl;
            break;
          case "document":
            src = fileUrl;
            break;
          case "audio":
            src = fileUrl;
            break;
          case "video":
            src = fileUrl;
            break;
          default:
            break;
        }
      }
      return dsf.url.getWebPath(src);
    },
    joinText() {
      let text = "加入在学";
      if (this.item) {
        text = this.item.isPlaned == 1 ? "取消加入在学" : "加入在学";
      }
      return text;
    },
    collectIcon() {
      let type = "shoucang";
      if (this.item) {
        type = this.item.isCollected == 1 ? "shoucang_shixin" : "shoucang";
      }
      return type;
    },
  },
  data() {
    return {
      commentText: "",
      tabActive: "课程介绍",
      rateNum: 0,
      rateReadonly: false, //评分只读状态
      isPlayError: false,
      it: null,
      item: null,
      chapters: null,
      curPlayItem: null, //当前播放的课件
      curPlayType: null, //当前课件类型
      playingTimer: null, //保存进度定时器
      curPlayingTime: "00:00:00", //当前视频播放节点
      seektime: 0, //定位视频播放位置
      id: this.$route.query.id || "53ac5afbae104f9089fd17a178d9d0a2",
      showIframe: false,
      playerLoaded: false, //播放器数据是否初始化完成
      playerPlaying: false, //播放器当前播放状态
      playerInit: true,
      autoPlay: true,
      fixedMenus: [
        {
          name: "课程介绍",
          id: "courseIntrduce",
          active: true,
        },
        {
          name: "课程目录",
          id: "courseChapter",
          active: false,
        },
        {
          name: "教师介绍",
          id: "teacherIntrduce",
          active: false,
        },
        {
          name: "相关推荐",
          id: "courseRecommend",
          active: false,
        },
      ],
    };
  },
  mounted() {
    if (!this.isDesign) {
      this.loadTechaersList();
      this.loadCourseChapterList();
      this.$nextTick(() => {
        console.log(this.$refs.playerFixedContent.offsetHeight);
      });
    }
    window.addEventListener("scroll", this.handleScroll, true);
  },
  methods: {
    reloadData() {
      this.id = this.$route.query.id;
      this.loadTechaersList();
      this.loadCourseChapterList();
    },
    loadTechaersList() {
      const sourceUrl = "nc/course/play/getPlayBase";
      const params = {
        courseId: this.id,
      };
      commoeLoad(sourceUrl, params, "get", res => {
        this.it = res.data;
      });
    },
    loadCourseChapterList() {
      const sourceUrl = "nc/course/play/getPlayTrend";
      const params = {
        courseId: this.id,
      };
      commoeLoad(sourceUrl, params, "get", ({ data }) => {
        console.log(data);
        if (!dsf.isEmptyObject(data)) {
          let { playTree, locationSite } = data;
          this.item = data;
          if (playTree && locationSite) {
            playTree.playItem = locationSite;
            this.chapters = playTree;
            this.curPlayItem = locationSite;
            this.curPlayType = locationSite.rTypeValue;
            this.seektime = this.formatSeekTime(locationSite.lastWatchPoint);
            if (locationSite.rTypeValue == "document") {
              this.showIframe = true;
              this.setSaveProgress();
            }
          }
        }
      });
    },
    changePlayerUrl(it) {
      this.chapters.playItem = it;
      if (it.rTypeValue !== "link") {
        const sourceUrl = "nc/course/play/getPlayRecord";
        const params = {
          courseId: this.id,
          coursewareId: it.id,
        };
        commoeLoad(sourceUrl, params, "get", res => {
          this.curPlayItem = res.data;
          this.curPlayType = it.rTypeValue;
          if (it.rTypeValue == "document") {
            this.showIframe = true;
            this.setSaveProgress();
          }
        });
      }
    },
    playerPlayingTime(it) {
      let data = dsf.date.secondsToTime(it);
      let time = data.hours + ":" + data.minutes + ":" + data.seconds;
      this.curPlayingTime = time;
    },
    formatSeekTime(t) {
      if (!t) t = "00:00:00";
      t = t.split(":");
      var s = t[0] * 60 * 60 + t[1] * 60 + t[2] * 1;
      return s;
    },
    //保存课程进度
    savePlayerProgress() {
      let that = this;
      const sourceUrl = "nc/course/play/pulseSaveRecord";
      const { curPlayingTime, curPlayItem, chapters } = this;
      const { id } = curPlayItem;
      const params = {
        courseId: this.id,
        coursewareId: id,
        watchPoint: curPlayingTime,
        pulseTime: 10,
      };
      commoeLoad(
        sourceUrl,
        params,
        "post",
        ({ data, success }) => {
          if (success) {
            _.each(chapters.children, (item, idx) => {
              if (item.id == curPlayItem.id) {
                that.chapters.children[idx].finishedRate = data.finishedRate;
              }
            });
          }
        },
        err => {
          dsf.log("保存进度异常");
        },
        false
      );
    },
    setSaveProgress() {
      this.clearPlayingTimer();
      this.playingTimer = setInterval(() => {
        this.savePlayerProgress();
      }, 10 * 1000);
    },
    playerHasPlay() {
      dsf.log("播放");
      this.$refs.mobilePlayer.playAudio();
      this.setSaveProgress();
    },
    playerHasPause() {
      dsf.log("暂停");
      if (this.playerLoaded) {
        this.savePlayerProgress();
        this.clearPlayingTimer();
      }
    },
    playerEnded() {
      this.seektime = 0.1;
      this.savePlayerProgress();
      this.clearPlayingTimer();
      this.$refs.mobilePlayer.playerLoadeddata();
    },
    playerHasLoaded() {
      dsf.log("加载完成..");
      this.playerLoaded = true;
    },
    clearPlayingTimer() {
      let { playingTimer } = this;
      if (playingTimer) clearInterval(playingTimer);
    },
    filterFileUrl(item) {
      console.log(item);
      let sourceUrl = "";
      if (item.rTypeValue == "document") {
        if (item.fileAdditionUrl) {
          const preUrl = dsf.config.setting_public_file_host;
          sourceUrl = preUrl + `/preview/file?url=${preUrl}${JSON.parse(item.fileAdditionUrl)[0].relativePath}`;
        }
      } else {
        if (item && item.fileUrl) {
          const sourceItem = JSON.parse(item.fileUrl);
          if (sourceItem.file) {
            sourceUrl = sourceItem.file[0].relativePath;
          } else {
            if (dsf.isArray(sourceItem)) {
              sourceUrl = sourceItem[0].relativePath;
            } else {
              sourceUrl = sourceItem.sd.relative || sourceItem.sd.relativePath;
            }
          }
        }
      }
      return sourceUrl;
    },
    toTeacherDetail(id) {
      const { teacherDetailUrl } = this;
      if (teacherDetailUrl) this.$router.push({ path: teacherDetailUrl + id });
    },
    joinStudy() {
      const sourceUrl = "nc/learn/plan/saveOrCancel";
      const id = this.id;
      const type = this.$route.query.type == "ztb" ? "class" : "course";
      const params = {
        id,
        type,
      };
      commoeLoad(
        sourceUrl,
        params,
        "post",
        ({ message, data, success }) => {
          if (success) this.item.isPlaned = data.isDo;
          dsf.layer.mobile.message(message, data.isDo);
        },
        err => {
          dsf.log("加入在学");
        },
        false
      );
    },
    rateChange() {
      console.log("rate");
    },
    toNote() {
      dsf.layer.mobile.message("暂未开放,敬请期待");
    },
    toHandle(type) {
      if (type == "fs") {
        dsf.layer.mobile.message("暂未开放,敬请期待");
      } else {
        const sourceUrl = "/dsfa/collect/saveOrCancel";
        const params = {
          ywlxValue: "course",
          ywlxText: "课程",
          ywid: this.id,
        };
        commoeLoad(
          sourceUrl,
          params,
          "post",
          ({ message, data, success }) => {
            if (success) this.item.isCollected = data.isDo;
            dsf.layer.mobile.message(message, data.isDo);
          },
          err => {
            dsf.log("收藏");
          },
          false
        );
      }
    },
    toReaommend() {
      let { recommendUrl } = this;
      recommendUrl && this.$router.push({ path: recommendUrl });
    },
    scrollTo(idx) {
      this.$nextTick(() => {
        const el = ["courseContainerIntrduce", "courseContainerChapter", "courseContainerTeacher", "courseContainerRecommend"][idx];
        document.querySelector(".ds-page-center").scrollTo({
          top: this.$refs[el].$el.offsetTop - 310, // +10 表示进入了此模块
          behavior: "smooth", // 平滑滚动
        });
      });
    },
    handleScroll() {
      this.$nextTick(() => {
        let scrollTop = document.querySelector(".ds-page-center").scrollTop;
        let defaultTop = 310; //视频高度 加上 标题高度 后续可以做扩展
        const winHeight = window.innerHeight;
        const docHeight = document.querySelector(".course-player-fixed-content").offsetHeight;
        const dHeight = Math.abs(winHeight - docHeight);
        const intrduceTop = this.$refs.courseContainerIntrduce.$el.offsetTop - defaultTop;
        const chapterTop = this.$refs.courseContainerChapter.$el.offsetTop - defaultTop;
        const teacherTop = this.$refs.courseContainerTeacher.$el.offsetTop - defaultTop;
        const recommendeTop = this.$refs.courseContainerRecommend.$el.offsetTop - defaultTop;
        if (scrollTop >= 100) {
          this.$refs.fixedContentTitle.style.position = "fixed";
          this.$refs.fixedContentTitle.style.top = "210px";
        } else {
          this.$refs.fixedContentTitle.style.position = "relative";
          this.$refs.fixedContentTitle.style.top = "0";
        }
        _.each(this.fixedMenus, (it, idx) => {
          if (scrollTop > intrduceTop && intrduceTop < chapterTop) {
            if (idx == 0) {
              it.active = true;
            } else {
              it.active = false;
            }
          }

          if (scrollTop > chapterTop && intrduceTop < teacherTop) {
            if (idx == 1) {
              it.active = true;
            } else {
              it.active = false;
            }
          }

          if (scrollTop > teacherTop) {
            if (idx == 2) {
              it.active = true;
            } else {
              it.active = false;
            }
          }

          if (scrollTop > recommendeTop) {
            if (idx == 3) {
              it.active = true;
            } else {
              it.active = false;
            }
          }
        });
      });
    },
  },
  beforeDestroy() {
    this.clearPlayingTimer();
    window.removeEventListener("scroll", this.handleScroll);
  },
});

function commoeLoad(sourceUrl, params, method, succFn, failFun, state = true) {
  const loading = state ? dsf.layer.mobile.loading() : "";
  dsf.http[method](sourceUrl, params)
    .done(res => {
      res.success && succFn && succFn(res);
      if (!res.success) error(res);
    })
    .error(err => {
      error(err);
    })
    .always(res => {
      dsf.layer.mobile.closeLoading(loading);
    });
  function error(err) {
    failFun && failFun(err);
    dsf.layer.mobile.message(err.message, false);
  }
}
</script>
<style lang="scss">
@import "../../../assets/styles/mobile/index.scss";
.dsf_nc_mobile_player {
  width: 100%;
  overflow: hidden;
  transition: all 0.5s;
  display: flex;
  flex-direction: column;
  // overflow-y: scroll;
  position: relative;
  background: #fff;
  padding-bottom: 30px;
  .fixed-top-cover {
    width: 100%;
    height: 210px;
    overflow: hidden;
    .van-image {
      width: 100%;
      height: 100%;
    }
  }
  &.ds-control {
    padding: 0 !important;
  }
  .positionAbs {
    position: absolute !important;
  }
  .course-player-player {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
    height: 210px;
  }
  .course-player-fixed-content {
    width: 100%;
    min-height: 1000px;
    box-sizing: border-box;
    position: relative;
    background: #fff;
    .course-content-header {
      height: 100px;
      .course-title {
        background: #fff;
        padding: 10px 15px;
        box-sizing: border-box;
        cursor: pointer;
        .title {
          height: 52px;
          position: relative;
          .type {
            height: 20px;
            display: inline-block;
            padding: 0 8px;
            box-sizing: border-box;
            font-size: 12px;
            border: 1px solid #d60e18;
            color: #d60e18;
            border-radius: 2px;
            line-height: 20px;
            transform: translateY(-2px);
          }
          .detail {
            height: 52px;
            font-size: 17px;
            color: #333;
            // text-indent: 50px;
            font-weight: bold;
            line-height: 26px;
          }
        }

        .more {
          height: 20px;
          line-height: 20px;
          color: #999999;
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding-top: 5px;
          .left-click {
            width: 90px;
            flex-shrink: 0;
            display: flex;
          }
          span {
            font-size: 14px;
          }
          .join-stud-btn {
            color: #fff;
            padding-left: 5px;
            border-radius: 2px;
            @include font_5;
            @include background-theme("normal");
          }
          .course-rate-wrap {
            display: flex;
            align-items: center;
            .course-rate {
              margin-right: 10px;
            }
          }
        }
      }
    }
    .course-tabs {
      .van-tabs__wrap {
        .van-tab__text {
          color: #fff;
          @include font_4;
        }
        .van-tab--active {
          // font-weight: 600;
        }
        .van-tabs__line {
          @include background-theme("main");
        }
      }
    }
    .desc {
      color: #333;
      line-height: 24px;
      @include font_5;
    }
  }
  .insert {
    width: 100%;
    padding: 5px 15px;
    box-sizing: border-box;
    &.insert-wrap {
      .insert-content {
        min-height: 100px;
        border: 1px dashed;
      }
    }
  }
  .fixed-iframe {
    background: #fff;
    z-index: 100;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    .close-icon {
      position: absolute;
      top: 10px;
      right: 10px;
      cursor: pointer;
    }
    iframe {
      width: 100%;
      height: 100%;
    }
  }
  .fixed-bottom {
    height: 60px;
    background: #ffffff;
    border-top: 1px solid #f1f1f1;
    box-sizing: border-box;
    box-shadow: 0 -2px 4px 0 rgba(238, 238, 238, 0.5);
    z-index: 20;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
    .handle-wrap {
      display: flex;
      margin: 10px 15px;
      .handle-items {
        display: flex;
        justify-content: center;
        align-items: center;
        dl {
          display: flex;
          flex-direction: column;
          align-items: center;
          &:first-child {
            margin-right: 30px;
          }
          .iconfont {
            font-size: 21px;
          }
          span {
            font-size: 13px;
          }
        }
      }
    }
    .note {
      @include font-4;
      color: #333333;
      margin: 0 30px 0 40px;
      line-height: 40px;
      position: relative;
      &::before {
        content: "";
        background: #e5e5e5;
        position: absolute;
        left: -15px;
        top: 12px;
        width: 1px;
        height: 16px;
      }
    }
    .join-wrap {
      width: 147px;
      .join-btn {
        color: #fff;
        width: 100%;
        height: 40px;
        line-height: 40px;
        @include font-4;
        @include background-theme("normal");
      }
    }
  }
  .course-tabs {
    .van-tabs__wrap--scrollable .van-tabs__nav {
      @include background-theme("normal");
    }
  }
  .tab-menus {
    @include background-theme("normal");
  }
  .fixed-title-visible {
    height: 54px;
  }
  .fixed-content-title-wrap {
    width: 100%;
    z-index: 2;
    position: relative;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: #ffffff;
    box-shadow: 0 1px 12px 0 rgba(213, 213, 213, 0.5);
    // padding: 20px 0 15px;
    height: 54px;
    .fixed-title {
      display: flex;
      align-items: center;
      cursor: pointer;
      .iconfont {
        margin-right: 5px;
      }
      span {
        font-size: 13px;
        color: #666666;
        line-height: 19px;
      }
      &.active {
        @include font_theme("normal");
        span {
          @include font_theme("normal");
        }
      }
    }
  }
}
</style>
