<!-- 标签选项侧部面板 -->
<template>
  <dsf-side-popup ref="dsfSidePopup" site="body" customClass="dsf-choice-side-popup">
    <div class="content" v-if="isShow">
      <div v-for="(items, listKey) in list" :key="listKey">
        <div class="title">{{ listKey }}</div>
        <div class="box" v-for="(item, key) in items" :key="key">
          <div v-if="item.items.length > 0">
            <div class="title" v-if="items.length > 1">{{ item.name }}</div>
            <div class="data">
              <div class="range" v-if="item.operator == 'range'">
                <van-field v-model="item.v1" :placeholder="'请输入最小' + item.name"></van-field>
                <b />
                <van-field v-model="item.v2" :placeholder="'请输入最大' + item.name"></van-field>
              </div>
              <div
                v-for="(one, i) in item.items"
                :key="i"
                :index="i"
                @click="changeChoice($event, one, item, i)"
                :class="{ big: one.text.length > 5, choice: one['choice'] }"
              >
                {{ one.text }}
              </div>
            </div>
          </div>
          <div v-else>
            <div class="title" v-if="items.length > 1">{{ item.name }}</div>
            <p style="font-size: 12px; text-align: center">暂无数据</p>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom">
      <div @click="changeShow">取消</div>
      <div @click="submit">确定</div>
    </div>
  </dsf-side-popup>
</template>

<script>
export default {
  name: "DsfChoiceSidePopup",
  props: {
    datas: {
      type: Array,
      default: function () {
        return [];
      },
    },
    submitFun: {
      type: Function,
      default: function () {},
    },
  },
  computed: {
    list: {
      get: function (newValue) {
        return this.getList();
      },
      set: function () {
        this.getList = function () {
          return this.refreshList();
        };
      },
    },
  },
  data() {
    return {
      getList: this.refreshList,
      isShow: true,
    };
  },
  methods: {
    changeShow(close) {
      this.$refs.dsfSidePopup.changeShow();
      console.log(1111);
      if (!close && this.$refs.dsfSidePopup.isShow) {
        this.list = {};
        let _this = this;
        setTimeout(function () {
          console.log(_this.list);
        }, 3000);
      }
      this.isShow = this.$refs.dsfSidePopup.isShow;
    },
    refreshList() {
      let _list = {};
      let data = this.datas;
      for (let i in data) {
        let obj = dsf.mix(true, {}, data[i], true);
        obj.index = i;
        for (let k in obj.items) {
          obj.items[k]["choice"] = obj.items[k]["choice"] || false;
        }
        if (!_list[obj.group]) {
          _list[obj.group] = [];
        }
        _list[obj.group].push(obj);
      }
      return _list;
    },
    changeChoice(e, one, parent, i) {
      let dom = e.target;
      one.index = i;
      if (!parent.choice) {
        parent.choice = {};
      }
      let c = one.text.length > 5 ? "big " : "";
      if (parent.choice[one.value]) {
        one.choice = false;
        delete parent.choice[one.value];
      } else {
        one.choice = true;
        if (parent.operator == "range") {
          parent.choice = {};
          parent.v1 = "";
          parent.v2 = "";
          let hasChoice = e.target.parentNode.querySelector(".choice");
          if (hasChoice) {
            hasChoice.setAttribute("class", c);
            parent["items"][hasChoice.getAttribute("index")].choice = false;
          }
          let hasInput = e.target.parentNode.querySelectorAll("input");
          if (hasInput) {
            hasInput[0].value = "";
            hasInput[1].value = "";
          }
        }
        parent.choice[one.value] = one;
      }
      e.target.setAttribute("class", c + (one.choice ? "choice" : ""));
    },
    submit() {
      let list = [];
      let value = {};
      let text = [];
      for (let i in this.list) {
        let items = this.list[i];
        for (let j in items) {
          let item = items[j];
          if (item.operator == "range") {
            if (item.v1 || item.v2) {
              if (item.v1) {
                value[item.queryCol + "_1"] = item.v1;
              }
              if (item.v2) {
                value[item.queryCol + "_2"] = item.v2;
              }
              text.push({
                text: item.name + "：" + (item.v1 ? item.v1 + (item.v2 ? "-" + item.v2 : "以下") : item.v2 + "以上"),
                index: item.index,
                itemI: -1,
                key: item.queryCol,
                type: "range",
              });
              for (let k in item.items) {
                item.items[k]["choice"] = false;
              }
            } else {
              for (let k in item.choice) {
                text.push({
                  text: item.name + "：" + item.choice[k].text,
                  index: item.index,
                  itemI: item.choice[k].index,
                  key: item.queryCol,
                  type: "range",
                });
                item.value = k.split(";");
                if (item.value[0]) {
                  value[item.queryCol + "_1"] = item.value[0];
                }
                if (item.value[0]) {
                  value[item.queryCol + "_2"] = item.value[1];
                }
              }
            }
          } else {
            item.value = [];
            for (let k in item.choice) {
              item.value.push(k);
              text.push({
                text: item.name + "：" + item.choice[k].text,
                index: item.index,
                itemI: item.choice[k].index,
                key: item.queryCol,
                type: item.operator,
                value: k,
              });
            }
            if (item.operator == "like" || item.operator == "slike" || item.operator == "elike") {
              item.value = item.value.join(" ");
              if (item.value) {
                value[item.queryCol] = item.value;
              }
            } else if (item.value.length > 0) {
              value[item.queryCol] = item.value;
            }
          }
          list.push(item);
        }
      }
      this.submitFun(list, value, text);
      this.changeShow(true);
    },
  },
};
</script>
