<template>
  <div class="ac-player" :finished="finished" @load="loadCourse">
    <div v-if="isDesign">播放详情</div>
    <template>
      <div class="player-info" ref="playerInfo">
        <div class="ac-player-wrap">
          <!--ac-player-background-black样式是为了当dsf-player的v-if被重置时元素消失导致页面抖动-->
          <div class="ac-player-background-black">
            <dsf-mobile-nc-player
              ref="player"
              :class="{ audio: currentPlayerType == 'audio' }"
              :cover-img="getCover(courseData.cover)"
              @playerPlay="playerPlayAddPlaySpeedProgress"
              @playerPause="playerPauseAddSpeedProgress"
              @playerPlayingTime="playerPlayingTime"
              @playerEnded="playerEnded"
              :videoSource="videoSrc()"
              :seekTime="seekTime"
              :allowFastForward="true"
              v-if="finished && (currentPlayerType == 'video' || currentPlayerType == 'audio') && isPlayer"
            ></dsf-mobile-nc-player>
            <div class="player-oth-wrap" v-else @click="isPriview">
              <img :src="coverImg" alt />
              <div class="play-icon">
                <i class="def-play-icon"></i>
              </div>
            </div>
          </div>
        </div>
        <van-tabs v-model="tabActive" class="course-play-box">
          <van-tab title="简介" name="简介">
            <div class="content" style="overflow-y: auto">
              <!-- 课程简介开始 -->
              <div class="course-wrap">
                <div class="course-inner">
                  <h3 class="course-title">
                    <span class="title-span">课程简介</span>
                    <van-button type="default" size="small" class="join-stud-btn" v-if="courseData.sfjrzx == 0" @click="joinStudy">
                      加入在学
                    </van-button>
                    <div class="course-rate-wrap" :class="{ disabled: isPlayError }">
                      <!-- <van-rate
                        class="course-rate"
                        v-model="rateNum"
                        :size="18"
                        allow-half
                        color="#FD952A"
                        void-icon="star"
                        void-color="#eee"
                        :readonly="rateReadonly"
                        @change="rateChange"
                        :disabled="isPlayError"
                      /> -->
                      <span>{{ showRate }}</span>
                    </div>
                  </h3>
                  <div class="course-container">
                    <h4 class="con-title">{{ !!courseData ? courseData.title : "暂无标题" }}</h4>
                    <p class="con-desc con-common-padding" v-show="!!courseData.abstract">{{ !!courseData ? courseData.abstract : "暂无" }}</p>
                    <!-- <div class="con-desc-show-wrap con-common-padding" v-show="!!courseData.abstract">
                      <span class="con-desc-show">展开</span>
                    </div>-->
                    <ul class="con-teachers con-common-padding">
                      <li v-for="(item, $index) in courseData.skjsold" :key="item + $index">{{ getTeacherDes(item) }}</li>
                    </ul>
                  </div>
                </div>
              </div>
              <!-- 课程简介结束 -->
              <!-- 教师简介开始 -->
              <div class="course-wrap" v-if="courseData && courseData.skjsnow && courseData.skjsnow.length > 0">
                <div class="course-inner">
                  <h3 class="course-title">教师简介</h3>
                  <div class="course-container" v-for="(item, $index) in courseData.skjsnow" :key="item + $index">
                    <h4 class="con-title con-title-padding">
                      {{ !!item.jsname ? item.jsname : "暂无" }}
                      <span
                        class="con-title-detail"
                        @click="toTeacherDetail(item.jsid)"
                        v-show="item.sfms == '1'"
                      >详情</span>
                    </h4>
                    <p class="con-desc con-common-padding">{{ !!item.dwzwzjzc ? item.dwzwzjzc.replace(/,|，/g, " ") : "" }}</p>
                  </div>
                </div>
              </div>
              <!-- 教师简介结束 -->
            </div>
          </van-tab>
          <van-tab title="章节" name="章节">
            <player-list @playerListItem="playerListItem" :id="$route.query.id" :type="$route.params.type" />
          </van-tab>
        </van-tabs>

        <!-- <div class="other-hide" v-if="!isPlayer">
          <div class="dsf-iframe-wrap" style="height: 100%">
            <i class="iconfont icon-guanbi2 defClose" @click="isPriview"></i>
            <dsf-iframe :src="videoSrc()" @load="iframeLoaded"></dsf-iframe>
          </div>
        </div> -->
      </div>
      <div class="player-fixed-bottom dsf-bottom">
        <interaction ref="interaction" :id="$route.query.id" type="00" :shareOpts="shareOpts" :disabled="isPlayError"></interaction>
      </div>
    </template>
  </div>
</template>
<script>
import PlayerList from "./mobileNcCoursePlayerList";
import Interaction from "./base/interaction";
import * as storeKey from "_nc/store/funName";

export default dsf.component({
  mixins: [$mixins.control],
  name: "DsfMobileNcCoursePlayer",
  ctrlCaption: "播放详情",
  data() {
    return {
      tabActive: "简介",
      isEnd: false,
      isPlaying: false,
      isPlayError: false,
      rateNum: 0,
      showRate: 0.0,
      rateReadonly: false, //评分只读状态
      loaded: false,
      finished: false,
      refreshing: false,
      timer: null, //定时器
      infoPosi: false,
      // listPosi: false,
      tagCollect: {
        dz: 0,
        sc: 0,
      },
      shareOpts: {
        title: "",
        content: "",
        url: window.location.href.split("#/")[0] + "#/shareCourse/" + this.$route.query.id,
      },
      currentPlayerType: "",
      isPlayer: true,
      coverImg: "",
    };
  },
  computed: {
    //课件ID
    playVideoId() {
      return this.$store.state.ncMedia.playVideoId || "";
    },
    playVideoTime() {
      return this.$store.state.ncMedia.playVideoTime || "00:00:00";
    },
    //视频地址信息包含高清、标清、超清
    videoSource() {
      return this.$store.state.ncMedia.src;
    },
    //视频地址
    videoSrc() {
      return () => {
        if (dsf.isUnDef(this.videoSource) || dsf.isUnDef(this.videoSource.sd)) {
          return "";
        } else {
          return $$webRoot.nc + this.videoSource.sd.relative;
        }
      };
    },
    //当前播放时间
    playingTime() {
      return this.$store.state.ncMedia.playingTime;
    },
    //视频初始化播放定位时间
    seekTime() {
      return this.$store.state.ncMedia.seekTime;
    },
    //当前视频进度情况
    progressInfo() {
      return this.$store.state.ncMedia.progressInfo;
    },
    //课程课件数据
    courseData() {
      return this.$store.state.ncMedia.course;
    },
  },
  created() {
  },
  mounted() {
    this.finished = false;
    window.setTimeout(() => {
      if(!this.isDesign) this.loadCourse();
    }, 500);
  },
  methods: {
    recharNum(data) {
      return data * 1;
    },
    isPriview() {
      this.isPlayer = !this.isPlayer;
    },
    loadCourse(loader, kcid) {
      if (this.refreshing) this.refreshing = false;
      loader = loader || this.dsf.layer.loading();
      let errorMsg = "";
      this.finished = false;
      this.$store
        .dispatch('media/Media_GetCourseAll', {
          kcid: kcid ? kcid : this.$route.query.id || "ba8dd9946d1248cea128f44aea2dc696",
          type: this.$route.params.type || 2, //1专题班 2课程
          dwid: "",
        })
        .then((result) => {
          console.log(this.courseData);
          this.rateNum = Number(this.courseData.average_fs || 0);
          this.showRate = this.courseData.rateNum;
          this.rateReadonly = this.courseData.kcsfpj == 1 ? true : false;
          this.shareOpts.title = "课程分享";
          this.shareOpts.content = this.courseData.title;
          // this.shareOpts.images = this.getCover(this.course.cover);
          this.shareOpts.image = dsf.getUploadFileInfo(result.courseData.cover)[0]["absolutePath"];
          this.tagCollect = {
            sc: Number(this.courseData.totalcollect),
            dz: Number(this.courseData.totaltag),
          };
          this.coverImg = this.getCover(this.courseData.cover);
          this.setPlayerType(this.courseData.bfinfo.kjlx_value);
        })
        .catch((err) => {
          errorMsg = err;
        })
        .finally(() => {
          this.loaded = false;
          this.finished = errorMsg ? false : true;
          this.isPlayError = errorMsg ? true : false;
          dsf.layer.closeLoading(loader);
          if (errorMsg) {
            window.setTimeout(() => {
              dsf.layer.message(errorMsg, false);
            }, 500);
          }
        });
    },
    //添加播放记录
    addSpeedProgress() {
      let params = {};
      this.$store
        .dispatch('media/Media_PostProgress', params)
        .then(function (res) {
          if (res.success) {
            if (res.state == "61101") {
              dsf.layer.message(res.message);
            }
          } else {
            dsf.layer.message(res.message, false);
          }
        })
        .catch(function (err) {
          console.log("添加播放记录服务器异常");
        });
    },

    /**
     * 播放器监听事件 start
     */
    //播放事件，定时器开始记录学时
    playerPlayAddPlaySpeedProgress() {
      if (this.currentPlayerType == "video" || this.currentPlayerType == "audio") {
        if (this.timer) {
          clearInterval(this.timer);
        }
        this.timer = setInterval(this.addSpeedProgress.bind(this), 1000 * 10);
      }
    },
    //暂停播放 并提交当前学时
    playerPauseAddSpeedProgress() {
      this.timer && clearInterval(this.timer);
      // this.addSpeedProgress();
    },
    //播放器播放时更新播放时间
    playerPlayingTime(t) {
      // dsf.log("播放中")
      this.isEnd = false;
      this.isPlaying = true;
      // console.log(t + "," + this.playingTime);
      this.$store.commit('media/Media_UpdatePlayTime', Math.ceil(t));
    },
    //播放完成
    playerEnded() {
      dsf.log("播放完成");
      this.addSpeedProgress();
      this.isEnd = true;
      this.isPlaying = false;
    },
    /**
     * 播放器监听事件 end
     */

    formatSeconds(value) {
      let time = dsf.date.secondsToTime(value / 1000);
      console.log(time.hours + ":" + time.minutes + ":" + time.seconds);
      return time.hours + ":" + time.minutes + ":" + time.seconds;
    },
    //格式化进度时间
    formatSeek(t) {
      if (!t) t = "00:00:00";
      t = t.split(":");
      var s = t[0] * 60 * 60 + t[1] * 60 + t[2] * 1;
      return s;
    },
    //跳转到名师详情
    toTeacherDetail(id) {
      this.$router.push("/recommend/AcTeacherDetail/" + id);
    },
    //课程评分
    rateChange(value) {
      // this.showRate = value;
      let that = this;
      let opts = {
        title: "课程评价",
        message: "确定当前评分为: " + value,
        asyncClose: true,
      };
      that.dsf.layer.confirm(
        opts,
        () => {
          let rateUrl = "nc/mobile/tyzj/kcbf/editYwdf";
          let params = {
            ywid: that.courseData.kcid,
            ywlx_text: "课程",
            ywlx_value: "00",
            fs: value,
          };
          let loader = that.dsf.layer.loading();
          let msg = null;
          that.dsf.http
            .post(rateUrl, params)
            .done(function (res) {
              if (res.success) {
                that.dsf.layer.message("评价课程成功");
                let timer = setTimeout(() => {
                  that.loadCourse(loader);
                  clearTimeout(timer);
                }, 1200);
              } else {
                msg = res.message;
              }
            })
            .error(function (error) {
              msg = "课程评价服务器异常";
            })
            .always(function () {
              if (msg) {
                that.dsf.layer.closeLoading(loader);
                that.dsf.layer.message(mgs, false);
              }
            });
        },
        () => {
          console.log("评价取消");
        }
      );
    },
    //加入在学
    joinStudy() {
      let that = this;
      let joinUrl = "zxgl/add";
      let params = {
        relid: that.courseData.kcid,
      };
      let loader = that.dsf.layer.loading();
      let errorMsg = null;
      that.dsf.http
        .get(joinUrl, params)
        .done(function (res) {
          if (res.success) {
            that.dsf.layer.message("加入在学成功");
            that.courseData.sfjrzx = 1;
            // let timer = setTimeout(() => {
            //   // that.loadCourse(loader);
            //   clearTimeout(timer);
            // }, 1200);
          } else {
            errorMsg = res.message;
          }
        })
        .error(function (error) {
          errorMsg = "服务器异常";
        })
        .always(function () {
          if (errorMsg) {
            that.dsf.layer.closeLoading(loader);
            that.dsf.layer.message(errorMsg, false);
          }
        });
    },
    //播放列表点击,切换章节课程
    playerListItem(item) {
      let _this = this;
      // this.jumpPlayerList();
      console.log(item.kjlx_value);
      this.addSpeedProgress(); //切换视频 保存进度 清除定时器
      if (this.timer) clearInterval(this.timer);
      // 1：视频 2：音频 3：文稿 4：富媒体 5:其他
      if (item.kjlx_value == 1 || item.kjlx_value == 2) {
        this.finished = false;
        this.setPlayerType(item.kjlx_value);
        //清空视屏源、重置进度、重置视频定位时间、重做视频播放时间进度
        this.$store.commit('media/Media_SetSource', null);
        this.$store.commit('media/Media_SetProgress', null);
        this.$store.commit('media/Media_SetSeekTime', null);
        this.$store.commit('media/Media_UpdatePlayTime', null);
        let q = dsf.queue();
        q.step((def, v) => {
          //获取课件信息更新播放视频
          let dz = item.kjlx_value == 1 ? item.spdz : item.ypdz;
          let type = item.kjlx_value == 1 ? "video" : "audio";
          let video = JSON.parse(dz)[0] || {};
          if (!video.id) {
            def.reject("该媒体不存在");
          }
          _this.$store
            .dispatch('media/Media_GetSource', {
              fileId: video.id,
              type: type,
            })
            .then((res) => {
              _this.$store.commit('media/Media_ChangeVideo', {
                videoId: item.kjid,
                videoTime: item.kjsc,
              });
              def.resolve();
            })
            .catch((error) => {
              def.reject();
            });
        });
        q.step((def, v) => {
          //获取进度信息
          _this.$store
            .dispatch('media/Media_GetProgress', {
              videoId: _this.playVideoId,
              kcId: _this.courseData.kcid,
            })
            .then(function () {
              def.resolve();
            })
            .catch(function (err) {
              dsf.reject(err);
            });
        });
        q.catch((err) => {
          dsf.layer.message(err,false);
        });
        q.finally(() => {
          this.finished = true;
        });
        q.exec();
      } else {
        if (this.$refs.player) this.$refs.player.pause();
        //试卷或问卷通过iframe打开
        if (item.type_text == "试卷") {
          let _params = {
            examId: item.examid,
            userId: _this.userId,
            deviceType: "mobile",
            businessId: item.businessid,
            entryType: 3,
          };
          let loader = _this.dsf.layer.loading();
          _this.dsf.http
            .get("nc/mobile/tyzj/exam/url", _params)
            .done((d) => {
              _this.dsf.layer.closeLoading(loader);
              if (d.success) {
                _this.$router.push({
                  name: "dsfIframe",
                  params: { url: dsf.config.webRoot + d.data.relativePath },
                });
              } else {
                _this.dsf.layer.message(d.message || "获取列表数据异常", false);
              }
            })
            .error((response) => {
              _this.dsf.layer.closeLoading(loader);
              _this.dsf.layer.message("服务器异常", false);
            });
        } else if (item.type_text == "问卷") {
          let loader = _this.dsf.layer.loading();
          let param = {
            wjid: item.id,
            type: "mobile",
          };
          _this.dsf.http
            .get("nc/mobile/tyzj/wj/tbWj", param)
            .done((d) => {
              _this.dsf.layer.closeLoading(loader);
              if (d.success) {
                _this.$router.push({
                  path: "/iframe?url=" + d.data.fullPath.replace("&isview", "&readonly"),
                });
              } else {
                _this.dsf.layer.message(d.message || "获取跳转链接异常", false);
              }
            })
            .error((response) => {
              _this.dsf.layer.closeLoading(loader);
              _this.dsf.layer.message("服务器异常", false);
            });
        } else {
          this.$store.commit('media/Media_SetSource',null);
          this.$store.commit('media/Media_SetProgress', null);
          this.$store.commit('media/Media_SetSeekTime', null);
          this.$store.commit('media/Media_UpdatePlayTime', null);
          let q = dsf.queue();
          let dz = "",
            type = "",
            fileId = "";
          q.step((def, v) => {
            switch (item.kjlx_value) {
              case "3":
                dz = JSON.parse(item.wgdz)[0];
                type = "text";
                fileId = "/preview/file?fileId=" + dz.id;
                break;
              case "4":
                dz = item.yddz;
                type = "media";
                fileId = item.yddz;
                break;
              default:
                dz = item.yddz;
                type = "media";
                fileId = item.yddz;
                break;
            }
            _this.isPlayer = false;
            _this.$store
              .dispatch('media/Media_GetSource', {
                fileId: fileId,
                type: type,
              })
              .then((res) => {
                _this.$store.commit('media/Media_ChangeVideo', {
                  videoId: item.kjid,
                  videoTime: item.kjsc,
                });
                def.resolve();
              })
              .catch((error) => {
                def.reject();
              });
          });
          q.step((def, v) => {
            //获取进度信息
            _this.$store
              .dispatch('media/Media_GetProgress', {
                videoId: _this.playVideoId,
                kcId: _this.courseData.kcid,
              })
              .then(function () {
                def.resolve();
              })
              .catch(function (err) {
                dsf.reject(err);
              });
          });
          q.catch((err) => {
            dsf.layer.message(err);
          });
          q.finally(() => {
            this.finished = true;
            console.log(type)
            this.currentPlayerType = type;
          });
          q.exec();
        }
      }
    },
    getTeacherDes(item) {
      let jsname = !item.jsname ? '' : item.jsname;
      return jsname + (!item.dwzwzjzc ? '' : " " + item.dwzwzjzc.replace(/,|，/g, " "));
    },
    setPlayerType(type) {
      if (type == 2) {
        this.currentPlayerType = "audio";
      } else if (type == 1) {
        this.currentPlayerType = "video";
      } else if (type == 4) {
        this.currentPlayerType = "media";
      } else if (type == 3) {
        this.currentPlayerType = "text";
      } else if (type == 5) {
        this.currentPlayerType = "other";
      }
    },
    getCover(cover) {
      let file = dsf.getUploadFileInfo(cover);
      let relativePath = file[0].relativePath;
      return relativePath ? $$webRoot.nc + relativePath : require("_nc/assets/styles/themes/img/file-read-18688.png");
    },
    iframeLoaded() {
      if (this.currentPlayerType == "text" || this.currentPlayerType == "media" || this.currentPlayerType == "other") {
        if (this.timer) {
          clearInterval(this.timer);
        }
        let t = this.seekTime;
        this.timer = setInterval(() => {
          t += 10;
          this.$store.commit('media/Media_UpdatePlayTime', Math.ceil(t));
          this.addSpeedProgress();
        }, 1000 * 10);
      }
    },
    //  closePriview() {
    //    this.finished = false;
    //    this.currentPlayerType = "audio";
    //  }
  },
  beforeDestroy() {
    if (this.timer) clearInterval(this.timer);
  },
  components: {
    PlayerList,
    Interaction,
  },
  // watch: {
  //   listPosi(v, ov) {
  //     if (v) {
  //       this.$refs.player.pause();
  //     } else {
  //       this.$refs.player.play();
  //     }
  //   }
  // },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      if (to.name == "Player") {
        vm.$store.commit('media/Media_UseMini', false);
      } else {
        if (this.currentPlayerType == "audio" || this.currentPlayerType == "video") {
          vm.$store.commit('media/Media_UseMini', true);
        }
      }
    });
  },
  beforeRouteLeave(to, form, next) {
    let s = this.$store;
    if (to.name != "dsfIframe" && (this.currentPlayerType == "audio" || this.currentPlayerType == "video")) {
      if (!this.isPlayError) {
        if (!this.isEnd && this.isPlaying) {
          //视频观看到一半未结束的情况下退出页面后切换到迷你播放器并展开自动播放
          s.commit('media/Media_UseMini', true);
          s.commit('media/Media_MiniHide', false);
          s.commit('media/Meida_MiniAutoPlay', true);
        } else {
          s.commit('media/Media_UseMini', true);
          s.commit('media/Media_MiniHide', false);
          s.commit('media/Meida_MiniAutoPlay', false);
        }
      } else {
        //如果当前页面加载课程播放信息出现错误，则需要将vuex中的播放器全局对象数据进行一次回滚
        s.commit('media/Media_RollBack');
      }
    } else {
      s.commit('media/Media_UseMini', false);
      s.commit('media/Media_MiniHide', false);
      s.commit('media/Meida_MiniAutoPlay', false);
    }
    s = null;
    next();
  },
});
</script>
