<template>
  <div class="channel-detail-source-item" @click="goSource">
    <div class="source-img">
      <van-image :src="getCover()" fit="fill">
        <template v-slot:loading>
          <van-loading type="spinner" size="20" />
        </template>
        <template v-slot:error>
          <img src="_nc/assets/styles/themes/img/file-read-18688.png" style="width: 100%; height: 100%" />
        </template>
      </van-image>
      <div class="source-img-info">{{ item["nc_pd_pdgldata.type_text"] || "" }}</div>
    </div>
    <div class="source-title">{{ item["nc_pd_pdgldata.title"] || ""}}</div>
  </div>
</template>

<script>
export default {
  name: "channelSourceItem",
  data() {
    return {};
  },
  props: {
    item: {
      type: Object,
      default: function () {
        return {};
      },
    },
  },
  computed: {},
  components: {},
  created: function () {},
  mounted: function () {},
  methods: {
    getCover: function () {
      if (!this.item["nc_pd_pdgldata.cover"]) {
        return "_nc/assets/styles/themes/img/file-read-18688.png";
      }
      let files = dsf.getUploadFileInfo(this.item["nc_pd_pdgldata.cover"]);
      return dsf.url.getWebPath(files[0].relativePath, $$webRoot.nc);
    },
    goSource() {
      let id = this.item["nc_pd_pdgldata.nc_zyb_id"];
      if (this.item["nc_pd_pdgldata.type_value"] == "00") {
        // 课程
        this.$router.push("/mobile/nc/recommendFile/mobile-courseDetail?id=" + id);
      } else if (this.item["nc_pd_pdgldata.type_value"] == "01") {
        // 频道
        this.$router.push("/mobile/nc/recommendFile/mobile-chanelDetail?id=" + id);
      } else if (this.item["nc_pd_pdgldata.type_value"] == "05") {
        // 专题班
        this.$router.push("/mobile/nc/recommendFile/mobile-specilaDetail?id=" + id);
      } 
    },
  },
};
</script>

<style scoped></style>
