<template>
  <div class="course-title-content ds-control">
    <div class="comment-wrap">
      <div class="commen-title">
        <h3 class="title-name">
          {{ title }}
          <span class="recommend-text" v-if="showMore" @click="toMorePage">
            <slot name="more">更多></slot>
          </span>
        </h3>
      </div>
    </div>
    <div class="content">
      <slot name="content"></slot>
    </div>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobleCourseContentTitle",
  ctrlCaption: "课程播放内容标题",
  mixins: [$mixins.control],
  props: {
    title: {
      type: String,
      default: "培训介绍",
      desc: "标题名称",
    },
    moreUrl: {
      type: String,
      default: "",
    },
    showMore: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {};
  },
  components: {},
  computed: {},
  created() {},
  mounted() {},
  methods: {
    toMorePage() {
      let { moreUrl } = this;
      moreUrl && this.$router.push({ path: moreUrl });
    },
  },
});
</script>
