<template>
  <van-row
    class="interaction-comp dsf-bottom"
    :class="{ disabled: disabled }"
    type="flex"
    justify="space-around"
  >
    <van-col span="2" @click="likeChange">
      <dsf-icon
        class="dianzan-icon"
        :name="disabled || liked ? 'dianzan_kuai' : 'dianzan'"
      ></dsf-icon>
      <span class="num">{{ likeNum > 0 ? likeNum : 0 }}</span>
    </van-col>
    <van-col span="2" @click="collectChange">
      <dsf-icon
        class="aixin-icon"
        :name="disabled || collected ? 'aixin_shixin' : 'aixin'"
      ></dsf-icon>
      <span class="num">{{ collectNum > 0 ? collectNum : 0 }}</span>
    </van-col>
    <!-- <van-col span="1" @click="shareChange" class="share">
      <dsf-icon class="fenxiang-icon" name="share"></dsf-icon>
      <span class="num">{{ shareNum > 0 ? shareNum : 0 }}</span>
    </van-col>
    <van-col v-if="showSlot" :style="{ width: slotWidth + 'rem' }">
      <slot name="content"></slot>
    </van-col> -->
  </van-row>
</template>

<script>
export default {
  props: {
    id: {
      type: [String, Number],
      default: "",
      desc: "事务id",
    },
    type: {
      type: String,
      default: "",
      desc: "事务类型(课程00、新闻xw、学员说xys、频道pd、名师ms、专题班ztb)",
    },
    likedFun: {
      type: Function,
      default: function () {},
    },
    collectedFun: {
      type: Function,
      default: function () {},
    },
    shareOpts: {
      type: Object,
      default: function () {
        return {};
      },
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    showSlot: {
      type: Boolean,
      default: false,
    },
    slotWidth: { type: [String, Number], default: "" },
  },
  data() {
    return {
      param: {
        ywlxValue: this.type,
        ywid: this.id,
      },
      liked: false,
      collected: false,
      likeNum: 0,
      collectNum: 0,
      shareNum: 0,
    };
  },
  created() {
    if(!this.isDesign){
      // 获取点赞状态
      this.getLiked();
      // 获取收藏状态
      this.getCollected();
      //获取分享
      this.getShare();
    }
  },
  methods: {
    getLiked() {
      dsf.http
        .get("nc/mobile/tyzj/hd/tags/isTags", this.param, $$webRoot.nc)
        .done((res) => {
          if (res.success) {
            this.liked = res.data.isTags;
            this.likeNum = res.data.num;
          } else {
            dsf.layer.message(res.message, false);
          }
        })
        .error(function (err) {
          dsf.layer.message(err.message, false);
        });
    },
    getCollected() {
      dsf.http
        .get("nc/mobile/tyzj/hd/collect/isCollect", this.param, $$webRoot.nc)
        .done((res) => {
          if (res.success) {
            this.collected = res.data.isCollect;
            this.collectNum = res.data.num;
          } else {
            dsf.layer.message(res.message, false);
          }
        })
        .error(function (err) {
          dsf.layer.message(err.message, false);
        });
    },
    likeChange() {
      if (!this.disabled) {
        this.http(0, (d) => {
          this.liked = !this.liked;
          this.likeNum = this.likeNum + (this.liked ? 1 : -1);
          this.likedFun(d);
        });
      }
    },
    collectChange() {
      if (!this.disabled) {
        this.http(1, (d) => {
          this.collected = !this.collected;
          this.collectNum = this.collectNum + (this.collected ? 1 : -1);
          this.collectedFun(d);
        });
      }
    },
    http(i, callBack) {
      let loader = this.dsf.layer.loading();
      let sql = [
        "nc/mobile/tyzj/hd/tags/saveOrCancel",
        "nc/mobile/tyzj/hd/collect/saveOrCancel",
      ][i];
      // let text = this[["liked", "collected"][i]];
      dsf.http
        .post(sql, this.param, $$webRoot.nc)
        .done((res) => {
          dsf.layer.closeLoading(loader);
          if (res.success) {
            dsf.layer.message(res.message, true);
            callBack(res.data);
          } else {
            dsf.layer.message(text, false);
          }
        })
        .error(function (err) {
          dsf.layer.closeLoading(loader);
          dsf.layer.message(err.message, false);
        });
    },
    getShare() {
      dsf.http
        .get("nc/mobile/tyzj/hd/getShareCount", this.param, $$webRoot.nc)
        .done((res) => {
          if (res.success) {
            this.shareNum = res.data;
            dsf.layer.message(res.message, true);
          } else {
            dsf.layer.message(res.message, false);
          }
        })
        .error(function (err) {
          dsf.layer.message(err.message, false);
        });
    },
    saveShare() {
      dsf.http
        .post("nc/mobile/tyzj/hd/share/saveOrCancel", this.param, $$webRoot.nc)
        .done((res) => {
          if (res.success) {
            // this.shareNum = res.data.num;
            dsf.layer.message(res.message, true);
            this.getShare();
          } else {
            dsf.layer.message(res.message, false);
          }
        })
        .error(function (err) {
          dsf.layer.message(err.message, false);
        });
    },
    shareChange() {
      dsf.platform.share(
        dsf.mix(
          true,
          {
            url: window.location.href,
            onSuccess: (data) => {
              this.saveShare();
            },
          },
          this.shareOpts
        )
      );
    },
  },
};
</script>