<template>
  <div>
    <dsf-treeselect-sidepopup
      ref="dsfTreeselectSidepopup"
      site="body"
      customClass="dsf-treeselect-recommend"
      :datas="choiceAllData"
      :submitFun="submitFunType"
    ></dsf-treeselect-sidepopup>
    <!-- DsfChoiceSidePopup -->
    <dsf-choice-side-popup ref="dsfChoiceSidepopup" :datas="choiceData" :submitFun="submitFun"></dsf-choice-side-popup>
    <DsfMobileNcDataGrid
      class="ac-course"
      :style="{ height: '100%' }"
      ref="dsfDatagrid"
      :http-param="datagrid_httpParam"
      :background-color="'#fff'"
      :padding="padding"
      :margin="margin"
      :col="col"
      :limit="limit"
      :hasLoad="hasLoad"
      :hasRefresh="hasRefresh"
    >
      <!--<template v-slot:search v-if="hasFilter">
        <div>
          <div class="ac-teacher-list-screen">
            <div class="ac-tch-lst-screen-item" @click="changeTreeSelectPopupShow">
              <dsf-icon name="quanbu" class="ac-tch-lst-screen-icon left"></dsf-icon>
              <span class="ac-tch-lst-screen-fun">全部</span>
            </div>
            <div class="ac-tch-lst-screen-item" @click="onscreen('ds_create_time')">
              <dsf-sort :iconName="'最新'"></dsf-sort>
            </div>
            <div class="ac-tch-lst-screen-item" @click="onscreen('ckl')">
              <dsf-sort :iconName="'最热'"></dsf-sort>
            </div>
            <div class="ac-tch-lst-screen-item" @click="changeSidePanelShow">
              <dsf-icon name="shaixuan" class="ac-tch-lst-screen-icon left"></dsf-icon>
              <span class="ac-tch-lst-screen-fun">筛选</span>
            </div>
          </div>
          <div class="dsf-searchopts" v-if='choiceText.length>0'>
            <span v-for="(obj,index) in choiceText" :key="index">
              {{obj.text}}
              <dsf-icon name='icon2' @click="deleteSerachV(obj,index)"></dsf-icon>
            </span>
          </div>
        </div>
      </template>-->
      <template v-slot:default="item">
        <div class="course-item" @click="toDetail(item.data._id)">
          <div class="course-img-wrap">
            <van-image :src="setImg(item.data)" fit="full" :width="'100%'" :height="'100%'">
              <template v-slot:loading>
                <van-loading type="spinner" size="20" />
              </template>
              <template v-slot:error>
                <img :src="require('_nc/assets/styles/themes/img/file-read-18688.png')" style="width: 100%; height: 100%" />
              </template>
            </van-image>
            <div class="course-time">
              <i class="time-icon"></i>
              <span>{{ item.data["nc_szk_mskcdetail.kcsc"] || "暂无课程时长" }}</span>
            </div>
          </div>
          <div class="course-desc">
            <h3 class="course-desc-title">
              <i class="course-desc-new" v-if="item.data['nc_szk_mskcdetail.sfnew']">new</i>{{ item.data["nc_szk_mskcdetail.title"] || "暂无标题" }}
            </h3>
            <p class="course-desc-teacher">{{ item.data["nc_szk_mskcdetail.skjs"] || "&nbsp;" }}</p>
            <div class="course-info">
              <span>
                <i class="course-info-icon-xs"></i>
                {{ item.data["nc_szk_mskcdetail.kcxs"] || 0 }}学时
              </span>
              <span>
                <i class="course-info-icon-see"></i>
                {{ item.data["nc_szk_mskcdetail.gkcs"] || 0 }}
              </span>
            </div>
          </div>
        </div>
      </template>
    </DsfMobileNcDataGrid>
  </div>
</template>
<script>
export default {
  name: "teacherCourseList",
  data() {
    return {
      datagrid_search: "",
      datagrid_httpParam: {},
      ds_create_time: "desc",
      ckl: "desc",
      choiceData: [],
      choiceText: [],
      labelText: [],
      choiceAllData: [],
      typeText: [],
      typeIds: [],
    };
  },
  props: {
    tchId: {
      type: String,
      default: "",
      desc: "教师id",
    },
    top: {
      type: String,
      default: "desc",
      desc: "最新排序",
    },
    hot: {
      type: String,
      default: "desc",
      desc: "最热排序",
    },
    col: {
      type: [String, Number],
      default: 2,
      desc: "一列显示几个，暂支持1和2",
    },
    hasLoad: {
      type: Boolean,
      default: true,
      desc: "是否有上拉加载",
    },
    hasRefresh: {
      type: Boolean,
      default: true,
      desc: "是否有下拉刷新",
    },
    dataSource: {
      type: String,
      default: "meta/list/data",
      desc: "请求接口，可自定义",
    },
    curr: {
      type: [String, Number],
      default: 1,
      desc: "从哪一页开始",
    },
    limit: {
      type: [String, Number],
      default: 12,
      desc: "每页显示多少",
    },
    httpParam: {
      type: Object,
      default: function () {
        return {};
      },
      desc: "接口参数",
    },
    hasFilter: {
      type: Boolean,
      default: true,
    },
    padding: {
      type: Array,
      default() {
        return [15, 8.5, 0, 8.5];
      },
    },
    margin: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
    detailRoute: {
      type: String,
      default: "",
    },
  },
  created() {
    this.initHttp();
  },
  methods: {
    initHttp() {
      let _this = this;
      let _datagrid_httpParam = {
        query: "{}",
        order: JSON.stringify([
          {
            code: "ds_update_time",
            sort: _this.top,
          },
          {
            code: "gkcs",
            sort: _this.hot,
          },
        ]),
        namespace: "nc.szk",
        pageName: "mskclist",
        id: _this.tchId,
      };
      this.datagrid_httpParam = _datagrid_httpParam;
    },
    toDetail(id) {
      this.$router.push({
        path: "/player/" + id + "/2",
      });
    },
    onscreen() {},
    onSearch() {
      this.query({
        searchValue: this.datagrid_search,
      });
    },
    changeSidePanelShow() {
      this.$refs.dsfChoiceSidepopup.changeShow();
      if (this.choiceData.length == 0) {
        this.getKcSxx();
      }
    },
    getKcSxx() {
      let loader = dsf.layer.loading();
      this.$http
        .get("nc/mobile/tj/kc/getKcSxx", {})
        .done((d) => {
          dsf.layer.closeLoading(loader);
          if (d.success) {
            this.choiceData = d.data;
          } else {
            dsf.layer.message(d.message || "获取列表数据异常", false);
          }
        })
        .error((response) => {
          dsf.layer.closeLoading(loader);
          dsf.layer.message("服务器异常", false);
        });
    },
    submitFun(data, value, text) {
      this.choiceData = data;
      this.labelText = text;
      this.choiceText = this.typeText;
      this.choiceText = this.choiceText.concat(text);
      this.query(value);
    },
    deleteSerachV(obj, index) {
      this.choiceText.splice(index, 1);

      let type = dsf.type(this.datagrid_httpParam.query);
      let query = this.datagrid_httpParam.query;
      if (type === "string") {
        query = JSON.parse(this.datagrid_httpParam.query);
      } else {
        query = dsf.mix(true, {}, query);
      }

      if (obj.type == "tree") {
        let idI = this.typeIds.indexOf(obj.value);
        this.typeIds.splice(idI, 1);
        let v = query[obj.key].split(" ");
        v.splice(v.indexOf(obj.value), 1);
        query[obj.key] = v.join(" ");
        let i = index - this.labelText.length;
        this.typeText.splice(i, 1);
      } else {
        if (obj.type == "range" && obj.itemI == -1) {
          this.choiceData[obj.index].v1 = "";
          this.choiceData[obj.index].v2 = "";
        } else {
          this.choiceData[obj.index].items[obj.itemI]["choice"] = false;
        }
        if (obj.type == "range") {
          delete query[obj.key + "_1"];
          delete query[obj.key + "_2"];
          this.choiceData[obj.index]["choice"] = {};
        } else {
          let v = query[obj.key];
          let isArray = true;
          if (obj.type == "like" || obj.type == "slike" || obj.type == "elike") {
            v = v.split(" ");
            isArray = false;
          }
          v.splice(v.indexOf(obj.value), 1);
          if (isArray) {
            query[obj.key] = v;
          } else {
            query[obj.key] = v.join(" ");
          }
          delete this.choiceData[obj.index]["choice"][obj.value];
        }
        let i = index - this.typeText.length;
        this.labelText.splice(i, 1);
      }
      this.query({}, query);
    },
    changeTreeSelectPopupShow() {
      this.$refs.dsfTreeselectSidepopup.changeShow(this.typeIds);
      if (this.choiceAllData.length == 0) {
        let loader = dsf.layer.loading();
        this.$http
          .get("meta/tree/dbSource", {
            rmId: "5ea7f2a5050d401fa129b04f28f3c38e",
          })
          .done((d) => {
            dsf.layer.closeLoading(loader);
            if (d.success) {
              this.choiceAllData = d.data;
            } else {
              dsf.layer.message(d.message || "获取列表数据异常", false);
            }
          })
          .error((response) => {
            dsf.layer.closeLoading(loader);
            dsf.layer.message("服务器异常", false);
          });
      }
    },
    submitFunType(key, ids, text) {
      this.typeText = text;
      this.choiceText = this.labelText;
      this.choiceText = this.choiceText.concat(text);
      this.typeIds = ids;
      this.query({
        [key]: ids.join(" "),
      });
    },
    query(value, query) {
      query = query || this.datagrid_httpParam.query;
      let type = dsf.type(query);
      if (type === "string") {
        query = JSON.parse(query);
      } else {
        query = dsf.mix(true, {}, query);
      }

      query = dsf.mix(query, value);
      this.datagrid_httpParam.query = JSON.stringify(query);
      this.$refs.dsfDatagrid.query();
    },
    setImg(data) {
      let cover = data["nc_szk_mskcdetail.cover"];
      if (cover) {
        cover = dsf.url.getWebPath(JSON.parse(cover)[0]["relativePath"], $$webRoot.nc);
      }
      return cover || "/";
    },
    queryOrder() {
      this.datagrid_httpParam.order = JSON.stringify([
        {
          code: "ds_update_time",
          sort: this.top,
        },
        {
          code: "gkcs",
          sort: this.hot,
        },
      ]);
      this.$refs.dsfDatagrid.query();
    },
  },
  watch: {
    top: {
      async handler(nv, ov) {
        // console.log("指标变化", nv);
        if (nv && nv != ov) {
          this.queryOrder();
        }
      },
      immediate: false,
    },
    hot: {
      async handler(nv, ov) {
        // console.log("指标变化", nv);
        if (nv && nv != ov) {
          this.queryOrder();
        }
      },
      immediate: false,
    },
  },
};
</script>
