import {
    DsfMobileNcCoursePlayer
} from "../player";
import {
    DsfMobileNcSpecilaDetail
} from "../recommend/special";
import {
    DsfMobileNcNewsDetail
} from "../recommend/news";
import {
    DsfMobileNcChannelDetail
} from "../recommend/channel";
import {
    DsfMobileNcTeacherDetail
} from "../recommend/teacher";
import {
    DsfMobileNcLiveSign
} from "../recommend/live";

let router = [{
        path: "/mobile/nc/coursePlayer",
        name: "dsfMobileNcCoursePlayer",
        component: DsfMobileNcCoursePlayer,
        desc: '课程详情'
    },
    {
        path: "/mobile/nc/specialDetail",
        name: "dsfMobileNcSpecilaDetail",
        component: DsfMobileNcSpecilaDetail,
        desc: '专题详情'
    },
    {
        path: "/mobile/nc/newsDetail",
        name: "dsfMobileNcNewsDetail",
        component: DsfMobileNcNewsDetail,
        desc: '新闻详情'
    },
    {
        path: "/mobile/nc/channelDetail",
        name: "dsfMobileNcChannelDetail",
        component: DsfMobileNcChannelDetail,
        desc: '频道详情'
    },
    {
        path: "/mobile/nc/teacherDetail",
        name: "dsfMobileNcTeacherDetail",
        component: DsfMobileNcTeacherDetail,
        desc: '师资详情'
    },
    {
        path: "/mobile/nc/liveSign",
        name: "dsfMobileNcLiveSign",
        component: DsfMobileNcLiveSign,
        desc: '直播详情'
    },
];

export default router;