<!-- 树选择侧面面板 -->
<template>
  <div>
    <van-popup
      class="dsf-choice-side-popup dsf-treeselect-sidepopup"
      :overlay-style="{ background: 'transparent' }"
      :class="customClass"
      :get-container="site"
      ref="popup"
      v-model="isShow"
      position="left"
      :style="{ width: '100%', height: '100%' }"
    >
      <van-tree-select
        v-if="isShow"
        :style="{ height: 'calc(100% - 0.98rem)' }"
        :items="list"
        :main-active-index="mainActiveIndex"
        :active-id="activeIds"
        @click-nav="onNavClick"
        @click-item="onItemClick"
      />
      <div class="bottom">
        <dsf-mobile-button class="cancel" @click="changeShow" plain type="primary">取消</dsf-mobile-button>
        <dsf-mobile-button class="submit" @click="submit">确定</dsf-mobile-button>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  name: "DsfTreeselectSidepopup",
  props: {
    show: {
      type: Boolean,
      default: false,
      desc: "默认是否显示",
    },
    datas: {
      type: Array,
      default: function () {
        return [];
      },
    },
    submitFun: {
      type: Function,
      default: function () {},
    },
    site: {
      default: "",
      desc: "挂载",
    },
    customClass: {
      type: String,
      default: "",
    },
    fmainActiveIndex: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      isShow: this.show,
      mainActiveIndex: this.fmainActiveIndex,
      activeIds: [],
      old_activeIds: [],
      activeTexts: {},
    };
  },
  computed: {
    list() {
      return dsf.mix(true, [], this.datas);
    },
  },
  methods: {
    changeShow(ids) {
      this.activeIds = ids;
      this.isShow = !this.isShow;
      if (this.isShow) {
        this.activeIds = dsf.mix(true, [], this.old_activeIds);
      }
    },
    onNavClick(index) {
      this.mainActiveIndex = index;
    },
    onItemClick(data) {
      let index = this.activeIds.indexOf(data.id);
      if (index > -1) {
        this.activeIds.splice(index, 1);
      } else {
        this.activeIds.push(data.id);
        data.ptext = this.list[this.mainActiveIndex].text;
        this.activeTexts[data.id] = data;
      }
    },
    submit() {
      this.old_activeIds = dsf.mix(true, [], this.activeIds);
      let text = [];
      let key = null;
      for (let i in this.old_activeIds) {
        let obj = this.activeTexts[this.old_activeIds[i]];
        text.push({
          text: obj.ptext + ">" + obj.text,
          value: obj.id,
          type: "tree",
          key: obj.queryCol,
        });
        key = obj.queryCol;
      }
      this.submitFun(key, this.old_activeIds, text, this.list[this.mainActiveIndex]);
      this.changeShow();
    },
  },
};
</script>
