<template>
  <div class="dsf_cardItemByLive" v-if="visible">
    <div class="content" v-if="isDesign">
      <div class="item-wrap pointer">
        <div class="img-box">
          <van-image :src="src | imgFormat" fit="cover"></van-image>
          <div class="live-type">直播中</div>
        </div>
        <div class="info">
          <p class="ell">上海全力打造全国新一轮全面开放新高地的新举措</p>
          <div class="live-time">{{ formatterDate("2020-12-22 22:00:00") }}</div>
          <div class="detail">张三</div>
        </div>
      </div>
    </div>
    <div class="content" @click="goDetail(data)" v-else :style="{ marign: marginString }">
      <div class="item-wrap pointer">
        <div class="img-box">
          <van-image :src="data['cover'] | imgFormat" fit="cover"></van-image>
          <div class="live-type" v-if="data['stateTxt']">{{ data["stateTxt"] }}</div>
        </div>
        <div class="info">
          <p class="ell">{{ data["title"] }}</p>
          <div class="live-time">{{ formatterDate(data["time"] || "") }}</div>
          <div class="detail">{{ data["teacher"] }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobileNcCardItemByLiveList",
  ctrlCaption: "直播卡片",
  mixins: [$mixins.card],
  props: {
    visible: {
      type: Boolean,
      default: true,
    },
    cardWidth: {
      type: String,
      default: "220px",
    },
    detailUrl: {
      type: String,
      default: "/mobile/nc/pagelive/liveDetail?id=",
    },
    keysMap: {
      type: Array,
      default: () => {
        return [
          { from: "cover", to: "cover", type: "text", desc: "封面" },
          { from: "stateTxt", to: "stateTxt", type: "text", desc: "直播状态文字" },
          { from: "title", to: "title", type: "text", desc: "标题" },
          { from: "time", to: "time", type: "", desc: "直播时间" },
          { from: "teacher", to: "teacher", type: "text", desc: "授课教师" },
          { from: "nc_live_info_id", to: "id", type: "text", desc: "id" },
        ];
      },
    },
  },
  data() {
    return {};
  },
  components: {},
  methods: {
    goDetail(item) {
      this.$router.push({
        path: this.detailUrl + item["id"],
      });
    },
    formatterDate(time, format) {
      return time ? dsf.date.format(time, !format ? "yyyy-mm-dd hh:ii" : format) : "";
    },
    getUrl(cover) {
      return cover ? dsf.url.getWebPath(JSON.parse(cover)[0].relativePath) : "";
    },
  },
});
</script>
<style lang="scss" scoped>
@import "../../../assets/styles/mobile/index.scss";
.dsf_cardItemByLive {
  .content {
    border-radius: 4.5px;
  }
  .item-wrap {
    padding: 10px;
    box-sizing: border-box;
    background: #fff;
    display: flex;
    .ell {
      font-size: 15px;
      margin-bottom: 5px;
    }
    .info {
      box-sizing: border-box;
      overflow: hidden;
      margin-left: 10px;
      .live-time {
        margin-top: 2px;
        color: #666;
      }
    }
    .detail {
      font-size: 13px;
      color: #666;
    }
  }
  .img-box {
    width: 104px;
    height: 58.5px;
    overflow: hidden;
    transition: 0.5s;
    position: relative;
    border-radius: 3px;
    flex-shrink: 0;
    & > .van-image {
      width: 100%;
      height: 100%;
    }
    .live-type {
      position: absolute;
      left: 0;
      top: 0;
      @include background-theme("normal");
      color: #fff;
      padding: 3px 3px;
      font-size: 10px;
      border-radius: 3px;
    }
  }
}
</style>
