<template>
  <div class="share-container live-sign-detail">
    <div v-if="isDesign">直播详情</div>
    <template v-if="noId === 1">
      <div class="box live-sign-inner">
        <div class="live-sign-cover">
          <van-image width="100%" height="100%" fit="fill" :src="setImg()">
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
            <template v-slot:error>
              <img :src="require('_nc/assets/styles/themes/img/file-read-18688.png')" style="width:100%;"/>
            </template>
          </van-image>
        </div>
        <div class="tec">
          <div v-if="pageInfo.speaker" class="van-ellipsis">主讲人：{{ pageInfo.speaker }}</div>
          <div class="van-ellipsis">已有{{ pageInfo.yypeo || 0 }}人参加</div>
        </div>
        <dsf-panel :panelData="{ name: '直播简介' }" class="intro-panel">
          <div slot="panel" class="course-item">
            <div class="title" v-html="pageInfo.name"></div>
            <div class="time">{{ formatterDate() }}</div>
            <div class="intro" v-html="pageInfo.abstract || '暂无简介'"></div>
            <div class="btns">
              <div @click="praise">
                <dsf-icon class="dianzan-icon" :class="{ on: pageInfo.sfdz == 1 }" :name="pageInfo.sfdz ? 'dianzan' : 'dianzan'"></dsf-icon>
                <span class="num">{{ pageInfo.tagsNum }}</span>
              </div>
              <div @click="collections">
                <dsf-icon class="aixin-icon" :class="{ on: pageInfo.sfsc == 1 }" :name="pageInfo.sfsc ? 'aixin' : 'aixin'"></dsf-icon>
                <span class="num">{{ pageInfo.collectNum }}</span>
              </div>
            </div>
          </div>
        </dsf-panel>
      </div>
      <div class="bottom-btns">
        <span class="share-btn" @click="repeatGo">分享</span>
        <span @click="liveOper" :class="{ ban: pageInfo.btnOper == 5 && pageInfo.playstatus == 0 }">{{ pageInfo.btnText }}</span>
      </div>
    </template>
    <div class="no-data" v-if="noId != -1 && noId !== 1">{{ noId }}</div>
    <!-- <van-skeleton title avatar :row="10" v-if="noId == -1" :animate="false" /> -->
  </div>
</template>

<script>
import dsfPanel from "_nc/mobile/base/dsfPanel";
// import interaction from "../interaction";
export default dsf.component({
  mixins: [$mixins.control],
  ctrlCaption: "直播详情",
  name: "DsfMobileNcLiveSign",
  components:{
    dsfPanel
  },
  data() {
    return {
      noId: -1,
      zbId: this.$route.query.id || '95d08a6e3f84463bad7cb818b6f874f0',
      userId: '',
      pageInfo: {},
    };
  },
  created() {
    if(!this.isDesign)this.initPage();
  },
  mounted() {},
  methods: {
    praise() {
      let _this = this;
      let loader = _this.dsf.layer.loading();
      _this.dsf.http
        .post("nc/mobile/tyzj/hd/tags/saveOrCancel", {
          ywlxValue: "zb",
          ywid: _this.zbId,
        }, $$webRoot.nc)
        .done((d) => {
          _this.dsf.layer.closeLoading(loader);
          if (d.state == 20000) {
            _this.initPage();
          }
        })
        .error((response) => {
          _this.dsf.layer.closeLoading(loader);
          _this.dsf.layer.message("服务器异常", false);
        });
    },
    collections() {
      let _this = this;
      let loader = _this.dsf.layer.loading();
      _this.dsf.http
        .post("nc/mobile/tyzj/hd/collect/saveOrCancel", {
          ywlxValue: "zb",
          ywid: _this.zbId,
        },$$webRoot.nc)
        .done((d) => {
          _this.dsf.layer.closeLoading(loader);
          if (d.state == 20000) {
            _this.initPage();
          }
        })
        .error((response) => {
          _this.dsf.layer.closeLoading(loader);
          _this.dsf.layer.message("服务器异常", false);
        });
    },
    formatterDate() {
      let s_1 = this.pageInfo.sdate;
      let s_2 = this.pageInfo.zbrq_stime;
      let e_1 = this.pageInfo.edate;
      let e_2 = this.pageInfo.zbrq_etime;
      if (s_1 === e_1) {
        return s_1 + " " + s_2 + " — " + e_2;
      } else {
        return s_1 + " " + s_2 + " — " + e_1 + " " + e_2;
      }
    },
    initPage() {
      let _this = this;
      let loader = _this.dsf.layer.loading();
      _this.dsf.http
        .get("nc/mobile/tj/zb/zbdetail", {
          zbid: _this.zbId,
        },$$webRoot.nc)
        .done((d) => {
          _this.dsf.layer.closeLoading(loader);
          if (d.success) {
            if (dsf.isEmptyObject(d.data) || !d.data) {
              _this.noId = "直播不存在";
            } else {
              _this.noId = 1;
              let _info = d.data;
              let _btnText = null;
              let _btnOper = null;
              let _iconText = null;
              if (_info.state == 1) {
                //按钮显示
                _btnText = "进入直播";
                _iconText = "直播";
                //按钮操作状态 1:已登陆-直播未预约 2:已登陆-直播已预约 3:已登陆-未开始未预约 4:已登陆-未开始已经预约 5:已结束-观看
                _btnOper = _info.isyy_value == 0 ? 1 : _info.isyy_value == 1 ? 2 : null;
              } else if (_info.state == 2) {
                _iconText = "预约";
                _btnText = _info.isyy_value == 0 || _info.isyy_value == -1 ? "预约" : _info.isyy_value == 1 ? "已预约" : "";
                _btnOper = _info.isyy_value == -1 ? -1 : _info.isyy_value == 0 ? 3 : _info.isyy_value == 1 ? 4 : null;
              } else if (_info.state == 3) {
                _iconText = "回放";
                _btnText = "回放";
                _btnOper = 5;
              }
              _this.$set(_info, "iconText", _iconText);
              _this.$set(_info, "btnText", _btnText);
              _this.$set(_info, "btnOper", _btnOper);
              _this.pageInfo = _info;
            }
          } else {
            _this.dsf.layer.message(d.message || "获取列表数据异常", false);
          }
        })
        .error((response) => {
          _this.dsf.layer.closeLoading(loader);
          _this.dsf.layer.message("服务器异常", false);
        });
    },
    //判断权限
    liveSkip() {
      let _this = this;
      let loader = _this.dsf.layer.loading();
      _this.dsf.http
        .post("nc/mobile/tj/zb/addLiveGroup", {
          zbid: _this.zbId,
          dduserid: _this.userId,
        },$$webRoot.nc)
        .done((d) => {
          _this.dsf.layer.closeLoading(loader);
          if (d.state == 20000) {
            _this.toDetail(_this.pageInfo.chatId);
          } else {
            _this.toError();
          }
        })
        .error((response) => {
          _this.dsf.layer.closeLoading(loader);
          _this.dsf.layer.message("服务器异常", false);
        });
    },
    //判断按钮操作
    liveOper() {
      let _this = this;
      let _info = _this.pageInfo;
      console.log(_info)
      //按钮操作状态 -1:未登录 1:已登陆-直播未预约 2:已登陆-直播已预约 3:已登陆-未开始未预约 4:已登陆-未开始已经预约 5:已结束-观看d
      if (_info.btnOper) {
        if (_info.btnOper == 2) {
          //进入直播
          _this.toDetail(_this.pageInfo.chatId);
        } else if (_info.btnOper == 3) {
          //预约
          _this.handleState();
        } else if (_info.btnOper == 4) {
          //取消预约
          // _this.handleState();
        } else if (_info.btnOper == 5 && _info.playstatus != 0) {
          //回放
          let loader = _this.dsf.layer.loading();
          _this.dsf.http
            .post("nc/mobile/tj/zb/playback", {
              zbid: _this.zbId,
            },$$webRoot.nc)
            .done((d) => {
              _this.dsf.layer.closeLoading(loader);
              if (d.state == 20000) {
                if (_info.playstatus == 2 || _info.playstatus == 1) {
                  dsf.platform.openLink(_info.playback_url);
                }
              }
            })
            .error((response) => {
              _this.dsf.layer.closeLoading(loader);
              _this.dsf.layer.message("服务器异常", false);
            });
        } else if (_info.btnOper == 1) {
          //直播预约
          _this.liveSkip();
        }
      }
    },
    handleState() {
      let _this = this;
      let _info = _this.pageInfo;
      let loader = _this.dsf.layer.loading();
      let _optype = _info.btnOper == 3 ? 1 : _info.btnOper == 5 ? 2 : _info.btnOper == 4 ? 3 : null;
      //btnOper按钮操作状态 -1:未登录 1:已登陆-直播未预约 2:已登陆-直播已预约 3:已登陆-未开始未预约 4:已登陆-未开始已经预约 5:已结束-观看
      //_optype操作类型(1预约，2.观看，3取消预约,4.加入直播 )
      if (_optype) {
        let parms = {
          nc_zbid: _info.nc_zbgl_jbxx_id,
          optype: _optype,
        };
        _this.dsf.http
          .post("nc/zbgl/zbOp", parms, $$webRoot.nc)
          .done((d) => {
            _this.dsf.layer.closeLoading(loader);
            if (d.state == 20000) {
              _this.initPage();
            } else {
              dsf.layer.message(data.message, false);
            }
          })
          .error((response) => {
            _this.dsf.layer.closeLoading(loader);
            _this.dsf.layer.message("服务器异常", false);
          });
      } else {
        // dsf.layer.message('', false);
      }
    },
    repeatGo() {
      let _this = this;
      this.dsf.platform.share({
        url: window.location.href.split("#/")[0] + "#/shareLive/" + this.$route.query.id,
        title: "直播分享",
        content: this.pageInfo.name,
        image: _this.setImg("absolutePath"),
      });
    },
    setImg(key) {
      let cover = this.pageInfo.cover;
      if (cover) {
        cover = dsf.url.getWebPath(JSON.parse(cover)[0][key || "relativePath"],$$webRoot.nc);
      }
      return cover || "";
    },
    toDetail(id) {
      dsf.platform.toConversation(
        id,
        function () {},
        function () {
          dsf.layer.message("未找到指定会话", false);
        }
      );
    },
    toError(item) {
      this.$router.push({
        path: "/recommend/liveError/" + this.zbId,
      });
    },
  },
});
</script>