<template>
  <div class="news-detail">
    <div v-if="isDesign">新闻详情</div>
    <van-nav-bar>
      <div slot="title">{{ newsInfo.name }}</div>
    </van-nav-bar>
    <div class="news-body dsf-content">
      <div class="news-mesg">
        <p class="van-ellipsis">
          <span>{{ newsInfo.time }}</span>
          <span style="display: block">来源：{{ newsInfo.source }}</span>
        </p>
        <p>
          <span>
            <dsf-icon class="yanjing-icon" name="yanjing"></dsf-icon>
            {{ newsInfo.llcs }}
          </span>
        </p>
      </div>
      <div class="news-title">
        {{ newsInfo.name }}
      </div>
      <div class="news-detail" ref="newsContent" @click="imgPreview" v-html="newsInfo.content"></div>
    </div>
    <div class="news-bottom dsf-bottom">
      <interaction ref="interaction" :id="$route.query.id" type="xw" :shareOpts="shareOpts"></interaction>
    </div>
    <van-image-preview v-model="imgShow" :images="imgPreviewSrc" :show-index="false"> </van-image-preview>
  </div>
</template>

<script>
import interaction from "../interaction";
export default dsf.component({
  mixins: [$mixins.control],
  ctrlCaption: "新闻详情",
  name: "DsfMobileNcNewsDetail",
  components: {
    interaction,
  },
  data() {
    return {
      imgPreviewSrc: [],
      imgShow: false,
      newsInfo: {
        id: "",
        name: "",
        time: "",
        source: "",
        content: "",
        llcs: "0",
        dz: "0",
        collect: "0",
      },
      shareOpts: {
        title: "资料分享",
        content: "",
        url: window.location.href.split("#/")[0] + "#/shareNew/" + this.$route.query.id,
      },
    };
  },
  computed: {},
  created: function () {
    let _this = this;
    if (!this.isDesign) _this.initPage();
  },
  mounted: function () {
    let _this = this;
  },
  methods: {
    imgPreview(e) {
      let _this = this;
      if (e.target.nodeName.toLowerCase() === "img") {
        _this.imgPreviewSrc = [];
        _this.imgPreviewSrc.push(e.target.src);
        _this.imgShow = true;
      }
    },
    initPage() {
      let loader = dsf.layer.loading();
      let _id = this.$route.query.id || "008f003250a8481e84ad28ad4dc3af24";
      let _this = this;
      this.$http
        .get("nc/mobile/tj/news/findNewsDetail", {
          id: _id,
          preview: "",
        },$$webRoot.nc)
        .done((d) => {
          dsf.layer.closeLoading(loader);
          console.log(d);
          if (d.success) {
            let _data = d.data;
            let _info = {
              id: _id,
              name: _data.name,
              time: _data.time,
              source: _data.source,
              content: _data.content,
              llcs: _data.llcs || 0,
              dz: _data.dz || 0,
              collect: _data.collect || 0,
            };
            _this.shareOpts.content = _data.name;

            _this.newsInfo = _info;
          } else {
            dsf.layer.message(d.message || "资料数据异常", false);
          }
        })
        .error((err) => {
          _this.dsf.layer.message(err.message, false);
        });
    },
  },
});
</script>
