<template>
  <DsfMobileNcDataGrid
    class="liveList"
    style="height: 100%"
    ref="dsfDatagrid"
    :http-param="datagrid_httpParam"
    :hasLoad="hasLoad"
    :hasRefresh="hasRefresh"
    data-source="meta/list/data"
    col="1"
    :limit="limit"
    :margin="margin"
    background-color="#fff"
    :padding="padding"
  >
    <van-cell slot="default" slot-scope="datagrid_list" :key="datagrid_list.data._id">
      <div class="live-item" @click="toDetail(datagrid_list.data._id)">
        <div class="img">
          <van-image width="100%" height="100%" fit="fill" :src="setImg(datagrid_list.data)">
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
            <template v-slot:error>
              <img :src="require('_nc/assets/styles/themes/img/file-read-18688.png')" style="width: 100%; height: 100%" />
            </template>
          </van-image>
          <div class="live-user-number">
            <template v-if="datagrid_list.data['nc_zbgl_mhzb_data.state'] == 1">
              <span>直播</span>
              {{ datagrid_list.data["nc_zbgl_mhzb_data.yypeo"] }}人
            </template>
            <template v-if="datagrid_list.data['nc_zbgl_mhzb_data.state'] == 2">
              <span>预约</span>
              {{ datagrid_list.data["nc_zbgl_mhzb_data.yypeo"] }}人
            </template>
            <template v-if="datagrid_list.data['nc_zbgl_mhzb_data.state'] == 3">
              <span>回放</span>
              {{ datagrid_list.data["nc_zbgl_mhzb_data.yypeo"] }}人
            </template>
          </div>
        </div>
        <div class="van-multi-ellipsis--l2 title-font">
          <dsf-icon class="live-icon" name="tongji" v-if="datagrid_list.data['nc_zbgl_mhzb_data.state'] == 1" />
          <dsf-icon class="live-icon" name="shexiang" v-if="datagrid_list.data['nc_zbgl_mhzb_data.state'] == 3" />
          <dsf-icon class="live-icon" name="yuyue1" v-if="datagrid_list.data['nc_zbgl_mhzb_data.state'] == 2" />
          {{ datagrid_list.data["nc_zbgl_mhzb_data.name"] }}
        </div>
        <div class="van-ellipsis desc-font">
          {{ datagrid_list.data["nc_zbgl_mhzb_data.speaker"]
          }}{{ datagrid_list.data["nc_zbgl_mhzb_data.speaker"] && datagrid_list.data["nc_zbgl_mhzb_data.job"] ? "-" : ""
          }}{{ datagrid_list.data["nc_zbgl_mhzb_data.job"] }}&nbsp;
        </div>
        <div class="van-ellipsis desc-font">{{ formatterDate(datagrid_list.data) }}</div>
      </div>
    </van-cell>
  </DsfMobileNcDataGrid>
</template>

<script>
import { dsfRouteView } from "../dsfBase";
export default {
  name: "liveList",
  mixins: [dsfRouteView],
  props: {
    detailUrl: {
      type: String,
      default: "",
    },
    limit: {
      type: Number,
      default: 10,
    },
    hasLoad: {
      type: Boolean,
      default: true,
      desc: "是否有上拉加载",
    },
    hasRefresh: {
      type: Boolean,
      default: true,
      desc: "是否有下拉刷新",
    },
    padding: {
      type: Array,
      default() {
        return [0, 15, 0, 15];
      },
    },
    margin: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
  },
  data() {
    return {
      datagrid_search: "",
      datagrid_httpParam: {
        query: "{}",
        // order: JSON.stringify([{ "code": "ds_update_time", "sort": "DESC" }]),
        namespace: "nc.zbgl.mhzb",
        pageName: "mhzblist",
      },
    };
  },
  methods: {
    formatterDate(item) {
      let _info = item;
      if (_info["nc_zbgl_mhzb_data.sdate"] == _info["nc_zbgl_mhzb_data.edate"]) {
        return _info["nc_zbgl_mhzb_data.sdate"] + " " + _info["nc_zbgl_mhzb_data.zbrq_stime"] + " 至 " + _info["nc_zbgl_mhzb_data.zbrq_etime"];
      } else {
        return _info["nc_zbgl_mhzb_data.sdate"] + " 至 " + _info["nc_zbgl_mhzb_data.edate"];
      }

      /* {{momentSdate(datagrid_list.data["nc_zbgl_mhzb_data.zbrq_sdate"])}}
        至 {{momentEdate(datagrid_list.data["nc_zbgl_mhzb_data.zbrq_edate"])}*/
    },
    onSearch() {
      this.datagrid_httpParam.query = { searchValue: this.datagrid_search };
      this.$refs.dsfDatagrid.query();
    },
    toDetail(id) {
      this.$router.push({
        path: this.detailUrl,
        query: {
          id: id,
        },
      });
    },
    parseJson(str) {
      return JSON.parse(str);
    },
    momentSdate(str) {
      return dsf.date.format(str, "yyyy-mm-dd hh:ii");
    },
    setImg(data) {
      let cover = data["nc_zbgl_mhzb_data.cover"];
      if (cover) {
        cover = dsf.url.getWebPath(JSON.parse(cover)[0]["relativePath"], $$webRoot.nc);
      }
      return cover || "/";
    },
    momentEdate(date) {
      return dsf.date.format(date, "yyyy-mm-dd hh:ii");
    },
    query() {
      this.datagrid_httpParam.query = JSON.stringify({
        searchValue: this.datagrid_search,
      });
      this.datagrid = this.$refs.dsfDatagrid.query();
    },
  },
};
</script>
