<template>
  <div class="dsf_nc_channelDetail ds-control">
    <div v-if="it">
      <div class="cover">
        <van-image :src="it['cover'] | imgFormat" fit="cover"></van-image>
        <div class="cover-mask">
          <p>{{ it.title }}</p>
        </div>
      </div>
      <div class="info">
        <div class="info-item info-left">
          <dd class="item">
            <span>{{ it.courseCount }}</span>
            <span class="sub-name">课程</span>
          </dd>
          <dd class="item">
            <span>{{ it.time }}</span>
            <span class="sub-name">发布</span>
          </dd>
        </div>
        <div class="info-item info-right">
          <dd class="item">
            <dsf-icon name="yanjing" />
            {{ it.hits }}
          </dd>
          <dd class="item">
            <dsf-icon name="iconfontzhizuobiaozhun13" />
            {{ subCount }}
          </dd>
        </div>
      </div>
      <div class="buttons">
        <div class="tags">
          <!-- <dl class="tag">主题教育</dl> -->
        </div>
        <!-- <button class="subcrible" @click="toSubscrible(it)">{{ subText }}</button> -->
      </div>
      <div :class="`insert ${isDesign ? 'insert-wrap' : ''}`">
        <div slot-name="chapter" :class="['insert-content', { 'insert-border': isDesign }]">
          <slot name="chapter"></slot>
        </div>
      </div>
      <div class="channel-intrduce">
        <div slot-name="desc" :class="['insert-content', { 'insert-border': isDesign }]">
          <slot name="desc"></slot>
        </div>
        <div class="text" v-html="it.describe || ''"></div>
      </div>
      <div class="fixed-button">
        <van-button round class="subcrible-btn" @click="toSubscrible(it)">{{ subText }}</van-button>
      </div>
    </div>
    <div v-if="isDesign">
      <div class="cover">
        <el-image :src="1 | imgFormat"></el-image>
        <div class="cover-mask">
          <p>测试</p>
        </div>
      </div>
      <div class="info">
        <div class="info-item info-left">
          <dd class="item">
            <span>17</span>
            <span class="sub-name">课程</span>
          </dd>
          <dd class="item">
            <span>17</span>
            <span class="sub-name">发布</span>
          </dd>
        </div>
        <div class="info-item info-right">
          <dd class="item">
            <dsf-icon name="yanjing" />
            25
          </dd>
          <dd class="item">
            <dsf-icon name="iconfontzhizuobiaozhun13" />
            25
          </dd>
        </div>
      </div>
      <div class="buttons">
        <div class="tags">
          <dl class="tag">主题教育</dl>
        </div>
        <!-- <button class="subcrible">订阅</button> -->
      </div>
      <div :class="`insert ${isDesign ? 'insert-wrap' : ''}`">
        <div slot-name="chapter" :class="['insert-content', { 'insert-border': isDesign }]">
          <slot name="chapter"></slot>
        </div>
      </div>
      <div class="channel-intrduce">
        <div :class="`insert ${isDesign ? 'insert-wrap' : ''}`">
          <div slot-name="desc" :class="['insert-content', { 'insert-border': isDesign }]">
            <slot name="desc"></slot>
          </div>
        </div>
        <div class="text">简介</div>
      </div>
    </div>
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobileNcPcChannelDetail",
  ctrlCaption: "频道详情",
  mixins: [$mixins.layout],
  props: {
    slots: {
      type: Array,
      default() {
        return [
          { name: "chapter", controls: [] },
          { name: "desc", controls: [] },
        ];
      },
    },
  },
  computed: {
    subText() {
      let text = "订阅";
      if (this.it.mySubscribed) {
        text = this.it.mySubscribed ? "已订阅" : "订阅";
      }
      return text;
    },
    subCount() {
      let count = 0;
      if (this.it.subscription) {
        count = this.it.subscription;
      }
      return count;
    },
  },
  data() {
    return {
      itemList: [],
      it: null,
      queryiId: this.$route.query.id || "4266fa929e3343c6a2dd7f1cc36abc75",
    };
  },
  mounted() {
    if (!this.isDesign) this.loadChannelDetail();
  },
  methods: {
    reloadData() {
      if (!this.isDesign) {
        this.queryiId = this.$route.query.id;
        this.loadChannelDetail();
      }
    },
    loadChannelDetail() {
      const sourceUrl = "nc/pack/channel/info";
      const params = {
        id: this.queryiId,
      };
      const loading = dsf.layer.mobile.loading();
      dsf.http
        .get(sourceUrl, params)
        .done(({ success, data, message }) => {
          if (success) {
            this.it = data;
          } else {
            error(message);
          }
        })
        .error(({ message }) => {
          error(message);
        })
        .always(res => {
          dsf.layer.mobile.closeLoading(loading);
        });
      function error(err) {
        dsf.layer.mobile.message("查询失败", false);
      }
    },
    toSubscrible(it) {
      console.log(it);
      const sourceUrl = "/dsfa/subscribe/saveOrCancel";
      const params = {
        ywlxValue: "channel",
        ywlxText: "频道",
        ywid: it.channelId,
      };
      const loading = dsf.layer.mobile.loading("提交中...");
      dsf.http
        .post(sourceUrl, params)
        .done(({ success, data, message }) => {
          if (success) {
            this.it.mySubscribed = data.isDo;
            this.it.subscription = data.isDo ? ++this.it.subscription : --this.it.subscription;
            dsf.layer.mobile.message(message);
          } else {
            error();
          }
        })
        .error(err => {
          error();
        })
        .always(res => {
          dsf.layer.mobile.closeLoading(loading);
        });
      function error() {
        dsf.layer.mobile.message("订阅失败", false);
      }
    },
  },
});
</script>
<style lang="scss" scoped>
@import "../../../assets/styles/mobile/index.scss";
.dsf_nc_channelDetail {
  // background: #ffffff;
  height: 100%;
  &.ds-control {
    padding: 0 !important;
  }
  .cover {
    width: 100%;
    height: 221px;
    position: relative;
    overflow: hidden;
    .el-image,
    .van-image {
      height: 100%;
      width: 100%;
    }
    .cover-mask {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0, 0, 0, 0.5);
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      p {
        font-size: 16px;
        color: #ffffff;
        padding: 10px 15px;
      }
    }
  }
  .info {
    width: 100%;
    display: flex;
    align-items: center;
    padding: 15px 0;
    &-item {
      flex: 1;
      text-align: center;
      display: flex;
      align-items: center;
      .item {
        display: flex;
        font-size: 15px;
        color: #333333;
        align-items: center;
        .sub-name {
          font-size: 13px;
          color: #666666;
          padding-top: 4px;
        }
        .iconfont {
          margin-right: 5px;
        }
      }
      &.info-left {
        position: relative;
        &::after {
          content: "";
          width: 1px;
          position: absolute;
          right: 0;
          height: 30px;
          background: #c5c5c5;
        }
        .item {
          flex: 1;
          flex-direction: column;
        }
      }
      &.info-right {
        justify-content: center;
        .item:first-child {
          margin-right: 12px;
        }
      }
    }
  }
  .buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
    .tags {
      display: flex;
      align-items: center;
      .tag {
        font-size: 11px;
        color: #1682f8;
        background: #e7f4ff;
        border-radius: 9.5px;
        margin: 0 6px;
        padding: 2px 8.5px;
      }
    }
    .subcrible {
      color: #ffffff;
      @include font_6;
      @include background-theme("normal");
      cursor: pointer;
      border-radius: 4px;
      border: none;
      outline: none;
      transition: all 0.5s;
      line-height: 30px;
      padding: 0 6px;
      &:hover {
        opacity: 0.9;
      }
    }
  }
  .insert-wrap {
    min-height: 50px;
    padding: 20px 0 25px;
  }
  .insert {
    width: 100%;
    height: auto;
    .insert-content {
      min-height: 100%;
    }
    .insert-border {
      height: 150px;
      border: 1px dashed;
    }
  }
  .channel-intrduce {
    padding-bottom: 10px;
    .insert-wrap {
      padding: 0;
    }
    .text {
      padding: 15px;
      font-size: 15px;
      color: #333333;
      line-height: 26px;
    }
  }
  .fixed-button {
    height: 60px;
    background: #ffffff;
    border-top: 1px solid #f1f1f1;
    box-sizing: border-box;
    box-shadow: 0 -2px 4px 0 rgba(238, 238, 238, 0.5);
    z-index: 20;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    .subcrible-btn {
      color: #fff;
      display: block;
      width: calc(100% - 30px);
      height: 40px;
      margin: 10px 15px;
      @include background-theme("normal");
    }
  }
}
</style>
