<template>
  <div class="dsf-player" @click="defaultClick()">
    <slot name="baberrage"></slot>
    <!-- <video-player
      class="video-player vjs-custom-skin"
      ref="videoPlayer"
      :playsinline="true"
      :options="playerOptions"
      @play="playerPlay($event)"
      @pause="playerPause($event)"
      @ended="playerEnded($event)"
      @loadeddata="playerLoadeddata($event)"
      @waiting="playerWaiting($event)"
      @playing="playerPlaying($event)"
      @timeupdate="playerTimeupdate($event)"
      @canplay="playerCanplay($event)"
      @canplaythrough="playerCanplaythrough($event)"
      @ready="playerReadied"
      @statechanged="playerStateChanged($event)"
    ></video-player> -->
  </div>
</template>
<script>
// import "videojs-contrib-hls";
// import "video.js/dist/video-js.css";
// import { videoPlayer } from "vue-video-player";
// import { state } from "../../../project/anHui/assets/style.scss";
export default {
  props: {
    autoPlay: {
      type: Boolean,
      default: false,
    },
    //是否直播
    isLive: {
      type: Boolean,
      default: false,
    },
    //是否获取网络状态
    netWork: {
      type: Boolean,
      default: false,
    },
    isPause: {
      //是否暂停
      type: Boolean,
      default: true,
    },
    seekTime: {
      //跳转到对应播放位置
      type: Number,
      default: 0,
    },
    coverImg: {
      //视频封面
      type: String,
      default: "",
    },
    videoSource: {
      //视频地址
      type: [String],
    },
    //允许拖动进度条
    allowFastForward: {
      type: Boolean,
      default: false,
    },
    //视频是否正在播放
    playIng: {
      type: Boolean,
      default: false,
    },
    showMark: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      initPlay: false,
      isSeeking: false,
      playerOptions: {
        // isFullscreen:false,
        playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
        autoplay: this.autoPlay, //如果true,浏览器准备好时开始回放。
        muted: false, // 默认情况下将会消除任何音频。
        loop: false, // 导致视频一结束就重新开始。
        preload: "auto", // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        language: "zh-CN",
        aspectRatio: "16:9", // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        hls: true,
        sources: [],
        poster: this.coverImg, //你的封面地址
        width: document.documentElement.clientWidth, //播放器宽度
        notSupportedMessage: "此视频暂无法播放",
        languages: {
          "zh-CN": {
            Play: "播放",
            Pause: "暂停",
            "Current Time": "当前时间",
            Duration: "时长",
            "Remaining Time": "剩余时间",
            "Stream Type": "媒体流类型",
            LIVE: "直播",
            Loaded: "加载完毕",
            Progress: "进度",
            Fullscreen: "全屏",
            "Non-Fullscreen": "退出全屏",
            Mute: "静音",
            Unmute: "取消静音",
            "Playback Rate": "播放速度",
            Subtitles: "字幕",
            "subtitles off": "关闭字幕",
            Captions: "内嵌字幕",
            "captions off": "关闭内嵌字幕",
            Chapters: "节目段落",
            "Close Modal Dialog": "关闭弹窗",
            Descriptions: "描述",
            "descriptions off": "关闭描述",
            "Audio Track": "音轨",
            "You aborted the media playback": "视频播放被终止",
            "A network error caused the media download to fail part-way.": "网络错误导致视频下载中途失败。",
            "The media could not be loaded, either because the server or network failed or because the format is not supported.":
              "网络问题无法加载。",
            "The media playback was aborted due to a corruption problem or because the media used features your browser did not support.":
              "由于视频文件损坏或是该视频使用了你的浏览器不支持的功能，播放终止。",
            "No compatible source was found for this media.": "无法找到此视频兼容的源。",
            "The media is encrypted and we do not have the keys to decrypt it.": "视频已加密，无法解密。",
            "Play Video": "播放视频",
            Close: "关闭",
            "Modal Window": "弹窗",
            "This is a modal window": "这是一个弹窗",
            "This modal can be closed by pressing the Escape key or activating the close button.": "可以按ESC按键或启用关闭按钮来关闭此弹窗。",
            ", opens captions settings dialog": ", 开启标题设置弹窗",
            ", opens subtitles settings dialog": ", 开启字幕设置弹窗",
            ", opens descriptions settings dialog": ", 开启描述设置弹窗",
            ", selected": ", 选择",
            "captions settings": "字幕设定",
            "Audio Player": "音频播放器",
            "Video Player": "视频播放器",
            Replay: "重播",
            "Progress Bar": "进度小节",
            "Volume Level": "音量",
            "subtitles settings": "字幕设定",
            "descriptions settings": "描述设定",
            Text: "文字",
            White: "白",
            Black: "黑",
            Red: "红",
            Green: "绿",
            Blue: "蓝",
            Yellow: "黄",
            Magenta: "紫红",
            Cyan: "青",
            Background: "背景",
            Window: "视窗",
            Transparent: "透明",
            "Semi-Transparent": "半透明",
            Opaque: "不透明",
            "Font Size": "字体尺寸",
            "Text Edge Style": "字体边缘样式",
            None: "无",
            Raised: "浮雕",
            Depressed: "压低",
            Uniform: "均匀",
            Dropshadow: "下阴影",
            "Font Family": "字体库",
            "Proportional Sans-Serif": "比例无细体",
            "Monospace Sans-Serif": "单间隔无细体",
            "Proportional Serif": "比例细体",
            "Monospace Serif": "单间隔细体",
            Casual: "舒适",
            Script: "手写体",
            "Small Caps": "小型大写字体",
            Reset: "重启",
            "restore all settings to the default values": "恢复全部设定至预设值",
            Done: "完成",
            "Caption Settings Dialog": "字幕设定视窗",
            "Beginning of dialog window. Escape will cancel and close the window.": "开始对话视窗。离开会取消及关闭视窗",
            "End of dialog window.": "结束对话视窗",
          },
        },
      },
      playingTime: 0,
      timeOutTimer: null,
    };
  },
  created() {
    if (!this.isLive) {
      this.playerOptions.controlBar = {
        timeDivider: true,
        durationDisplay: true,
        remainingTimeDisplay: false,
        fullscreenToggle: true, //全屏按钮
      };
      this.$nextTick(() => {
        const nextBtn = this.player.controlBar.addChild("button");
        const waterMark = this.player.controlBar.addChild("button");
        //   menuBtn.addClass("vjs-icon-menu");
        //   menuBtn.on("tap", this.playerList);
        nextBtn.addClass("vjs-icon-next");
        nextBtn.on("tap", this.playerNext);
        // waterMark.addClass('waterMark');
      });
    }
    document.addEventListener("fullscreenchange", function (event) {
      if (document.fullscreenElement) {
        console.log("进入全屏");
      } else {
        console.log("退出全屏");
      }
    });
    this.playerOptions.sources = [this.videoSrc()]; // [this.videoSource];
  },
  mounted() {
    this.initPlay = true;
    var video = this.$el.querySelector("video");
    video.setAttribute("x5-video-player-type", "h5-page");
    video.setAttribute("x-webkit-airplay", true);
    video.setAttribute("x5-video-player-fullscreen", false);
    // alert(video.parentNode.innerHTML);
    video.removeAttribute("x5-playsinline");
    // video.setAttribute("x5-video-orientation","landscape|portrait")
    // video.setAttribute("raw-controls", false);
    //判断是否为苹果---让其自动加载
    // if (!this.isAndroid() && this.netWork) {
    //   this.playerLoadeddata()

    //   // this.player.play();
    // }
  },
  computed: {
    player() {
      return this.$refs.videoPlayer.player;
    },
    videoSrc() {
      return function () {
        let url = this.videoSource;
        if (!url) return {};
        let arr = url.split(".");
        let extName = arr[arr.length - 1];
        let type = "";
        switch (extName) {
          // case "mp3":
          //   type = 'audio/mpeg3';
          //   break;
          case "m3u8":
            type = "application/x-mpegURL";
            break;
          case "mp4":
            type = "video/mp4";
            break;
          default:
            type = "";
            break;
        }
        if (type) {
          return {
            type: type,
            src: url,
          };
        } else {
          return url;
        }
      };
    },
  },
  // components: { videoPlayer },
  methods: {
    defaultClick() {
      console.log(1);
    },
    isAndroid() {
      let u = window.navigator.userAgent;
      if (u.indexOf("Android") > -1 && u.indexOf("Linux") > -1) {
        return true;
      }
      return false;
    },
    fullScreen() {
      const player = this.$refs.videoPlayer.player;
      player.requestFullscreen(); //调用全屏api方法
      player.isFullscreen(true);
      player.play();
    },
    //暂停
    pause() {
      this.player.pause();
    },
    play() {
      this.player.play();
    },
    currentTime(t) {
      this.player.currentTime(t);
    },
    //下一集
    playerNext() {
      this.$emit("playerNext");
    },
    //右上角菜单
    //  playerList() {
    //    this.$emit("playerList");
    //  },
    //播放
    playerPlay(player) {
      this.$emit("playerPlay");
    },
    //暂停
    playerPause(player) {
      this.$emit("playerPause");
    },
    //播放结束
    playerEnded(player) {
      this.$emit("playerEnded");
    },
    //监听播放状态改变
    playerStateChanged(state) {
      this.$emit("playerState", state);
    },

    //视频准备好，可以跳转到对用播放时间
    playerReadied(player) {
      if (this.isAndroid()) {
        this.player.muted(true);
      }
    },
    // 视频数据加载完成
    playerLoadeddata(player) {
      if (this.isAndroid()) {
        if (this.autoPlay && this.player) {
          //安卓必须先调用一下播放，并在开始播放后才能定位
          this.player.play();
        }
        if (this.netWork) {
          this.$emit("netWorkAutoPlayer");
        }
      } else {
        //苹果可以在此处直接定位
        if (this.seekTime) {
          this.player.currentTime(this.seekTime);
          this.playingTime = this.player.currentTime();
        }
        if (this.player && this.netWork) {
          this.player.muted(true);
          setTimeout(() => {
            this.player.play();
            this.$emit("netWorkAutoPlayer");
          }, 200);
        }
      }
    },
    // DOM元素上的readyState更改导致播放停止
    playerWaiting(player) {
      this.timeOutTimer = setTimeout(function () {
        dsf.layer.message("当前网络加载缓慢,请耐心等待");
        clearTimeout(this.timeOutTimer);
      }, 5000);
    },
    //播放中
    playerPlaying(player) {
      this.$emit("playerPlaying", player);
    },
    //当前已经播放的时间
    playerTimeupdate(player) {
      if (this.timeOutTimer) clearTimeout(this.timeOutTimer);
      if (this.initPlay && !this.isSeeking) {
        let seekTime = this.seekTime;
        //安卓需要在此处定位
        this.isSeeking = true;
        if (this.isAndroid()) {
          window.setTimeout(() => {
            this.initPlay = false;
            if (seekTime) {
              this.player.currentTime(seekTime);
              this.playingTime = this.player.currentTime();
            }
            this.player.muted(false);
            this.player.play();
          }, 1000);
        } else {
          this.initPlay = false;
        }
      } else {
        if (this.allowFastForward) {
          this.playingTime = player.currentTime();
          this.$emit("playerPlayingTime", this.playingTime);
        } else {
          let newTime = player.currentTime();
          //如果时间突然比当前时间快10秒则认为用户拖动的进度条
          if (newTime - this.playingTime > 10) {
            window.setTimeout(() => {
              this.player.currentTime(this.playingTime);
            });
          } else {
            this.playingTime = player.currentTime();
            this.$emit("playerPlayingTime", newTime);
          }
        }
      }
    },
    playerCanplay(player) {
      // alert('canplay')
    },
    playerCanplaythrough(player) {
      // this.playingTime = this.player.currentTime();
      this.$emit("playerCanplaythrough");
    },
  },
  beforeDestroy() {
    this.$refs.videoPlayer.dispose();
    if (this.timeOutTimer) clearTimeout(this.timeOutTimer);
  },
  updated() {
    console.log("change:", this.videoSource);
  },
  watch: {
    videoSource(n, o) {
      let vs = this.videoSrc();
      if (!vs || !vs.src || vs.type) return;
      this.playerOptions.sources[0].src = vs.src;
      this.playerOptions.sources[0].type = vs.type;
    },
    playIng(state) {
      if (!state) {
        console.log(state);
        //   this.player.pause();
      }
    },
  },
};
</script>
