<!--推荐页面组件-->
<template>
  <div class="ac-recommend">
    <van-tabs v-model="active" :swipe-threshold="6" :swipeable="true" :animated="true" :duration="0.5" :sticky="true" @click="changeUrl">
      <van-tab v-for="item in items" :key="item.name" :title="item.name" :name="item.id">
        <div v-if="!isDesign" :key="item.name">
          <ac-teacher-list
            v-if="item.id == 'shizi'"
            :detail-url="teacherDetail"
            :key="item.name"
            :margin="[8, 0, 0, 0]"
            style="height: 100%"
          ></ac-teacher-list>
          <live-list
            ref="liveList"
            :detail-url="liveDetail"
            v-else-if="item.id == 'zhibo'"
            :key="item.name"
            :margin="[8, 0, 0, 0]"
            style="height: 100%"
          ></live-list>
          <channel v-else-if="item.id == 'pindao'" :detail-url="channelDetail" :key="item.name" :margin="[8, 0, 0, 0]" style="height: 100%"></channel>
          <news-list
            v-else-if="item.id == 'ziliao'"
            :detail-url="newsDetail"
            :max-top-number="5"
            :key="item.name"
            :margin="[8, 0, 0, 0]"
            style="height: 100%"
          ></news-list>
          <course-list
            ref="courseList"
            :detail-url="courseDetail"
            v-else-if="item.id == 'kecheng'"
            :key="item.name"
            :margin="[8, 0, 0, 0]"
            style="height: 100%"
          ></course-list>
          <special-list
            v-else-if="item.id == 'zhuanti'"
            :key="item.name"
            :margin="[8, 0, 0, 0]"
            :detail-url="specialDetail"
            style="height: 100%"
          ></special-list>
        </div>
      </van-tab>
    </van-tabs>
    <dsf-icon class="search-icon" name="fangdajing" @click="searchHandler" />
  </div>
</template>
<script>
import { AcTeacherList } from "./teacher";
import { liveList } from "./live";
import { newsList } from "./news";
import { courseList } from "./course";
import { specialList } from "./special";
import { channel } from "./channel";
export default dsf.component({
  name: "DsfMobileNcRecommend",
  ctrlCaption: "推荐",
  mixins: [$mixins.control],
  components: { AcTeacherList, liveList, newsList, courseList, specialList, channel },
  data() {
    return {
      active: "",
      searchValue: this.searchStr,
      items: [
        { name: "课程", id: "kecheng" },
        { name: "专题", id: "zhuanti" },
        { name: "频道", id: "pindao" },
        { name: "资料", id: "ziliao" },
        { name: "直播", id: "zhibo" },
        { name: "师资", id: "shizi" },
      ],
      actives: ["kecheng", "zhuanti", "pindao", "ziliao", "zhibo", "shizi"],
    };
  },
  props: {
    defaultActive: String,
    searchStr: [String],
    searchDetail: {
      type: String,
      default: "/mobile/nc/recommendFile/mobile-globlesearch",
    },
    courseDetail: {
      type: String,
      default: "/mobile/nc/recommendFile/mobile-courseDetail",
    },
    specialDetail: {
      type: String,
      default: "/mobile/nc/recommendFile/mobile-specilaDetail",
    },
    channelDetail: {
      type: String,
      default: "/mobile/nc/recommendFile/mobile-chanelDetail",
    },
    newsDetail: {
      type: String,
      default: "/mobile/nc/recommendFile/mobile-newsDetail",
    },
    liveDetail: {
      type: String,
      default: "/mobile/nc/recommendFile/mobile-liveDetail",
    },
    teacherDetail: {
      type: String,
      default: "/mobile/nc/recommendFile/mobile-teacherDetail",
    },
  },
  mounted() {
    if (this.$route.query && dsf.isDef(this.$route.query.id)) {
      const id = this.$route.query.id;
      const index = id > 5 || id < 0 ? 0 : id;
      this.active = this.actives[index];
    }
  },
  watch: {
    $route(val) {
      if (val.query && dsf.isDef(val.query.id)) {
        const id = val.query.id;
        const index = id > 5 || id < 0 ? 0 : id;
        this.active = this.actives[index];
      }
    },
  },
  methods: {
    searchHandler: function () {
      this.$router.push({
        path: this.searchDetail,
      });
    },
    $refresh(name, args) {
      if (name == "liveDetail") {
        this.$refs.liveList && this.$refs.liveList[0].query();
      } else if (name == "Player") {
        this.$refs.courseList && this.$refs.courseList[0].query();
      }
    },
    changeUrl(name) {
      let idx = 0;
      this.items.forEach((item, index) => {
        if (item.id === name) {
          idx = index;
        }
      });
      this.$router.replace({ query: { id: idx } });
    },
  },
  // watch: {
  //   defaultActive: {
  //     handler: function (val, oldVal) {
  //       console.log(val);
  //       let act = val;
  //       if (act) {
  //         let result = _.find(this.items, (item) => item.name == act);
  //         this.active = dsf.isDef(result) ? result.id : this.items[0].id;
  //       }
  //     },
  //     immediate: true,
  //     deep: true,
  //   },
  // },
  // beforeRouteEnter(to, from, next) {
  //   next((vm) => {
  //     console.log(to, from, next);
  //     //当1级路由跳入该页时,且当defaultActive有值时重新获取当前的默认激活项目
  //     if (from.meta.index == 1 || !from.meta.index) {
  //       let act = vm.defaultActive;
  //       if (act) {
  //         let result = _.find(vm.items, (item) => item.name == act);
  //         vm.active = dsf.isDef(result) ? result.id : vm.items[0].id;
  //       }
  //     }
  //   });
  // },
  // beforeRouteUpdate (to, from, next) {
  //   // 在当前路由改变，但是该组件被复用时调用
  //   // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
  //   // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
  //   // 可以访问组件实例 `this`
  //   // alert('d')
  //   next()
  // },
  // beforeRouteLeave (to, from, next) {
  //   // 导航离开该组件的对应路由时调用
  //   // 可以访问组件实例 `this`
  //   // alert('d');
  //   next();
  // }
});
</script>