<template>
  <div class="bbs-wrap">
    <div class="bbs-container bbs-add">
      <div class="content">
        <van-field name="radio" label="类型" class="select" :class="{ 'no-val': block == -1 }" ref="select">
          <template #input>
            <van-dropdown-menu>
              <van-dropdown-item v-model="block" :options="blocks" @change="changeBlock" />
            </van-dropdown-menu>
          </template>
        </van-field>
        <!-- <div ref="title">
          <div class="title">标题</div>
          <van-field
            v-model="title"
            rows="2"
            autosize
            type="textarea"
            placeholder="请输入标题"
          />
        </div> -->
        <div>
          <div class="title">内容</div>
          <van-field v-model="content" rows="5" autosize type="textarea" placeholder="请输入内容" />
        </div>
        <div class="upload">
          <div class="title">上传图片</div>
          <van-uploader v-model="uploader" :max-count="9" multiple :after-read="uploadAfterRead" />
        </div>
      </div>
      <div class="button" @click="submit">提交</div>
    </div>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobileNcUserBack",
  ctrlCaption: "我的个人信息",
  mixins: [$mixins.control],
  data() {
    return {
      typeObj: {},
      blocks: [
        { text: "产品建议", value: 1 },
        { text: "使用问题", value: 2 },
        { text: "其他反馈", value: 3 },
      ],
      block: 3,
      uploader: [],
      title: "",
      content: "",
    };
  },
  created() {
    // this.getType();
  },
  computed: {
    pageTitle() {
      return "";
    },
  },
  methods: {
    getType() {
      dsf.http
        .post("meta/data?id=588001088f794ee7ba570f6e4488c527&pageName=edit&namespace=nc.operate.feedback&data=%7B%22_id%22%3A%22588001088f794ee7ba570f6e4488c527%22%7D", {})
        .done(d => {
          if (d.state == "20000") {
            this.typeObj = d.data;
            this.blocks = d.data["nc_operate_feedback.type"];
            console.log(this.blocks);
          } else {
            this.dsf.layer.message(d.message, false);
          }
        })
        .error(d => {
          this.dsf.layer.message(d.message, false);
        })
        .always(() => {});
    },

    uploadAfterRead(files) {
      if (!files.length) {
        files = [files];
      }
      _.forEach(files, file => {
        file.status = "uploading";
        file.message = "上传中...";

        let error = false;
        let formData = new FormData();
        formData.append("file", file.file);
        dsf.http
          .post("file/upload", formData, {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          })
          .done(d => {
            if (d.state == "20000") {
              file.status = "done";
              file.data = d.data[0];
            } else {
              error = d.message || true;
            }
          })
          .error(d => {
            error = d.message || true;
          })
          .always(() => {
            if (error) {
              file.status = "failed";
              file.message = "上传失败";
              file._message = error;
            }
          });
      });
    },
    changeBlock(value) {
      if (value != "-1" && this.blocks[0].value == "-1") {
        this.blocks.shift(1);
      }
    },
    submit() {
      console.log(this.uploader);
      let upArr = [];
      _.each(this.uploader, item => {
        upArr.push(item.data);
      });
      if (this.block == -1) {
        dsf.layer.message("请选择版块", false);
        this.$refs.select.$el.scrollIntoView();
        return;
      }
      let loader = this.dsf.layer.loading();
      let params = {
        namespace: "nc.operate.feedback",
        pageName: "edit",
        data: {
          "nc_operate_feedback.type": this.blocks[0],
          "nc_operate_feedback.content": this.content,
          "nc_operate_feedback.file": JSON.stringify(upArr),
          "nc_operate_feedback.contact": "",
          _id: this.typeObj._id,
        },
      };
      params.data = JSON.stringify(params.data);
      dsf.http
        .post("meta/persistData", params)
        .done(d => {
          if (d.state == "20000") {
            this.dsf.layer.message("提交成功", true);
            let timer = setTimeout(() => {
              this.$router.go(-1);
            }, 1500);
          } else {
            this.dsf.layer.message(d.message, false);
          }
        })
        .error(d => {
          this.dsf.layer.message(d.message, false);
        })
        .always(() => {
          this.dsf.layer.closeLoading(loader);
        });
    },
  },
});
</script>
<style lang="scss" scoped>
@import "../../../assets/styles/mobile/index.scss";
.bbs-add {
  padding: 12px;
  overflow: auto;

  & > .button {
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    border-radius: 5px;
    @include background-theme("normal");
    @include font_4;
    margin-top: 12px;
  }
  .ds-editor {
    height: 150px;
    @include font_3;
    &:empty::before {
      color: #999;
      @include font_3;
    }
  }
  & > .content {
    background: #fff;

    & > div {
      padding: 12px;
      border-bottom: 1px solid #e5e5e5;
      color: #333;
      @include font_3;

      .title {
        margin: 4px 0 16px;
        line-height: 24px;
      }

      .van-field__control {
        color: #666;
        @include font_3;
        justify-content: flex-end;
      }

      & > .van-cell {
        padding: 0;
      }

      &:last-child {
        border: 0;
      }

      &.select {
        height: 56px;
        display: flex;
        align-items: center;
        @include font_3;

        .van-dropdown-menu::after {
          border: 0;
        }

        .van-dropdown-menu__item {
          text-align: right;
          justify-content: flex-end;
          padding-right: 10px;

          .van-dropdown-menu__title {
            @include font_3;
            color: #666;
          }
        }

        .van-popup--top {
          left: 12px;
          width: calc(100% - 24px);
        }

        &.no-val {
          .van-dropdown-menu__title {
            color: #b2b2b2;
          }
        }
      }

      &.upload {
        .van-uploader__preview {
          margin-right: 0.165rem;
          margin-bottom: 0.165rem;

          &:nth-child(3n + 3) {
            margin-right: 0;
          }
        }

        .van-uploader__preview-image,
        .van-uploader__upload {
          width: 2.07rem;
          height: 1.55rem;

          &.van-uploader__upload {
            margin: 0;
          }
        }
      }
    }
  }
}
</style>
