<template>
  <div ref="detail" class="channel-detail">
    <div class="channel-detail-top">
      <div class="channel-detail-img">
        <van-image :src="getCover()" fit="fill">
          <template v-slot:loading>
            <van-loading type="spinner" size="20" />
          </template>
          <template v-slot:error><img :src="require('_nc/assets/styles/themes/img/file-read-18688.png')" style="width:100%;height:100%"></template>
        </van-image>
      </div>
      <div class="channel-detail-info-box">
        <div class="channel-detail-info-title ellipsis">{{channelData.title||''}}</div>
        <div class="channel-detail-info-label-box">
          <div v-for="(label,index) in channelData['label_text']" :key="index" class="channel-label">{{label||''}}</div>
        </div>
        <div class="channel-detail-info-info">
          <div class="channel-detail-info-info-label">共{{channelData.count||0}}门</div>
          <div class="channel-detail-info-info-label">{{'发布 '+channelData.ds_update_time||''}}</div>
          <div class="channel-detail-info-info-label">
            <dsf-icon name="yanjing"></dsf-icon>{{channelData.djs||0}}
          </div>
          <div class="channel-detail-info-info-label">
            <dsf-icon name="iconfontzhizuobiaozhun13"></dsf-icon>{{channelData.dys||0}}
          </div>
        </div>
        <div class="channel-detail-read-btn" @click="subscribe">{{channelData.sfdy?'取消订阅':'+ 订阅'}}</div>
      </div>
    </div>
    <div ref="tabs">
      <van-tabs v-model="activeName" animated swipeable>
        <van-tab v-for="item in items" :key="item.name" :title="item.name" :name="item.name">
          <channelInfo v-if="item.name=='简介'" :key="item.name" :text="channelData.content||''"></channelInfo>
          <channelDetailList :id="channelId" v-else-if="item.name=='列表'" :key="item.name" style="height:100%"></channelDetailList>
          <channelDetailSource :id="channelId" v-else-if="item.name=='相关资源'" :key="item.name" style="height:100%"></channelDetailSource>
          <div v-else></div>
        </van-tab>
      </van-tabs>
    </div>
    <div class="channel-detail-bottom-box">
      <div class="channel-detail-bottom">
        <interaction ref="interaction" :id="channelId" type="pd" :shareOpts="shareOpts"></interaction>
      </div>
    </div>
  </div>
</template>

<script>
import channelDetailList from './channelDetailList';
import channelDetailSource from './channelDetailSource';
import channelInfo from './channelInfo';
import Interaction from "../interaction";

export default dsf.component({
  mixins: [$mixins.control],
  name: "DsfMobileNcChannelDetail",
  ctrlCaption: "频道详情",
  data() {
    return {
      activeName: '简介',
      items: [
        {name: "简介"},
        {name: "列表"},
        {name: "相关资源"},
      ],
      channelData:{},
      shareOpts: {
        title: "频道分享",
        content: "",
        url: window.location.href.split('#/')[0] +'#/shareChannel/'+this.$route.query.id,
      },
      channelId:this.$route.query.id
    }
  },
  computed: {},
  components: {channelDetailList, channelInfo, channelDetailSource, Interaction},
  created: function () {
    this.reqData();
  },
  mounted: function () {
    this.$nextTick(()=>{
      this.$refs.tabs.style.height = window.innerHeight + 'px';
    })
  },
  methods: {
    throttle(func, wait) {
      let timeout;
      return function () {
        let context = this;
        let args = arguments;
        if (!timeout) {
          timeout = setTimeout(() => {
            timeout = null;
            func.apply(context, args)
          }, wait)
        }
      }
    },
    reqData(){
      if(!this.channelId){
        dsf.layer.message('频道ID不存在');
      }else{
        const loaderIndex=dsf.layer.loading();
        dsf.http.get('nc/mobile/tj/pd/getPdInfo', {
          id:this.channelId
        },$$webRoot.nc).done((d) => {
          dsf.layer.closeLoading(loaderIndex);
          if (d.state === 20000) {
            d.data.ds_update_time=d.data.ds_update_time.slice(0,10);
            this.channelData=d.data;
            this.shareOpts.content=d.data.title;
            this.shareOpts.image = dsf.getUploadFileInfo(d.data.cover)[0]['absolutePath'];
          } else {
            dsf.layer.alert({
              title: '频道获取失败',
              message: d.message + " 请联系管理员"
            });
          }
        }).error(() => {
          dsf.layer.closeLoading(loaderIndex);
          dsf.layer.alert({
            title: '频道获取失败',
            message: " 请联系管理员"
          })
        })
      }
    },
    subscribe(){
      const loaderIndex=dsf.layer.loading();
      dsf.http.post('dsfa/subscribe/saveOrCancel', {
        ywlxValue: 'pd',
        ywlxText: '频道',
        ywid:this.channelData['nc_zyb_id']
      },$$webRoot.nc).done((d) => {
        dsf.layer.closeLoading(loaderIndex);
        if (d.state === 20000) {
          this.channelData.sfdy=d.data.isDo;
          dsf.layer.message(d.data.isDo?'订阅成功':'取消成功','success');
          this.$set(this.channelData,'dys',d.data.isDo?++this.channelData.dys:--this.channelData.dys);
        } else {
          dsf.layer.alert({
            title: '订阅失败',
            message: d.message + " 请联系管理员"
          });
        }
      }).error(() => {
        dsf.layer.closeLoading(loaderIndex);
        dsf.layer.alert({
          title: '订阅失败',
          message: " 请联系管理员"
        })
      })
    },
    getCover: function () {
      if(!this.channelData['cover']){
        let src=require("_nc/assets/styles/themes/img/file-read-18688.png");
        return src;
      }
      let files = dsf.getUploadFileInfo(this.channelData['cover']);
      return dsf.url.getWebPath(files[0].relativePath, $$webRoot.nc);
    }
  }
});
</script>