<template>
  <DsfMobileNcDataGrid
    class="channel"
    height="100%"
    :max-top-number="maxTopNumber"
    :top-props="'teas_jwgl_bjgl_nowbcsjy.sxrs'"
    ref="dsfDatagrid"
    :http-param="datagrid_httpParam"
    :background-color="'#fff'"
    :padding="padding"
    :margin="margin"
    :top-margin="[0, -15, 0, -15]"
    :col="col"
    :limit="limit"
    :hasLoad="hasLoad"
    :hasRefresh="hasRefresh"
  >
    <template v-slot:default="item">
      <channelItem :item="item.data" :detail-url="detailUrl"></channelItem>
    </template>
  </DsfMobileNcDataGrid>
</template>

<script>
import { dsfRouteView } from "../dsfBase";
import channelItem from "./channelItem";

export default {
  name: "channel",
  mixins: [dsfRouteView],
  data() {
    return {
      datagrid_httpParam: {
        query: JSON.stringify({
          searchValue: "",
        }),
        order: [],
        namespace: "nc.pd",
        pageName: "mhpdlist",
      },
    };
  },
  props: {
    detailUrl: {
      type: String,
      default: "",
    },
    maxTopNumber: {
      type: Number,
      default: 5,
    },
    col: {
      type: [String, Number],
      default: 1,
      desc: "一列显示几个，暂支持1和2",
    },
    hasLoad: {
      type: Boolean,
      default: true,
      desc: "是否有上拉加载",
    },
    hasRefresh: {
      type: Boolean,
      default: true,
      desc: "是否有下拉刷新",
    },
    dataSource: {
      type: String,
      default: "meta/list/data",
      desc: "请求接口，可自定义",
    },
    curr: {
      type: [String, Number],
      default: 1,
      desc: "从哪一页开始",
    },
    limit: {
      type: [String, Number],
      default: 12,
      desc: "每页显示多少",
    },
    httpParam: {
      type: Object,
      default: function () {
        return {};
      },
      desc: "接口参数",
    },
    padding: {
      type: Array,
      default() {
        return [0, 15, 0, 15];
      },
    },
    margin: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
  },
  computed: {},
  components: { channelItem },
  created: function () {},
  mounted: function () {},
  methods: {},
};
</script>
