<template>
  <div class="dsf_nc_minInfo">
    <div class="dsf_nc_minInfo_top">
      <div class="head">
        <van-image style="width: 60px; height: 60px" class="round" round :src="setImage(persion[topList[0]] || '')" :fit="fill">
          <template v-slot:error>
            <div class="image-slot-error">
              <img src="../../../assets/imgs/mobile/head.png" alt="" />
            </div>
          </template>
        </van-image>
        <div class="info">
          <p class="name">{{ persion[topList[1] || ""] }}</p>
          <p class="job">
            <span>{{ persion[topList[2] || ""] }}</span>
            <span>{{ persion[topList[3] || ""] }}</span>
          </p>
        </div>
      </div>
    </div>
    <div class="learnInfo">
      <div class="info-box" v-for="(item, idx) in itemList" :key="idx">
        <p class="num" :style="{ color: item.color }">
          <span>{{ previewData[item.field] || 0 }}</span>
          {{ item.unit }}
        </p>
        <p>{{ item.text }}</p>
      </div>
    </div>
    <DsfMobileMeMenus :isDesign="isDesign" :menus="menus" class="menu menu1"></DsfMobileMeMenus>
    <DsfMobileMeMenus :isDesign="isDesign" :menus="menus2" class="menu menu2"></DsfMobileMeMenus>
    <!-- <van-button class="exit" block>退出</van-button> -->
  </div>
</template>
<script>
export default dsf.component({
  name: "DsfMobileNcMineInfo",
  ctrlCaption: "我的个人信息",
  mixins: [$mixins.control],
  props: {
    list: {
      type: String,
      default: "注册登录,课程学习,专题培训,获得证书",
    },
    unit: {
      type: String,
      default: "天,门,次,项",
    },
    colorList: {
      type: String,
      default: "#FF8F00,#F66C6C,#0E8CEE,#1EBDB8",
    },
    fieldList: {
      type: String,
      default: "a,b,c,d",
    },
    reqUrl: {
      type: String,
      default: "",
    },
    topString: {
      type: String,
      default: "cover,name,unit,job",
    },
    menus: {
      type: Array,
      default: () => {
        return [
          {
            id: 1,
            name: "培训记录",
            icon: "lianxi",
            url: "",
            subhead: "",
          },
          {
            id: 2,
            name: "我的笔记",
            icon: "shouxieqianpi",
            url: "",
            subhead: "",
          },
          {
            id: 3,
            name: "我的收藏",
            icon: "shoucang2",
            url: "",
            subhead: "",
          },
          {
            id: 4,
            name: "学习档案",
            icon: "wendang",
            url: "",
            subhead: "",
          },
        ];
      },
    },
    menus2: {
      type: Array,
      default: () => {
        return [
          {
            id: 1,
            name: "快速指南",
            icon: "zhinan",
            url: "",
            subhead: "",
          },
          {
            id: 2,
            name: "客服顾问",
            icon: "lianxikefu",
            url: "",
            subhead: "",
          },
          {
            id: 3,
            name: "意见反馈",
            icon: "fankui",
            url: "",
            subhead: "",
          },
          {
            id: 4,
            name: "更新记录",
            icon: "yunduanxiazai",
            url: "",
            subhead: "",
          },
          {
            id: 4,
            name: "账号绑定",
            icon: "password1",
            url: "",
            subhead: "",
          },
        ];
      },
    },
  },
  computed: {},
  data() {
    return {
      itemList: [],
      topList: [],
      persion: {},
      previewData: {},
    };
  },
  mounted() {
    //格式化
    let list = this.list.split(",");
    let unit = this.unit.split(",");
    let colorList = this.colorList.split(",");
    let fieldList = this.fieldList.split(",");
    this.topList = this.topString.split(",");
    list.forEach((item, index) => {
      let obj = { text: item, unit: unit[index] || "", color: colorList[index] || "", field: fieldList[index] || "" };
      this.itemList.push(obj);
    });

    if (this.isDesign) {
      return;
    } else {
      this.userInfo();
    }
  },
  methods: {
    userInfo() {
      if (this.reqUrl) {
        this.dsf.http
          .get(this.reqUrl, {})
          .done(res => {
            if (res.success) {
              this.persion = res.data.persion || {};
              this.previewData = res.data.previewData || {};
            } else {
              this.dsf.layer.mobile.message(res.message || "获取数据异常", false);
            }
          })
          .error(response => {
            this.dsf.layer.mobile.message(response.message || "获取数据异常", false);
          });
      }
    },
    setImage(item) {
      // const data = item;
      let src = "";
      // if (dsf.isDef(data) && data[0]) {
      //   src = data[0].photo ? dsf.url.getWebPath(JSON.parse(data[0].photo)[0].relativePath) : "";
      // }
      if (item) {
        src = dsf.url.getWebPath(JSON.parse(item)[0].relativePath) || "";
      }
      return src;
    },
  },
});
</script>
<style lang="scss" scoped>
@import "../../../assets/styles/mobile/index.scss";
.dsf_nc_minInfo {
  // height: 213px;
  .dsf_nc_minInfo_top {
    background-image: linear-gradient(180deg, #0176f6 0%, #5eabff 57%, rgba(14, 122, 255, 0) 98%, rgba(14, 122, 255, 0) 100%);
    position: relative;
  }
  /deep/ .ds-me-menus-row-label {
    font-size: 15px;
  }
  /deep/ .ds-me-menus-row-icon {
    font-size: 17px;
  }
  /deep/ .ds-me-menus-row {
    padding: 0 16px !important;
    margin: 0 15px !important;
    background: #fff;
  }
  .menu {
    // background: #fff;
  }
  .menu1 {
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .menu2 {
    margin-bottom: 10px;
  }
  .head {
    padding: 30px 49px 15px 49px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    .round {
      border-radius: 50%;
      border: 1px solid #fff;
      flex-shrink: 0;
      .image-slot-error img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .info {
    margin-left: 20px;
    color: #fff;
    p:first-child {
      margin-bottom: 10px;
      font-size: 15px;
    }
    .job {
      font-size: 13px;
      width: 100%;
      overflow: hidden;
      span:first-child {
        margin-right: 5px;
      }
    }
  }
  .learnInfo {
    height: 70px;
    border-radius: 8px;
    margin: 0 15px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    background: #fff;
    .info-box {
      font-size: 14px;
      // text-align: center;
      .num {
        color: #ff8f00;
        font-size: 13px;
        margin-bottom: 10px;
        span {
          font-size: 19px;
        }
      }
    }
  }
  .exit {
    @include background-theme("normal");
    color: #fff;
  }
}
</style>
