<template>
  <div class="dsf_nc_mobile_livePlay" v-if="visible">
    <div class="player-box" :style="{ height: getPlayHeight(), width: playerWidth }">
      <live-player v-if="getLivePlayer" :detailData="ncLiveInfo" :playerHeight="playerHeight" :height="playerHeight" :state="liveInfo.state.value"></live-player>
      <div v-else-if="getVideoPlay">
        <coursePlayer :height="playerHeight" :videoSource="getVideoSource" :isLive="liveInfo.state.value == 0 ? true : false"></coursePlayer>
      </div>
      <!-- value 0预告 1直播中 2结束 -->
      <div class="img-box" v-if="getLiveState" :style="{ height: playerHeight }">
        <van-image width="100%" height="100%" fit="fill" :src="ncLiveInfo.cover | imgFormat">
          <template v-slot:loading>
            <van-loading type="spinner" size="20" />
          </template>
          <template v-slot:error>
            <img :src="dsf.config.setting_error_img" />
          </template>
        </van-image>
        <div v-if="ncLiveInfo.stateValue == 0" class="liveClose">该直播间被停用</div>
        <div v-else-if="liveInfo.state.value === 2" class="liveClose">直播结束</div>
        <div v-else-if="liveInfo.state.value === 0" class="liveClose">
          <p>开播时间:{{ ncLiveInfo.timeSdate || "" }}</p>
        </div>
      </div>
    </div>
    <!--简介 -->
    <div class="flex1">
      <div v-if="showBrief" class="abstract">
        <div class="live-detail-title">{{ ncLiveInfo.title || "暂无标题" }}</div>
        <div class="abstract-title"><span class="title-span">简介</span></div>
        <div class="live-detail-abstract" v-if="ncLiveInfo.content" v-html="ncLiveInfo.content"></div>
      </div>
      <div class="video-info padding-15" v-if="liveInfo.state.value === 0">
        <div class="time">
          <span>倒计时：</span>
          <span>{{ time.days }}</span>
          <span>:</span>
          <span>{{ time.hours }}</span>
          <span>:</span>
          <span>{{ time.minutes }}</span>
          <span>:</span>
          <span>{{ time.seconds }}</span>
        </div>
        <div>{{ liveInfo.subscribeTotal || 0 }}人 预约</div>
      </div>
    </div>
    <div v-if="showLineLive" @click="goLiveLine" class="detail-bottom padding-15">{{ liveInfo.state.value == 2 ? "直播回放" : "进入直播" }}</div>
    <div v-if="!showLineLive && liveInfo.state.value === 0" @click="changeState" class="detail-bottom padding-15">
      {{ liveInfo.subscribe ? "已预约" : "立即预约" }}
    </div>
  </div>
</template>
<script>
import livePlayer from "../../base/live/livePlayer.vue";
import coursePlayer from "../../base/course/coursePlayer.vue";

export default dsf.component({
  name: "DsfMobileNcLivePlay",
  ctrlCaption: "直播播放页",
  mixins: [$mixins.control],
  props: {
    visible: {
      type: Boolean,
      default: true,
    },
    sourceUrl: {
      default: "nc/live/info",
      type: String,
    },
    ratio: {
      default: "4:3",
      type: String,
    },
    playerWidth: {
      default: "100%",
      type: String,
    },
  },
  data() {
    return {
      id: "",
      playerHeight: "",
      liveInfo: {
        state: {
          value: 5,
        },
      },
      ncLiveInfo: {},
      time: {
        days: "00",
        hours: "00",
        minutes: "00",
        seconds: "00",
      },
      getLivePlayer: false,
      getVideoPlay: false,
      getVideoSource: false,
      getLiveState: false,
      showBrief: false,
      showLineLive: false,
    };
  },

  components: {
    livePlayer,
    coursePlayer,
  },

  computed: {},
  created() {},
  mounted() {
    this.id = this.queryString["id"] || "4e74636177744ab2a0816bf9b3d7c44f";
    // this.id = this.queryString["id"] || "44c8d879bb834d2c9b1b2f8ac1b55fdc";
    this.getLiveinfo();
  },
  methods: {
    getPlayHeight() {
      let width = $(".player-box").width();
      let arr = this.ratio.split(":");
      this.playerHeight = (width * arr[0]) / (arr[1] * 100) + "rem";
      console.log(this.playerHeight);
      return this.ncLiveInfo.typeValue === "1" && this.ncLiveInfo.stateValue != 0 ? (this.liveInfo.state.value == 0 ? `auto` : "100vh") : (width * arr[0]) / (arr[1] * 100) + "rem";
    },
    //获取直播信息
    getLiveinfo() {
      this.dsf.http
        .get(this.sourceUrl, {
          id: this.id,
        })
        .done(res => {
          if (res.success) {
            this.ncLiveInfo = res.data.ncLiveInfo;
            this.liveInfo = res.data;
            this.timeDown();
            this.getLivePlayer = this.LivePlayer();
            this.getVideoPlay = this.VideoPlay();
            this.showBrief = this.Brief();
            this.VideoSource();
            this.getLiveState = this.LiveState();
          } else {
            this.dsf.layer.mobile.message(res.message || "获取直播数据异常", false);
          }
        })
        .error(response => {
          this.dsf.layer.mobile.message(response.message || "获取直播数据异常", false);
        });
    },
    timeDown() {
      if (this.ncLiveInfo.timeSdate) {
        let zbsj = dsf.date.parse(this.ncLiveInfo.timeSdate, "yyyy-mm-dd hh:ii:ss");
        let currDate = new Date();
        let b = dsf.date.timeDown(currDate, zbsj);
        this.time = b;
      }
    },
    //预约
    changeState() {
      let loader = this.dsf.layer.loading();
      this.dsf.http
        .post("/dsfa/subscribe/saveOrCancel", {
          ywlxValue: "live",
          ywlxText: "直播",
          ywid: this.id,
        })
        .done(d => {
          this.dsf.layer.closeLoading(loader);
          if (d.success) {
            this.liveInfo.subscribe = d.data.isDo;
            if (d.data.isDo) {
              ++this.liveInfo.subscribeTotal;
            } else {
              --this.liveInfo.subscribeTotal;
              if (this.liveInfo.subscribeTotal < 0) {
                this.liveInfo.subscribeTotal = 0;
              }
            }
            this.dsf.layer.message(this.liveInfo.subscribe ? "预约成功" : "取消预约成功", true);
          } else {
            this.dsf.layer.message(d.message || "操作数据异常", false);
          }
        })
        .error(response => {
          this.dsf.layer.closeLoading(loader);
          this.dsf.layer.message("服务器异常", false);
        });
    },

    //保利威视
    LivePlayer() {
      let flag = false;
      if (this.liveInfo.state.value === 1 || this.liveInfo.state.value === 2) {
        //直播中或者直播结束 并且是发布状态
        if (this.ncLiveInfo.typeValue === "1" && this.ncLiveInfo.stateValue != 0) {
          //类型是保利直播
          flag = true;
        }
      }
      return flag;
    },
    //获取视频播放
    VideoPlay() {
      let flag = false;
      if (this.liveInfo.state.value === 1 || this.liveInfo.state.value === 2) {
        //直播中和直播结束
        // 3 课件 4视频
        if (this.ncLiveInfo.typeValue === "3" || this.ncLiveInfo.typeValue === "4") {
          //视频播放并且有video
          if (this.ncLiveInfo.stateValue !== "0") {
            //启用状态
            flag = true;
          }
        }
      }
      return flag;
    },
    Brief() {
      let flag = false;
      //预告时显示简介
      if (this.liveInfo.state.value === 0) {
        flag = true;
      } else if (this.liveInfo.state.value === 1 || this.liveInfo.state.value === 2) {
        //直播中和直播结束
        // 3 课件 4视频
        if (this.ncLiveInfo.typeValue === "3" || this.ncLiveInfo.typeValue === "4") {
          //视频播放并且有video
          flag = true;
        }
        if (this.ncLiveInfo.stateValue == 0) {
          flag = true;
        }
        if (this.ncLiveInfo.typeValue === "2") {
          //链接直播 直播连接存在 并且未停用
          flag = true;
          //显示进入链接直播按钮
          if (this.ncLiveInfo.liveUrl && this.ncLiveInfo.stateValue !== "0") {
            this.showLineLive = true;
          }
        }
      }
      return flag;
    },
    async VideoSource() {
      let video = null;
      //预约中
      if (this.liveInfo.state.value === 0 || this.ncLiveInfo.stateValue == "0") {
        return;
      }
      if (this.ncLiveInfo.typeValue === "4") {
        //  4视频直播 (后续需要增加切片)
        if (this.ncLiveInfo.video && this.ncLiveInfo.video.length > 2) {
          video = dsf.url.getWebPath(JSON.parse(this.ncLiveInfo.video)[0].relativePath);
          this.getVideoSource = video;
        }
      } else if (this.ncLiveInfo.typeValue === "3") {
        //3 课件
        let res = await this.getVideoUrlApi({
          coursewareId: this.ncLiveInfo.coursewareValue,
        });
        if (res.fileUrl && res.fileUrl.length > 2) {
          video = dsf.url.getWebPath(JSON.parse(res.fileUrl).sd.relative);
          !video && dsf.layer.message("视频不可用", false);
          console.log(JSON.parse(res.fileUrl));
          this.getVideoPlay = false;
          setTimeout(() => {
            this.getVideoPlay = true;
            this.getVideoSource = video;
          });
        }
      }
    },
    getVideoUrlApi(params) {
      return new Promise((resolve, reject) => {
        let loading = dsf.layer.loading();
        dsf.http
          .get("nc/courseware/queryVideoUrlById", params)
          .done(d => {
            if (d.success) {
              resolve(d.data);
            } else {
              dsf.layer.message(d.message || "请求异常", false);
              reject();
            }
          })
          .error(response => {
            dsf.error(response);
            dsf.layer.message(response.message || "请求异常", false);
            reject();
          })
          .always(() => {
            dsf.layer.closeLoading(loading);
          });
      });
    },
    //获取直播状态
    LiveState() {
      let flag = false;
      //直播结束
      if (this.liveInfo.state.value === 2) {
        if (this.ncLiveInfo.stateValue == 0) {
          //停用
          flag = true;
        }
        if (this.ncLiveInfo.typeValue == 2) {
          // 链接
          flag = true;
        }
      } else if (this.liveInfo.state.value === 0) {
        //直播预告
        flag = true;
      } else if (this.liveInfo.state.value === 1) {
        if (this.ncLiveInfo.typeValue == 2) {
          // 链接直播中 不管停用不停用
          flag = true;
        }
        //课件直播.视频直播停用
        if ((this.ncLiveInfo.typeValue == 1 || this.ncLiveInfo.typeValue == 3 || this.ncLiveInfo.typeValue == 4) && this.ncLiveInfo.stateValue == 0) {
          flag = true;
        }
      }

      return flag;
    },
    goLiveLine() {
      window.open(this.ncLiveInfo.liveUrl);
    },
  },
});
</script>
<style lang="scss" scoped>
@import "../../../assets/styles/mobile/index.scss";
.dsf_nc_mobile_livePlay {
  height: 100%;
  display: flex;
  flex-direction: column;
  .player-box {
    flex-shrink: 0;
    width: 100%;
    height: auto;
  }
  .img-box {
    width: 100%;
    height: 100%;
    position: relative;
    img {
      width: 100%;
      height: 100%;
    }
    .liveClose {
      width: 100%;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      text-align: center;
      height: 50px;
      line-height: 50px;
      background: rgba(217, 219, 222, 0.7);
    }
  }
  .padding-15 {
    padding: 0 15px;
    box-sizing: border-box;
  }
  .flex1 {
    padding-bottom: 10px;
    background: #fff;
    flex: 1;
  }
  .video-info {
    .time {
      margin: 10px 0;
    }
  }
  .detail-bottom {
    flex-shrink: 0;
    height: 0.96rem;
    @include background-theme("normal");
    box-sizing: border-box;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 16px;
    justify-content: center;
    color: #fff;
  }

  .abstract {
    background-color: #fff;
    line-height: 25px;
    padding: 15px 15px;
    flex-shrink: 0;
    .abstract-title {
      position: relative;
      padding-left: 10px;
      display: flex;
      align-items: center;
      margin-bottom: 5px;
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 5px;
        height: 20px;
        @include background-theme("normal");
      }
      .title-span {
        font-size: 18px;
        line-height: 24px;
      }
    }
    .live-detail-title {
      font-weight: bold;
      padding-left: 0;
    }
    .live-detail-abstract {
      // min-height: 50px;
      text-align: justify;
      @include font_5;
      color: rgba(51, 51, 51, 1);
    }
  }
}
</style>
