<template>
  <div class="ac-teacher-detail-wrap">
    <div class="ac-teacher-detail-header">
      <div class="ac-tch-dtl-hd-top">
        <div class="ac-tch-dtl-hd-name-wrap">
          <div class="ac-tch-dtl-hd-name">{{ pageInfo.name }}</div>
          <van-image :src="setImg(pageInfo.photo)" fit="full" class="ac-tch-dtl-hd-ico">
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
            <template v-slot:error>
              <img :src="require('_nc/assets/styles/themes/img/file-read-18689.png')" style="width: 100%; height: 100%" />
            </template>
          </van-image>
        </div>
      </div>
      <div class="ac-tch-dtl-hd-bottom">
        <div class="ac-tch-dtl-hd-post">
          <div class="ac-tch-dtl-hd-post-cont">{{ pageInfo.job }}</div>
        </div>
        <div class="ac-tch-dtl-hd-course-wrap">
          <div class="course-subscribe" @click="subscribeOn">
            <span>{{ pageInfo.isSubscribe ? "取消订阅" : "订阅" }}</span>
          </div>
          <div class="course-inner">
            <span class="course-num">{{ pageInfo.courseNum || 0}}</span>
            <span class="course-name">门课程</span>
          </div>
        </div>
        <div class="ac-tch-dtl-hd-story-wrap">
          <p class="story-content" :class="storyState == 'close' ? 'van-multi-ellipsis--l3' : 'van-t'" v-html="pageInfo.description"></p>
          <div class="story-content-icon" @click="onStory()" v-text="storyState == 'close' ? '展开' : '收起'"></div>
        </div>
      </div>
    </div>
    <div class="ac-teacher-detail-domain-wrap">
      <div class="domain-title-wrap">
        <div class="domain-title-inner">
          <div class="domain-title-icon"></div>
          <div class="domain-title-name">擅长领域</div>
        </div>
      </div>
      <div class="domain-label-wrap">
        <div v-for="(item, index) in pageInfo.label" :key="index" class="domain-label-item">
          <span>{{ item }}</span>
        </div>
      </div>
    </div>
    <div class="ac-teacher-detail-lesson-wrap">
      <div class="lesson-title-wrap">
        <div class="lesson-title-left">
          <div class="lesson-title-icon"></div>
          <div class="lesson-title-name">课程</div>
        </div>
        <div class="lesson-title-right">
          <div class="lesson-filter-item">
            <dsf-sort :iconName="'最新'" @click="sortTop"></dsf-sort>
            <!-- <div class="lesson-filter-item-name">最新</div>
             <div class="lesson-filter-item-icon-g">
               <dsf-icon name="shangjiantou" class="top" :class="top == 'up'?'on':'def'"></dsf-icon>
               <dsf-icon name="xiajiantou" class="bottom" :class="top == 'down'?'on':'def'"></dsf-icon>
             </div>-->
          </div>
          <div class="lesson-filter-item">
            <dsf-sort :iconName="'最热'" @click="sortHot"></dsf-sort>
            <!--<div class="lesson-filter-item-name">最热</div>
            <div class="lesson-filter-item-icon-g">
              <dsf-icon name="shangjiantou" class="top" :class="hot == 'up'?'on':'def'"></dsf-icon>
              <dsf-icon name="xiajiantou" class="bottom" :class="hot == 'down'?'on':'def'"></dsf-icon>
            </div>-->
          </div>
        </div>
      </div>
      <teacher-course-list
        v-if="pageInfo.id"
        :has-load="true"
        :has-refresh="true"
        :limit="8"
        :padding="[8, 8, 8, 8]"
        :hot="hot"
        :top="top"
        :tchId="pageInfo.id"
      ></teacher-course-list>
    </div>
  </div>
</template>

<script>
import { TeacherCourseList } from "../course";
export default dsf.component({
  mixins: [$mixins.control],
  name: "DsfMobileNcTeacherDetail",
  ctrlCaption: "师资详情",
  components: { TeacherCourseList },
  data() {
    return {
      storyState: "close",
      labelList: [],
      top: "desc",
      hot: "desc",
      id: null,
      pageInfo: {
        id: "",
        name: "",
        job: "",
        courseNum: "",
        label: [],
        photo: "",
        description: "",
      },
    };
  },
  props: {
    col: {
      type: [String, Number],
      default: 3,
      desc: "一列显示几个，暂支持1和2",
    },
    hasLoad: {
      type: Boolean,
      default: true,
      desc: "是否有上拉加载",
    },
    hasRefresh: {
      type: Boolean,
      default: true,
      desc: "是否有下拉刷新",
    },
    dataSource: {
      type: String,
      default: "meta/list/data",
      desc: "请求接口，可自定义",
    },
    curr: {
      type: [String, Number],
      default: 1,
      desc: "从哪一页开始",
    },
    limit: {
      type: [String, Number],
      default: 12,
      desc: "每页显示多少",
    },
    httpParam: {
      type: Object,
      default: function () {
        return {};
      },
      desc: "接口参数",
    },
    detailRoute: {
      type: String,
      default: "",
    },
  },
  created() {
    let _this = this;
    if (!this.isDesign) _this.initPage();
  },
  mounted() {},
  methods: {
    subscribeOn() {
      let that = this;
      const loaderIndex = dsf.layer.loading();
      dsf.http
        .post("nc/mobile/tyzj/hd/subscribe/saveOrCancel", {
          ywlxValue: "ms",
          ywid: that.$route.query.id,
        },$$webRoot.nc)
        .done((d) => {
          dsf.layer.closeLoading(loaderIndex);
          if (d.state === 20000) {
            d.data.isDo ? (that.pageInfo.isSubscribe = 1) : (that.pageInfo.isSubscribe = 0);
            dsf.layer.message(d.data.isDo ? "订阅成功" : "取消成功", "success");
          } else {
            dsf.layer.alert({
              title: "订阅失败",
              message: d.message + " 请联系管理员",
            });
          }
        })
        .error(() => {
          dsf.layer.closeLoading(loaderIndex);
          dsf.layer.alert({
            title: "订阅失败",
            message: " 请联系管理员",
          });
        });
    },
    setImg(url) {
      let cover = url;
      if (cover) {
        cover = $$webRoot.nc + url;
      }
      return cover || "/";
    },
    repeatGo() {
      this.dsf.platform.share({
        url: window.location.href,
        title: "名师详情",
      });
    },
    sortTop(ref) {
      this.top = ref;
    },
    sortHot(ref) {
      this.hot = ref;
    },
    onStory() {
      let _story = this.storyState;
      if (_story == "close") {
        this.storyState = "open";
      } else if (_story == "open") {
        this.storyState = "close";
      }
    },
    onscreen(who) {
      let _this = this;
      if (_this[who] == "down") {
        _this[who] = "up";
      } else {
        _this[who] = "down";
      }
    },
    initPage() {
      let loader = dsf.layer.loading();
      let _id = this.$route.query.id || "71c0dcd8220f403dbc8bb552aeaca909";
      let _this = this;
      this.$http
        .get("nc/mobile/tj/ms/findMsInfo", {id: _id },$$webRoot.nc)
        .done((d) => {
          dsf.layer.closeLoading(loader);
          if (d.success) {
            let _data = d.data;
            let _info = {
              id: _data.nc_szk_id,
              name: _data.name,
              job: _data.job_text,
              courseNum: _data.courseNum,
              label: _data.label_text ? _data.label_text.split("^") : [],
              photo: _data.photo ? JSON.parse(_data.mobphoto)[0].relativePath : null,
              description: _data.description ? _data.description.replace(/<\/?.+?>/g, "").replace(/ /g, "") : '-',
              isSubscribe: _data.isSubscribe, //0（未订阅） 1（已订阅）
            };
            _this.pageInfo = _info;
          } else {
            dsf.layer.message(d.message || "获取列表数据异常", false);
          }
        })
        .error((response) => {
          dsf.layer.closeLoading(loader);
          dsf.layer.message("服务器异常", false);
        });
    },
  },
});
</script>
