<template>
  <van-row class="interaction-comp" :class="{ disabled: disabled }" type="flex" justify="space-around">
    <van-col span="1" @click="likeChange">
      <dsf-icon class="dianzan-icon" :name="disabled || liked ? 'dianzan_kuai' : 'dianzan'"></dsf-icon>
      <span class="num">
        {{ likeNum < 0 ? 0 : likeNum }}
      </span>
    </van-col>
    <van-col span="1" @click="collectChange">
      <dsf-icon class="aixin-icon" :name="disabled || collected ? 'aixin_shixin' : 'aixin'"></dsf-icon>
      <span class="num">
        {{ collectNum < 0 ? 0 : collectNum }}
      </span>
    </van-col>
    <van-col span="6" @click="shareChange" class="share">
      <dsf-icon class="fenxiang-icon" name="fenxiang"></dsf-icon>
      <span>分享</span>
    </van-col>
  </van-row>
</template>

<script>
/* eslint-disable */

export default {
  props: {
    id: {
      type: [String, Number],
      default: "",
      desc: "事务id",
    },
    type: {
      type: String,
      default: "",
      desc: "事务类型(课程00、新闻xw、学员说xys、频道pd、名师ms、专题班ztb)",
    },
    likedFun: {
      type: Function,
      default: function () {},
    },
    collectedFun: {
      type: Function,
      default: function () {},
    },
    shareOpts: {
      type: Object,
      default: function () {
        return {};
      },
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      param: {
        ywlxValue: this.type,
        ywid: this.id,
      },
      liked: false,
      collected: false,
      likeNum: 0,
      collectNum: 0,
    };
  },
  created() {
    // 获取点赞状态
    this.getLiked();
    // 获取收藏状态
    this.getCollected();
  },
  methods: {
    getLiked() {
      dsf.http
        .get("nc/mobile/tyzj/hd/tags/isTags", this.param, $$webRoot.nc)
        .done((res) => {
          if (res.success) {
            this.liked = res.data.isTags;
            this.likeNum = res.data.num;
          } else {
            dsf.layer.message(res.message, false);
          }
        })
        .error(function (err) {
          dsf.layer.message(err.message, false);
        });
    },
    getCollected() {
      dsf.http
        .get("nc/mobile/tyzj/hd/collect/isCollect", this.param, $$webRoot.nc)
        .done((res) => {
          if (res.success) {
            this.collected = res.data.isCollect;
            this.collectNum = res.data.num;
          } else {
            dsf.layer.message(res.message, false);
          }
        })
        .error(function (err) {
          dsf.layer.message(err.message, false);
        });
    },
    likeChange() {
      if (!this.disabled) {
        this.http(0, (d) => {
          this.liked = !this.liked;
          this.likeNum = this.likeNum + (this.liked ? 1 : -1);
          this.likedFun(d);
        });
      }
    },
    collectChange() {
      if (!this.disabled) {
        this.http(1, (d) => {
          this.collected = !this.collected;
          this.collectNum = this.collectNum + (this.collected ? 1 : -1);
          this.collectedFun(d);
        });
      }
    },
    http(i, callBack) {
      let loader = dsf.layer.loading();
      let sql = ["nc/mobile/tyzj/hd/tags/saveOrCancel", "nc/mobile/tyzj/hd/collect/saveOrCancel"][i];
      let text = this[["liked", "collected"][i]] ? "取消" : "操作";
      dsf.http
        .post(sql, this.param, $$webRoot.nc)
        .done((res) => {
          dsf.layer.closeLoading(loader);
          if (res.success) {
            dsf.layer.message(text + "成功", true);
            callBack(res.data);
          } else {
            dsf.layer.message(text + "失败", false);
          }
        })
        .error(function (err) {
          dsf.layer.closeLoading(loader);
          dsf.layer.message(err.message, false);
        });
    },
    shareChange() {
      dsf.platform.share(
        dsf.mix(
          true,
          {
            url: window.location.href,
          },
          this.shareOpts
        )
      );
    },
  },
};
</script>

<style></style>
