<template>
  <div class="dsf-player-mini" :class="{ hide: !currentShow }">
    <div class="close" @click="close">
      <img src="_nc/assets/images/player/mini/close.png" class="close" />
    </div>
    <div class="info-content" @click="toDetail">
      <van-image :src="coverImg" class="info-content-conver" :fit="'full'">
        <template v-slot:loading>
          <van-loading type="spinner" size="20" />
        </template>
        <template v-slot:error>
          <img :src="require('_nc/assets/styles/themes/img/file-read-18688.png')" style="width: 100%; height: 100%" />
        </template>
      </van-image>
      <div class="info-content-inner">
        <p class="info-content-title van-ellipsis">{{ title }}</p>
        <p class="info-content-subtitle">{{ subTitle }}</p>
      </div>
    </div>
    <div class="controller">
      <div class="up" @click="toDetail">
        <img src="_nc/assets/images/player/mini/up.png" />
      </div>
      <div class="waiting" v-if="isWaiting">
        <van-loading type="spinner" />
      </div>
      <div class="pause" @click="pause" v-if="!isWaiting && isPlaying">
        <img src="_nc/assets/images/player/mini/pause.png" />
      </div>
      <div class="play" @click="play" v-if="!isWaiting && !isPlaying">
        <img src="_nc/assets/images/player/mini/play.png" />
      </div>
      <!-- <div class="hide"><img src="_nc/assets/images/player/mini/hide.png"></div> -->
    </div>
    <div class="hide-bar" @click="showOrHide">
      <img v-if="currentShow" src="_nc/assets/images/player/mini/hide.png" />
      <img v-else src="_nc/assets/images/player/mini/open.png" />
    </div>
    <div style="display: none">
      <!-- <video-player
        ref="videoPlayer"
        :playsinline="true"
        :options="playerOptions"
        @play="playerPlay($event)"
        @pause="playerPause($event)"
        @ended="playerEnded($event)"
        @loadeddata="playerLoadeddata($event)"
        @waiting="playerWaiting($event)"
        @playing="playerPlaying($event)"
        @timeupdate="playerTimeupdate($event)"
        @canplay="playerCanplay($event)"
        @canplaythrough="playerCanplaythrough($event)"
        @ready="playerReadied"
        @statechanged="playerStateChanged($event)"
      ></video-player> -->
    </div>
  </div>
</template>
<script>
// import "videojs-contrib-hls";
// import "video.js/dist/video-js.css";
// import { videoPlayer } from "./vue-video-player";
export default {
  name: "dsfPlayerMini",
  // components: { videoPlayer },
  computed: {
    player() {
      return this.$refs.videoPlayer.player;
    },
    videoSrc() {
      return function () {
        let url = this.videoSource;
        let arr = url.split(".");
        let extName = arr[arr.length - 1];
        let type = "";
        switch (extName) {
          case "mp3":
            type = "audio/mp3";
            break;
          case "m3u8":
            type = "application/x-mpegURL";
            break;
          case "mp4":
            type = "video/mp4";
            break;
          default:
            type = "";
            break;
        }
        if (type) {
          return {
            type: type,
            src: url,
          };
        } else {
          return url;
        }
      };
    },
  },
  props: {
    autoPlay: {
      type: Boolean,
      default: true,
    },
    videoSource: {
      //视频地址
      type: [String],
    },
    seekTime: {
      //跳转到对应播放位置
      type: Number,
      default: 0,
    },
    coverImg: {
      //视频封面
      type: String,
      default: "",
    },
    title: {
      type: String,
      default: "",
    },
    subTitle: {
      type: String,
      default: "",
    },
    isShow: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      initPlay: false,
      isSeeking: false,
      currentShow: this.isShow,
      isPlaying: false,
      isWaiting: true,
      playerOptions: {
        playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
        autoplay: this.autoPlay, //如果true,浏览器准备好时开始回放。
        muted: false, // 默认情况下将会消除任何音频。
        loop: false, // 导致视频一结束就重新开始。
        preload: "auto", // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        language: "zh-CN",
        aspectRatio: "16:9", // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        hls: true,
        sources: [],
        poster: this.coverImg, //你的封面地址
        width: document.documentElement.clientWidth, //播放器宽度
        notSupportedMessage: "此视频暂无法播放",
        languages: {
          "zh-CN": {
            Play: "播放",
            Pause: "暂停",
            "Current Time": "当前时间",
            Duration: "时长",
            "Remaining Time": "剩余时间",
            "Stream Type": "媒体流类型",
            LIVE: "直播",
            Loaded: "加载完毕",
            Progress: "进度",
            Fullscreen: "全屏",
            "Non-Fullscreen": "退出全屏",
            Mute: "静音",
            Unmute: "取消静音",
            "Playback Rate": "播放速度",
            Subtitles: "字幕",
            "subtitles off": "关闭字幕",
            Captions: "内嵌字幕",
            "captions off": "关闭内嵌字幕",
            Chapters: "节目段落",
            "Close Modal Dialog": "关闭弹窗",
            Descriptions: "描述",
            "descriptions off": "关闭描述",
            "Audio Track": "音轨",
            "You aborted the media playback": "视频播放被终止",
            "A network error caused the media download to fail part-way.": "网络错误导致视频下载中途失败。",
            "The media could not be loaded, either because the server or network failed or because the format is not supported.":
              "视频因格式不支持或者服务器或网络的问题无法加载。",
            "The media playback was aborted due to a corruption problem or because the media used features your browser did not support.":
              "由于视频文件损坏或是该视频使用了你的浏览器不支持的功能，播放终止。",
            "No compatible source was found for this media.": "无法找到此视频兼容的源。",
            "The media is encrypted and we do not have the keys to decrypt it.": "视频已加密，无法解密。",
            "Play Video": "播放视频",
            Close: "关闭",
            "Modal Window": "弹窗",
            "This is a modal window": "这是一个弹窗",
            "This modal can be closed by pressing the Escape key or activating the close button.": "可以按ESC按键或启用关闭按钮来关闭此弹窗。",
            ", opens captions settings dialog": ", 开启标题设置弹窗",
            ", opens subtitles settings dialog": ", 开启字幕设置弹窗",
            ", opens descriptions settings dialog": ", 开启描述设置弹窗",
            ", selected": ", 选择",
            "captions settings": "字幕设定",
            "Audio Player": "音频播放器",
            "Video Player": "视频播放器",
            Replay: "重播",
            "Progress Bar": "进度小节",
            "Volume Level": "音量",
            "subtitles settings": "字幕设定",
            "descriptions settings": "描述设定",
            Text: "文字",
            White: "白",
            Black: "黑",
            Red: "红",
            Green: "绿",
            Blue: "蓝",
            Yellow: "黄",
            Magenta: "紫红",
            Cyan: "青",
            Background: "背景",
            Window: "视窗",
            Transparent: "透明",
            "Semi-Transparent": "半透明",
            Opaque: "不透明",
            "Font Size": "字体尺寸",
            "Text Edge Style": "字体边缘样式",
            None: "无",
            Raised: "浮雕",
            Depressed: "压低",
            Uniform: "均匀",
            Dropshadow: "下阴影",
            "Font Family": "字体库",
            "Proportional Sans-Serif": "比例无细体",
            "Monospace Sans-Serif": "单间隔无细体",
            "Proportional Serif": "比例细体",
            "Monospace Serif": "单间隔细体",
            Casual: "舒适",
            Script: "手写体",
            "Small Caps": "小型大写字体",
            Reset: "重启",
            "restore all settings to the default values": "恢复全部设定至预设值",
            Done: "完成",
            "Caption Settings Dialog": "字幕设定视窗",
            "Beginning of dialog window. Escape will cancel and close the window.": "开始对话视窗。离开会取消及关闭视窗",
            "End of dialog window.": "结束对话视窗",
          },
        },
      },
    };
  },
  mounted() {
    this.isWaiting = true;
    this.initPlay = true;
    var video = this.$el.querySelector("video");
    video.setAttribute("x5-video-player-type", "h5-page");
    video.setAttribute("x-webkit-airplay", true);
    video.setAttribute("x5-video-player-fullscreen", false);
    video.removeAttribute("x5-playsinline");
    // video.setAttribute('raw-controls', false);
    console.log(this.seekTime);
  },
  methods: {
    isAndroid() {
      var u = window.navigator.userAgent;
      if (u.indexOf("Android") > -1 && u.indexOf("Linux") > -1) {
        return true;
      }
      return false;
    },
    //播放
    play() {
      this.player.play();
    },
    //暂停
    pause() {
      this.player.pause();
    },

    playerLoadeddata(player) {
      if (this.isAndroid()) {
        if (this.autoPlay && this.player) {
          //安卓必须先调用一下播放，并在开始播放后才能定位
          this.player.play();
        }
      } else {
        //苹果可以在此处直接定位
        if (this.seekTime) {
          this.player.currentTime(this.seekTime);
        }
      }
    },

    playerPlay(player) {
      this.isPlaying = true;
      this.$emit("playerPlay", player);
    },
    playerPause(player) {
      this.isPlaying = false;
      this.$emit("playerPause", player);
    },
    playerWaiting(player) {
      this.isWaiting = true;
      this.$emit("playerWaiting", player);
    },
    playerPlaying(player) {
      this.$emit("playerPlaying", player);
    },
    //当前已经播放的时间
    playerTimeupdate(player) {
      if (this.initPlay && !this.isSeeking) {
        let seekTime = this.seekTime;
        //安卓需要在此处定位
        this.isSeeking = true;
        if (this.isAndroid()) {
          window.setTimeout(() => {
            this.initPlay = false;
            if (seekTime) {
              this.player.currentTime(seekTime);
            }
            this.player.muted(false);
            this.player.play();
          }, 1000);
        } else {
          this.initPlay = false;
        }
      } else {
        this.$emit("playerPlayingTime", player.currentTime());
      }
    },
    playerReadied(player) {
      this.isWaiting = false;
      if (this.isAndroid()) {
        this.player.muted(true);
      }
      this.$emit("playerReadied", player);
    },
    //监听播放状态改变
    playerStateChanged(state) {
      this.$emit("playerState", state);
    },
    playerCanplay(player) {
      this.$emit("playerCanplay");
    },
    playerCanplaythrough(player) {
      this.isWaiting = false;
      this.$emit("playerCanplaythrough");
    },
    playerEnded() {
      this.$emit("playerEnded");
    },
    close() {
      this.$emit("close");
    },
    toDetail() {
      this.$emit("go");
    },
    showOrHide() {
      this.currentShow = !this.currentShow;
      if (this.currentShow) {
        this.$emit("show");
      } else {
        this.$emit("hide");
      }
    },
  },
  watch: {
    videoSource: {
      handler(n, o) {
        let vs = this.videoSrc();
        if (vs && vs.type) this.$set(this.playerOptions.sources, 0, vs);
        //   this.$set(this.playerOptions.sources, 0, vs);
        dsf.log(this.playerOptions.sources);
      },
      immediate: true,
    },
  },
};
</script>
