<template>
  <div style="height: 100vh; overflow: hidden">
    <DsfMobileNcDataGrid
      class="global-search"
      ref="dg"
      style="height: 100%"
      :immediate="false"
      :dataSource="'nc/mobile/es/search/data'"
      :http-param="datagrid_httpParam"
      :background-color="'#fff'"
      :col="1"
      :limit="12"
      :hasLoad="true"
      :hasRefresh="true"
      :margin="margin"
      :padding="padding"
    >
      <template v-slot:search>
        <form action="javascript:return true" @submit.prevent>
          <van-search
            v-model="searchValue"
            :show-action="true"
            :placeholder="'请输入搜索关键字'"
            @search="searchHandler"
            @cancel="cancelHandler"
          ></van-search>
        </form>
      </template>
      <template v-slot:default="item">
        <div @click="toDetail(item.data)" class="g-item">
          <p class="g-item-title van-multi-ellipsis--l2">{{ toText(item.data) }}</p>
          <p class="g-item-content">
            <span class="g-item-tag">{{ item.data.typename }}</span>
          </p>
        </div>
      </template>
    </DsfMobileNcDataGrid>
  </div>
</template>
<script>
let html2Text = document.createElement("div");
export default dsf.component({
  name: "DsfMobileNcGlobalSearch",
  ctrlCaption: "全局搜索",
  mixins: [$mixins.control],
  props: {
    padding: {
      type: Array,
      default() {
        return [0, 10, 0, 10];
      },
    },
    margin: {
      type: Array,
      default() {
        return [8, 0, 0, 0];
      },
    },
    courseDetail: {
      type: String,
      default: "/mobile/nc/recommendFile/mobile-courseDetail",
    },
    specialDetail: {
      type: String,
      default: "/mobile/nc/recommendFile/mobile-specilaDetail",
    },
    channelDetail: {
      type: String,
      default: "/mobile/nc/recommendFile/mobile-chanelDetail",
    },
    newsDetail: {
      type: String,
      default: "/mobile/nc/recommendFile/mobile-newsDetail",
    },
    liveDetail: {
      type: String,
      default: "/mobile/nc/recommendFile/mobile-liveDetail",
    },
    teacherDetail: {
      type: String,
      default: "/mobile/nc/recommendFile/mobile-teacherDetail",
    },
    // studentDetail: {
    //   type: String,
    //   default: "dd",
    // },
  },
  data() {
    return {
      searchValue: "",
      datagrid_httpParam: {
        query: JSON.stringify({ searchContent: "" }),
        order: JSON.stringify([{ code: "update_time", sort: "asc" }]),
        namespace: "",
      },
    };
  },
  mounted() {},
  methods: {
    toText(item) {
      html2Text.innerHTML = item.title;
      return html2Text.innerText;
    },
    searchHandler: function () {
      if (this.searchValue) {
        this.datagrid_httpParam.query = JSON.stringify({ searchContent: this.searchValue });
        this.$refs.dg.query();
      } else {
        this.$refs.dg.clearData();
      }
    },
    cancelHandler: function () {
      this.searchValue = "";
      this.$router.go(-1);
    },
    toDetail(item) {
      let type = item.typeid;
      let path = "";
      const id = item.id;
      switch (type) {
        case "00":
          // 课程
          path = this.courseDetail;
          break;
        case "01":
          // 频道
          path = this.channelDetail;
          break;
        case "05":
          // 专题班
          path = this.specialDetail;
          break;
        case "11":
          // 资料
          path = this.newsDetail;
          break;
        case "12":
          // 师资
          path = this.teacherDetail;
          break;
        // case "13":
        //   // 学员说
        //   path = this.studentDetail;
        //   break;
        case "14":
          // 直播
          path = this.liveDetail;
          break;
        default:
          path = "";
          break;
      }
      if (path) {
        this.$router.push({
          path: path,
          query: {
            id: id,
          },
        });
      }
    },
  },
});
</script>
