<template>
  <div v-if="isDesign" class="ds_teacher_list ds-control">
    <div class="cover">
      <el-image src="https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg"></el-image>
    </div>
    <div class="desc">
      <h3 class="desc-title">
        <span class="name">张三</span>
        <span class="total">15门精品课程</span>
      </h3>
      <div class="info ellipsis-3">中央党校（国家行政学院）</div>
      <div class="tags">
        <label class="tag-title">擅长领域</label>
        <dl class="tag">自贸区</dl>
      </div>
    </div>
  </div>
  <div v-else class="ds_teacher_list ds-control" @click="click">
    <div class="cover">
      <el-image :src="data['cover'] | imgFormat" fit="cover"></el-image>
    </div>
    <div class="desc">
      <h3 class="desc-title">
        <span class="name">{{data['name']}}</span>
        <span class="total">{{data['total']}}门精品课程</span>
      </h3>
      <div class="info ellipsis-3">{{data['position']}}</div>
      <div class="tags">
        <label class="tag-title">擅长领域</label>
        <dl class="tag" v-for="it in data['tags']" :key="it.name">{{it.name}}</dl>
      </div>
    </div>
  </div>
</template>

<script>
export default dsf.component({
  name: "DsfMobileNcTeacherList",
  ctrlCaption: "师资卡片",
  mixins: [$mixins.control],
  design: {
    isMask: false
  },
  props: {
    keysMap: {
      type: Array,
      default() {
        return [
          { from: 'cover', to: 'cover', type: 'text', desc: '封面' },
          { from: 'name', to: 'name', type: 'text', desc: '教师姓名' },
          { from: 'total', to: 'total', type: 'text', desc: '课程数量' },
          { from: 'position', to: 'position', type: 'text', desc: '职位' },
          { from: 'tags', to: 'tags', type: 'text', desc: '擅长领域' },
        ];
      }
    },
    // 跳转方式
    clickConfig: {
      type: Object,
      default: null
    },
    itemClick: {
      type: Function,
      default: null
    }
  },
  methods: {
    click() {
      let res = this.itemClick?.(this.local);
      if (res && res.then) {
        res.then(r => {
          if (r !== false) {
            this.toUrl();
          }
        });
      } else if (res !== false) {
        this.toUrl();
      }
    },
    toUrl() {
      if (!this.clickConfig) return;
      let { url, state } = this.clickConfig;
      url = this.$replace(url, this.local);
      this.$open({ url, state });
    }
  }
});
</script>
<style lang="scss" scoped>
.ds_teacher_list {
  height: 119px;
  display: flex;
  box-sizing: border-box;
  margin: 10px 15px;
  &.ds-control {
    padding: 0 !important;
  }
  .cover {
    width: 85px;
    height: 119px;
    overflow: hidden;
    flex-shrink: 0;
    border-radius: 4px;
    margin-right: 10px;
    .el-image {
      width: 100%;
      height: 100%;
    }
  }
  .desc {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &-title {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .name {
        font-size: 15px;
        color: #333333;
        line-height: 22px;
      }
      .total {
        font-size: 13px;
        color: #f66c6c;
      }
    }
    .info {
      font-size: 13px;
      color: #666666;
      line-height: 16px;
    }
    .tags {
      display: flex;
      align-items: center;
      &-title {
        font-size: 13px;
        color: #666666;
      }
      .tag {
        font-size: 11px;
        color: #1682f8;
        background: #e7f4ff;
        border-radius: 9.5px;
        margin: 0 6px;
        padding: 2px 8.5px;
      }
    }
  }
}
</style>