<template>
  <div class="dsf-info-panel ds_control">
    <div class="dsf-info-panel-title-box">
      <div class="dsf-info-panel-title ellipsis van-hairline--top-bottom">
        <div class="dsf-info-panel-title-line"></div>
        {{name}}
      </div>
      <div
        v-if="showMore"
        class="dsf-info-panel-more van-hairline--top-bottom"
        @click.stop="more"
      >更多</div>
      <template v-else>
        <slot name="customer"></slot>
      </template>
    </div>
    <slot name="panel"></slot>
  </div>
</template>

<script>
export default {
  name: "dsfInfoPanel",
  data() {
    return {
      name: "",
      url: "",
      showMore: false,
      customer: false
    };
  },
  props: {
    panelData: {
      type: Object,
      default: function() {
        return {
          name: "", //标题
          url: "", //更多跳转的url
          showMore: true //是否显示更多
        };
      }
    }
  },
  computed: {},
  components: {},
  created: function() {
    this.name = this.panelData.name;
    this.url = this.panelData.url;
    this.showMore = this.panelData.showMore;
    this.customer = this.panelData.customer;
  },
  mounted: function() {},
  methods: {
    more() {
      console.log(this.url);
    }
  }
};
</script>
