<template>
  <DsfMobileNcDataGrid
    class="ac-specialList"
    height="100%"
    ref="dsfDatagrid"
    :http-param="datagrid_httpParam"
    :background-color="'#fff'"
    :col="col"
    :limit="limit"
    :hasLoad="hasLoad"
    :hasRefresh="hasRefresh"
    :margin="margin"
    :padding="padding"
  >
    <van-cell slot="default" slot-scope="datagrid_list" :key="datagrid_list.data._id">
      <div class="citem" @click="toDetail(datagrid_list.data._id)">
        <div class="imgbox">
          <van-image :src="setImg(datagrid_list.data)" fit="full" :width="'100%'" :height="'100%'">
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
            <template v-slot:error>
              <img :src="require('_nc/assets/styles/themes/img/file-read-18688.png')" style="width: 100%; height: 100%" />
            </template>
          </van-image>
          <div class="tnum ds_font7">
            <span>
              {{ stringafter(datagrid_list.data["nc_ztb_mhlbdata.pxrq_sdate"]) }}至{{ stringafter(datagrid_list.data["nc_ztb_mhlbdata.pxrq_edate"]) }}
            </span>
          </div>
        </div>
        <div class="rcontent">
          <div class="toptitle ds_font5_m">
            <p class="ell2">
              <span v-if="datagrid_list.data['nc_ztb_mhlbdata.sfbx'] == 1" class="s2 ds_font5">必修</span>
              {{ datagrid_list.data["nc_ztb_mhlbdata.title"] }}
            </p>
          </div>
          <div class="cencontent">
            <div class="txt">必修学时：{{ datagrid_list.data["nc_ztb_mhlbdata.bxxs"] }}</div>
            <div class="txt">选修学时：{{ datagrid_list.data["nc_ztb_mhlbdata.xxxs"] }}</div>
            <div class="p3 txt">学员人数：{{ datagrid_list.data["nc_ztb_mhlbdata.studentNum"] }}</div>
          </div>
        </div>
      </div>
    </van-cell>
  </DsfMobileNcDataGrid>
</template>
<script>
export default {
  name: "specialList",
  data() {
    return {
      datagrid_search: "",
      datagrid_httpParam: {
        query: '{"searchValue":""}',
        order: "[]",
        namespace: "nc.ztb",
        pageName: "mhlblist",
      },
    };
  },
  props: {
    detailUrl: {
      type: String,
      default: "",
    },
    courseList: {
      type: Array,
      default: () => {
        return [];
      },
    },
    col: {
      type: [String, Number],
      default: 1,
      desc: "一列显示几个，暂支持1和2",
    },
    hasLoad: {
      type: Boolean,
      default: true,
      desc: "是否有上拉加载",
    },
    hasRefresh: {
      type: Boolean,
      default: true,
      desc: "是否有下拉刷新",
    },
    dataSource: {
      type: String,
      default: "meta/list/data",
      desc: "请求接口，可自定义",
    },
    curr: {
      type: [String, Number],
      default: 1,
      desc: "从哪一页开始",
    },
    limit: {
      type: [String, Number],
      default: 10,
      desc: "每页显示多少",
    },
    httpParam: {
      type: Object,
      default: function () {
        return {};
      },
      desc: "接口参数",
    },
    padding: {
      type: Array,
      default() {
        return [0, 15, 0, 17];
      },
    },
    margin: {
      type: Array,
      default() {
        return [0, 0, 0, 0];
      },
    },
    detailRoute: {
      type: String,
      default: "",
    },
  },
  methods: {
    onSearch() {
      this.datagrid_httpParam.query = { searchValue: this.datagrid_search };
      this.$refs.dsfDatagrid.query();
    },
    toDetail(id) {
      this.$router.push({
        path: this.detailUrl,
        query: {
          id: id,
        },
      });
    },
    setImg(data) {
      let cover = data["nc_ztb_mhlbdata.cover"];
      if (cover) {
        cover = dsf.url.getWebPath(JSON.parse(cover)[0]["relativePath"], $$webRoot.nc);
      }
      return cover || "/";
    },
    stringafter(str) {
      let t = "";
      if (str) t = str.substr(5);
      return t;
    },
  },
};
</script>
