<template>
  <div style="min-height: 100%">
    <van-nav-bar :title="pageInfo.name" @click-left="$router.go(-1)" />
    <div class="live-detail-video" :class="pageInfo.state != 2 ? 'video-active' : ''">
      <!--预留放直播和视频播放控件-->
      <!-- <van-image v-if="pageInfo.state==2" width="100%" height="100%" fit="fill" :src="coverPath()" /> -->
      <van-image v-if="pageInfo.state == 2" width="100%" height="100%" fit="fill" :src="coverPath()">
        <template v-slot:loading>
          <van-loading type="spinner" size="20" />
        </template>
        <template v-slot:error>
          <img :src="require('_nc/assets/styles/themes/img/file-read-18688.png')" />
        </template>
      </van-image>
      <dsf-player v-if="pageInfo.state != 2" :videoSource="{ src: pageInfo.m3u8_play_url }" :cover-img="coverPath()"></dsf-player>
      <div class="video-info" v-if="pageInfo['zbrq_sdate'] && pageInfo.state == 2">
        <div>
          <i> 倒计时： </i>
          <span>{{ time.days }}</span>
          <i>:</i>
          <span>{{ time.hours }}</span>
          <i>:</i>
          <span>{{ time.minutes }}</span>
          <i>:</i>
          <span>{{ time.seconds }}</span>
        </div>
        <div>{{ pageInfo.yypeo || 0 }}人 预约</div>
      </div>
      <div class="video-online-num">
        {{ pageInfo.gkpeo || 0 }} 人{{ pageInfo.state == 3 ? "已观看" : "在线" }}
        <!-- <dsf-icon class="live-icon" @click="shareFun()" name="daochu" /> -->
      </div>
    </div>
    <dsf-panel class="live-detail-body" style="width: 100%" :panelData="{ name: '简介', showMore: false, customer: true }">
      <template v-slot:customer>
        <div class="live-detail-rate-warp">
          <van-rate
            class="live-detail-rate"
            v-model="pageInfo.average_fs"
            :size="18"
            allow-half
            color="#FD952A"
            void-icon="star"
            void-color="#eee"
            :readonly="rateReadonly"
            @change="rateChange"
          /><span>{{ showRate }}</span>
        </div>
      </template>
      <template v-slot:panel>
        <div class="live-detail-abstract">
          {{ pageInfo.abstract || "暂无简介" }}
        </div>
      </template>
    </dsf-panel>
    <div v-if="pageInfo.state == 2" @click="changeState(pageInfo.isyy_value == '1' ? '3' : '1')" class="live-detail-bottom">
      {{ pageInfo.isyy_value == "1" ? "已预约" : "立即预约" }}
    </div>
    <div v-else class="live-detail-bottom no-bg">
      <interaction ref="interaction" :id="$route.params.id" type="zb" :shareOpts="shareOpts"></interaction>
    </div>
  </div>
</template>

<script>
import { dsfRouteView } from "../dsfBase";
import interaction from "../interaction";

export default {
  name: "liveDetail",
  mixins: [dsfRouteView],
  components: { interaction },
  data() {
    return {
      timerId: null,
      active: 0,
      pageInfo: {},
      time: {
        days: "00",
        hours: "00",
        minutes: "00",
        seconds: "00",
      },
    };
  },
  computed: {
    showRate() {
      if (dsf.isUnDef(this.pageInfo.average_fs)) {
        return "0.0";
      } else {
        let n = Number(this.pageInfo.average_fs);
        return n.toFixed(1);
      }
    },
    coverPath() {
      return function () {
        let pageInfo = this.pageInfo;
        let cover = dsf.getUploadFileInfo(pageInfo.cover, 0);
        return cover.relativePath ? dsf.url.getWebPath(cover.relativePath, $$webRoot.nc) : "";
      };
    },
    tagsNum: function () {
      if (!this.pageInfo.tagsNum) {
        return 0;
      } else {
        let n = parseInt(this.pageInfo.tagsNum);
        return n < 0 ? 0 : n;
      }
    },
    collectNum: function () {
      if (!this.pageInfo.collectNum) {
        return 0;
      } else {
        let n = parseInt(this.pageInfo.collectNum);
        return n < 0 ? 0 : n;
      }
    },
    shareOpts: function () {
      return {
        title: "直播分享",
        content: this.pageInfo.name,
        url: window.location.href.split("#/")[0] + "#/shareLive/" + this.$route.params.id,
      };
    },
    rateReadonly() {
      if (dsf.isUnDef(this.pageInfo.nc_zbgl_jbxx_id)) {
        return true;
      } else {
        if (!this.pageInfo.average_fs) {
          return false;
        }
        return true;
      }
    },
  },
  created: function () {
    let _this = this;
    _this.initPage();
  },
  mounted: function () {},
  destroyed() {
    this.clearTimer();
  },
  methods: {
    shareFun() {
      // let pageInfo = this.pageInfo;
      // this.dsf.platform.share({
      //   url: window.location.href,
      //   title: pageInfo.name
      // })
    },
    //提交评价
    rateChange(value) {
      let that = this;
      let opts = {
        title: "评价",
        message: "确定当前评分为: " + value,
        asyncClose: true,
      };
      dsf.layer.confirm(
        opts,
        () => {
          that.evaluate(that.pageInfo.nc_zbgl_jbxx_id, value);
        },
        () => {
          console.log("评价取消");
        }
      );
    },
    changeState(status) {
      let loader = dsf.layer.loading();
      const userid = this.dsf.$user.loginInfo.userId;
      let _id = this.$route.params.id;
      let _this = this;
      this.$http
        .post("nc/mobile/tj/zb/zbOp", {
          nc_zbid: _id,
          nc_students_id: userid,
          optype: status,
        })
        .done((d) => {
          dsf.layer.closeLoading(loader);
          if (d.success) {
            _this.pageInfo.isyy_value = status;
            dsf.layer.message(status == "1" ? "预约成功" : "取消预约成功", true);
          } else {
            dsf.layer.message(d.message || "操作数据异常", false);
          }
        })
        .error((response) => {
          dsf.layer.closeLoading(loader);
          dsf.layer.message("服务器异常", false);
        });
    },
    initPage() {
      this.loadData((data) => {
        if (dsf.isUnDef(data.tagsNum)) {
          data.tagsNum = 0;
        }
        if (dsf.isUnDef(data.collectNum)) {
          data.collectNum = 0;
        }
        if (dsf.isUnDef(data.average_fs)) {
          data.average_fs = 0;
        }
        data.average_fs = data.average_fs * 1;
        this.pageInfo = data;
        this.timeDown();
      });
    },
    timeDown() {
      if (this.pageInfo.zbrq_sdate) {
        this.timerId = window.setInterval(() => {
          let zbsj = dsf.date.parse(this.pageInfo.zbrq_sdate, "yyyy-mm-dd hh:ii:ss");
          let currDate = new Date();
          let b = dsf.date.timeDown(currDate, zbsj);
          this.time = b;
          if (this.time.days == "00" && this.time.hours == "00" && this.time.minutes == "00" && this.time.seconds == "00") {
            this.clearTimer();
          }
        }, 1000);
      }
    },
    clearTimer() {
      if (this.timerId) {
        window.clearInterval(this.timerId);
      }
    },
    loadData(callback) {
      let loader = dsf.layer.loading();
      let _id = this.$route.params.id;
      let _this = this;
      this.$http
        .get("nc/mobile/tj/zb/zbdetail", {
          zbid: _id,
        })
        .done((d) => {
          dsf.layer.closeLoading(loader);
          if (d.success) {
            let _data = d.data;
            callback & callback(_data);
          } else {
            dsf.layer.message(d.message || "获取直播数据异常", false);
          }
        })
        .error((response) => {
          dsf.layer.closeLoading(loader);
          dsf.layer.message("服务器异常", false);
        });
    },
    evaluate(id, value, callback) {
      let rateUrl = "nc/mobile/tyzj/kcbf/editYwdf";
      let params = {
        ywid: id,
        ywlx_text: "直播",
        ywlx_value: "zb",
        fs: value,
      };
      let message = "";
      let loader = dsf.layer.loading();
      dsf.http
        .post(rateUrl, params)
        .done(function (res) {
          if (res.success) {
            message = "感谢您的评价";
          } else {
            message = res.message;
          }
        })
        .error(function (error) {
          message = "提交出现错误";
        })
        .always(function () {
          dsf.layer.closeLoading(loader);
          if (message) {
            window.setTimeout(() => {
              dsf.layer.message(message);
            }, 500);
          }
        });
    },
  },
};
</script>