<template>
  <div class="learn-detail">
    <div v-if="isDesign">专题详情</div>
    <div class="learn-detail-inner" :style="{ bottom: signBtn ? '45px' : 0 }">
      <div class="learn-cover">
        <van-image width="100%" height="100%" fit="fill" :src="setImg()">
          <template v-slot:loading>
            <van-loading type="spinner" size="20" />
          </template>
          <template v-slot:error>
            <img :src="require('_nc/assets/styles/themes/img/file-read-18688.png')" style="width:100%;"/>
          </template>
        </van-image>
        <!-- <dsf-icon name="zhuanfa" class="learn-relay" @click="repeatGo"></dsf-icon> -->
        <div class="learn-keyword-label">
          <div class="keyword-label-item" v-for="(item, index) in pageInfo.label" :key="index">{{ item }}</div>
        </div>
        <div
          class="learn-to"
          :class="signState == '3' ? 'out' : ''"
          v-if="signState == '1' || signState == '2' || signState == '3'"
          @click="continueGO()"
        >
          <span v-text="signState == '1' ? '开始学习' : signState == '2' ? '继续学习' : signState == '3' ? '已结业' : ''"></span>
        </div>
      </div>
      <van-tabs v-model="tabActive" @click="tabClick">
        <van-tab title="简介" name="简介">
          <div class="brief-wrap" :class="signBtn == true ? 'btn-blank' : ''">
            <div class="brief-inner">
              <div class="brief-content-item">
                <dsf-panel style="width: 100%; border-radius: 0" :panelData="{ name: '基本信息', url: '', showMore: false }">
                  <div slot="panel" class="course-item">
                    <div class="brief-title">{{ pageInfo.title }}</div>
                    <div class="brief-date-wrap">
                      <div class="brief-date-item">
                        报名日期：
                        <span>{{ pageInfo.bmrq_sdate }}</span> 至
                        <span>{{ pageInfo.bmrq_edate }}</span>
                      </div>
                      <div class="brief-date-item">
                        培训日期：
                        <span>{{ pageInfo.pxrq_sdate }}</span> 至
                        <span>{{ pageInfo.pxrq_edate }}</span>
                      </div>
                    </div>
                    <div class="brief-course-select-wrap">
                      <div class="brief-course-select-item orange">
                        <div class="course-select-icon-wrap">
                          <dsf-icon name="bixiuke" class="course-select-icon"></dsf-icon>
                          <div class="course-select-name">必修</div>
                        </div>
                        <div class="num-hour-class-wrap">
                          <div class="num">{{ pageInfo.bxcount }}</div>
                          <div class="unit">(门)</div>
                        </div>
                        <div class="sprit-wrap">
                          <div class="sprit"></div>
                        </div>
                        <div class="num-hour-class-wrap">
                          <div class="num">{{ pageInfo.bxxs }}</div>
                          <div class="unit">(学时)</div>
                        </div>
                      </div>
                      <div class="brief-course-select-item blue">
                        <div class="course-select-icon-wrap">
                          <dsf-icon name="xuanxiuke" class="course-select-icon"></dsf-icon>
                          <div class="course-select-name">选修</div>
                        </div>
                        <div class="num-hour-class-wrap">
                          <div class="num">{{ pageInfo.xxcount }}</div>
                          <div class="unit">(门)</div>
                        </div>
                        <div class="sprit-wrap">
                          <div class="sprit"></div>
                        </div>
                        <div class="num-hour-class-wrap">
                          <div class="num">{{ pageInfo.xxxs }}</div>
                          <div class="unit">(学时)</div>
                        </div>
                      </div>
                    </div>
                    <div class="brief-info-wrap">
                      <div class="brief-info">
                        学员人数：
                        <span>{{ pageInfo.studentNum }}</span
                        >人
                      </div>
                      <div class="brief-info">
                        主办单位：
                        <span>{{ pageInfo.zbdw }}</span>
                      </div>
                      <div class="brief-info">
                        承办单位：
                        <span>{{ pageInfo.cjdw }}</span>
                      </div>
                    </div>
                  </div>
                </dsf-panel>
              </div>
              <div class="brief-content-item">
                <dsf-panel style="width: 100%; border-radius: 0" :panelData="{ name: '教学目的', url: '', showMore: false }">
                  <div slot="panel" class="course-item">
                    <div class="brief-describe" v-html="pageInfo.content"></div>
                  </div>
                </dsf-panel>
              </div>
            </div>
          </div>
        </van-tab>
        <van-tab title="课程" name="课程">
          <div class="course-wrap" :class="signBtn == true ? 'btn-blank' : ''">
            <div class="course-inner" v-if="Object.keys(pageCourse).length > 0">
              <div class="course-mask" v-if="courseMask"></div>
              <dsf-panel
                style="width: 100%; border-radius: 0"
                v-for="(item, index) in pageCourse"
                :key="index"
                :panelData="{ name: item.dy_name, url: '', showMore: false }"
              >
                <div slot="panel" class="course-item">
                  <div class="course-notice">
                    <div>必修：</div>
                    <div class="course-notice-margin">
                      <span>{{ item.bxcount }}</span
                      >门
                    </div>
                    <div class="course-notice-margin">
                      <span>{{ item.bxxs }}</span
                      >学时
                    </div>
                    <div>选修：</div>
                    <div class="course-notice-margin">
                      <span>{{ item.xxcount }}</span
                      >门
                    </div>
                    <div class="course-notice-margin">
                      <span>{{ item.xxxs }}</span
                      >学时
                    </div>
                  </div>
                  <div
                    class="course-list-wrap"
                    v-for="(items, indexs) in item.CHILDREN"
                    :key="indexs"
                    @click="items.study == '0' && pageGo(items, item.nc_zyb_id)"
                  >
                    <div class="course-list-inner">
                      <div class="course-list-inner-lt">
                        <div class="course-list-top">
                          <dsf-icon
                            :name="
                              items.type_value == '02' ? 'lianxi' : items.type_value == '04' ? 'wenjuan' : items.type_value == '05' ? 'word' : ''
                            "
                            class="course-icon"
                            v-if="items.type_text !== '课程'"
                          ></dsf-icon>
                          <i :class="['icon', getIcon(items)]" v-else></i>
                          <div class="course-title" :class="{ ban: items.study == '1' }">{{ items.name }}</div>
                        </div>
                        <div class="course-list-bottom">
                          <div class="course-class-hour">
                            <span v-if="items.type_value == '05'">{{ items.xs }}学时</span>
                            <span v-else>{{ items.type_text }}</span>
                            <div v-if="items.sfbx_value == '1'" class="bxIcon">必修</div>
                          </div>
                          <div class="course-date" v-if="items.kcsc">{{ items.kcsc }}</div>
                        </div>
                      </div>
                      <div class="course-list-inner-rt">
                        <van-circle
                          :value="recharNum(items.wcjd)"
                          :rate="100"
                          :speed="50"
                          size="35px"
                          :stroke-width="80"
                          :text="items.wcjd"
                          layer-color="#ebedf0"
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </dsf-panel>
            </div>
          </div>
        </van-tab>
        <van-tab title="考核" name="考核" v-if="signState != '0' && signState != '4' && signState != '5'">
          <div class="examine-wrap" :class="signBtn == true ? 'btn-blank' : ''">
            <div class="examine-inner">
              <dsf-panel style="width: 100%; border-radius: 0" :panelData="{ name: '进度', url: '', showMore: false }">
                <div slot="panel" class="examine-item">
                  <template v-if="pageExamine.length > 0">
                    <div v-for="(item, index) in pageExamine" :key="index" class="examine-list">
                      <div class="examine-title">{{ item.name }}</div>
                      <div class="examine-progress-wrap">
                        <div class="examine-progress-item" v-show="item.totalBxxs && item.totalBxxs != '0'">
                          <div class="progress-item-lt">
                            <span class="progress-item-lt-key bx">必修</span>
                          </div>
                          <div class="progress-item-ct">
                            <van-progress
                              :percentage="formatterNum(item.bxjd)"
                              stroke-width="25"
                              track-color="#cccccc"
                              color="#FA5151"
                              :pivot-text="(item.bxjd || 0) + '%'"
                            />
                          </div>
                          <div class="progress-item-rt">
                            <span>{{ item.totalBxxsjl }}</span>
                            <span>/</span>
                            <span>{{ item.totalBxxs }}</span>
                          </div>
                        </div>
                        <div class="examine-progress-item" v-show="item.totalXxxs && item.totalXxxs != '0'">
                          <div class="progress-item-lt">
                            <span class="progress-item-lt-key xx">选修</span>
                          </div>
                          <div class="progress-item-ct">
                            <van-progress
                              :percentage="formatterNum(item.xxjd)"
                              stroke-width="25"
                              track-color="#cccccc"
                              color="#2A93FD"
                              :pivot-text="(item.xxjd || 0) + '%'"
                            />
                          </div>
                          <div class="progress-item-rt">
                            <span>{{ item.totalXxxsjl }}</span>
                            <span>/</span>
                            <span>{{ item.totalXxxs }}</span>
                          </div>
                        </div>
                      </div>
                    </div>
                  </template>
                </div>
              </dsf-panel>
              <div class="exam-wrap" :class="signBtn == true ? 'btn-blank' : ''" v-if="pageExamine.length > 0">
                <button :class="['exam-item-btn', asment.jdsfwc ? '' : 'btn-not-allow']" @click="gotoSum(asment.jdsfwc)">
                  <i class="icon iconfont icon-icon_shiyongwendang"></i>
                  小结
                </button>
                <button :class="['exam-item-btn', asment.jdsfwc ? '' : 'btn-not-allow']" @click="gotoExam(asment)">
                  <i class="icon icon-wenjuan iconfont"></i>
                  <span>结业考试</span>
                </button>
              </div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
    <div
      class="sign-up dsf-bottom"
      v-if="signBtn"
      :class="signState == '4' ? 'seal' : signState == '5' ? 'out' : ''"
      @click="signState == '0' && signOn(pageInfo.title)"
    >
      <div class="sign-up-content">
        <span v-text="signState == '0' ? '立即报名' : signState == '4' ? '封闭式' : signState == '5' ? '未授权' : ''" />
        <span v-if="signState == '0' && pageInfo.bmxz_value == '1'">
          （剩余
          <span>{{ pageInfo.systudent }}</span
          >人）
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import dsfPanel from "_nc/mobile/base/dsfPanel";
export default dsf.component({
  mixins: [$mixins.control],
  name: "DsfMobileNcSpecilaDetail",
  ctrlCaption: "专题详情",
  data() {
    return {
      progressList: {},
      nummm: 30,
      currentRate: 100,
      tabActive: "",
      pageInfo: {
        cover: null, //relativePath封面照
        label: [], //关键词
        title: "", //标题
        bmrq_sdate: "", //报名开始时间
        bmrq_edate: "", //报名结束事件
        pxrq_sdate: "", //培训开始时间
        pxrq_edate: "", //培训结束事件
        bxcount: "", //必修总门数
        bxxs: "", //必修学时
        xxcount: "", //选修总门数
        xxxs: "", //选修学时
        studentNum: "", //学员人数
        zbdw: "", //主办单位
        cjdw: "", //承建单位
        abstract: "", //教学目的
        bmxz_value: "", //报名是否限制人数， 1限制 0不限制
        systudent: null, //剩余学员数
      },
      pageCourse: [],
      courseMask: true,
      signBtn: true,
      nc_zyb_id: "",
      signState: "",
      pageExamine: {},
      continueLearn: "",
      ztbId: this.$route.query.id,
      userId: "",
      asment: "",
    };
  },
  created() {
    // let source ="ncLogin/userLogin";
    // let params = {
    //   userName: "ncadmin",
    //   password: dsf.md5(dsf.md5("111111")),
    //   code: "good",
    // };
    // this.dsf.http
    //   .post(source, params,$$webRoot.nc)
    //   .done((d) => {})
    //   .error((response) => {});

    if (!this.isDesign) {
      this.initInfo();
      this.initCoursePanel();
    }
  },
  mounted() {},
  methods: {
    tabClick(name, title) {
      // console.log(title);
      // this.$store.commit("Special_tab_name", title);
    },
    formatterNum(img) {
      return parseInt(img) > 100 ? 100 : parseInt(img);
    },
    setImg(key) {
      let cover = this.pageInfo.cover || null;
      if (cover) {
        cover = dsf.url.getWebPath(JSON.parse(cover)[0][key || "relativePath"], $$webRoot.nc);
      }
      return cover || "";
    },
    continueGO() {
      let _this = this;
      _this.$router.push({
        path: "/player/" + _this.continueLearn + "/2",
      });
    },
    repeatGo() {
      let _this = this;
      this.dsf.platform.share({
        url: window.location.href.split("#/")[0] + "#/shareSpecial/" + this.$route.query.id,
        title: "专题班分享",
        content: this.pageInfo.title,
        image: _this.setImg("absolutePath"),
      });
    },
    recharNum(data) {
      let wcjd = data;
      wcjd = wcjd * 1;
      return wcjd;
    },
    pageGo(res, id) {
      let _this = this;
      if (res.type_text == "试卷") {
        let _params = {
          examId: res.relid,
          userId: _this.userId,
          deviceType: "mobile",
          businessId: res.bussinesid,
          entryType: "1",
        };
        let loader = _this.dsf.layer.loading();
        _this.dsf.http
          .get("nc/mobile/tyzj/exam/url", _params,$$webRoot.nc)
          .done((d) => {
            _this.dsf.layer.closeLoading(loader);
            if (d.success) {
              _this.$router.push({
                name: "dsfIframe",
                params: { url: dsf.config.webRoot + d.data.relativePath },
              });
            } else {
              _this.dsf.layer.message(d.message || "获取列表数据异常", false);
            }
          })
          .error((response) => {
            _this.dsf.layer.closeLoading(loader);
            _this.dsf.layer.message("服务器异常", false);
          });
      } else if (res.type_text == "问卷") {
        let loader = _this.dsf.layer.loading();
        let param = {
          wjid: res.relid,
          type: "mobile",
          ztbid: _this.ztbId,
        };
        _this.dsf.http
          .get("nc/mobile/tyzj/wj/tbWj", param,$$webRoot.nc)
          .done((d) => {
            _this.dsf.layer.closeLoading(loader);
            if (d.success) {
              _this.$router.push({
                path: "/iframe?url=" + d.data.fullPath.replace("&isview", "&readonly"),
              });
            } else {
              _this.dsf.layer.message(d.message || "获取跳转链接异常", false);
            }
          })
          .error((response) => {
            _this.dsf.layer.closeLoading(loader);
            _this.dsf.layer.message("服务器异常", false);
          });
      } else if (res.type_text == "课程") {
        _this.$router.push({
          path: "/player/" + res.relid + "/2",
        });
      } else if (res.type_text == "作业") {
        _this.dsf.layer.message("暂无", false);
      }
    },
    signOn(message) {
      let _this = this;
      if ((_this.pageInfo.bmxz_value == 1 && _this.pageInfo.systudent > 0) || _this.pageInfo.bmxz_value == 0) {
        //开始报名
        let loader = _this.dsf.layer.loading();
        _this.dsf.http
          .post("nc/mobile/tj/ztb/addXymdBm", {
            nc_zyb_id: _this.nc_zyb_id,
          }, $$webRoot.nc)
          .done((d) => {
            _this.dsf.layer.closeLoading(loader);
            if (d.success) {
              let timer = setTimeout(() => {
                _this.initInfo();
                _this.initCoursePanel();
                clearTimeout(timer);
              }, 1500);
              _this.dsf.layer.message(d.message, true);
            } else {
              _this.dsf.layer.message(d.message || "报名失败", false);
            }
          })
          .error((response) => {
            _this.dsf.layer.closeLoading(loader);
            _this.dsf.layer.message("服务器异常", false);
          });
        // _this.$dialog
        //   .confirm({
        //     title: "您是否确认报名",
        //     message: message,
        //     className: "signCustom",
        //     /*confirmButtonColor: '#FF6A00'*/
        //   })
        //   .then(() => {
        //     let loader = _this.dsf.layer.loading();
        //     _this.dsf.http
        //       .post("nc/mobile/tj/ztb/addXymdBm", {
        //         nc_zyb_id: _this.nc_zyb_id,
        //       },$$webRoot.nc)
        //       .done((d) => {
        //         _this.dsf.layer.closeLoading(loader);
        //         if (d.success) {
        //           let timer = setTimeout(() => {
        //             _this.initInfo();
        //             _this.initCoursePanel();
        //             clearTimeout(timer);
        //           }, 1500);
        //           _this.dsf.layer.message(d.message, true);
        //         } else {
        //           _this.dsf.layer.message(d.message || "报名失败", false);
        //         }
        //       })
        //       .error((response) => {
        //         _this.dsf.layer.closeLoading(loader);
        //         _this.dsf.layer.message("服务器异常", false);
        //       });
        //   })
        //   .catch(() => {
        //     console.log("点击了取消");
        //   });
      } else {
        _this.dsf.layer.message("报名人数已达上限", false);
      }
    },
    initInfo() {
      let _this = this;
      let loader = _this.dsf.layer.loading();
      dsf.http
        .get("nc/mobile/tyzj/ztb/getZtbInfo", {
          id: _this.ztbId,
        }, $$webRoot.nc)
        .done((d) => {
          _this.dsf.layer.closeLoading(loader);
          if (d.success) {
            let _data = d.data;
            let _info = {
              cover: _data.cover, //?JSON.parse(_data.cover)[0].relativePath:null,//relativePath封面照
              label: _data.label_text ? _data.label_text.split("^") : [], //关键词
              title: _data.title, //标题
              bmrq_sdate: _data.bmrq_sdate, //报名开始时间
              bmrq_edate: _data.bmrq_edate, //报名结束事件
              pxrq_sdate: _data.pxrq_sdate, //培训开始时间
              pxrq_edate: _data.pxrq_edate, //培训结束事件
              bxcount: _data.bxcount, //必修总门数
              bxxs: _data.bxxs, //必修学时
              xxcount: _data.xxcount, //选修总门数
              xxxs: _data.xxxs, //选修学时
              studentNum: _data.studentNum, //学员人数
              zbdw: _data.zbdw, //主办单位
              cjdw: _data.cjdw, //承建单位
              content: _data.content, //教学目的
              bmxz_value: parseInt(_data.bmxz_value), //报名是否限制人数， 1限制 0不限制
              systudent: parseInt(_data.systudent), //剩余学员数
            };
            _this.nc_zyb_id = _data.nc_zyb_id; //专业版id
            _this.signState = _data.STATE; //按钮状态
            _this.signState == "0" || _this.signState == "4" || _this.signState == "5" ? (_this.signBtn = true) : (_this.signBtn = false);
            _this.signState == "1" || _this.signState == "2" ? (_this.courseMask = false) : (_this.courseMask = true);
            _this.pageInfo = _info;
            console.log(_data);
            if (_this.signState != "0" && _this.signState != "5" && this.signState != "") {
              this.initExamine();
            }
            _this.continueLearn = _data.newKcId;
          } else {
            _this.dsf.layer.message(d.message || "获取列表数据异常", false);
          }
        })
        .error((response) => {
          _this.dsf.layer.closeLoading(loader);
          _this.dsf.layer.message("服务器异常", false);
        });
    },
    initCoursePanel() {
      let _this = this;
      let loader = _this.dsf.layer.loading();
      this.$http
        .get("nc/mobile/tyzj/ztb/getZtbKcList", {
          ztbid: _this.ztbId,
          userId: _this.userId,
        },$$webRoot.nc)
        .done((d) => {
          _this.dsf.layer.closeLoading(loader);
          if (d.success) {
            let _data = d.data.data;
            console.log(_data);
            _this.pageCourse = _data;
          } else {
            _this.dsf.layer.message(d.message || "获取列表数据异常", false);
          }
        })
        .error((response) => {
          _this.dsf.layer.closeLoading(loader);
          _this.dsf.layer.message("服务器异常", false);
        });
    },
    initExamine() {
      let _this = this;
      let loader = _this.dsf.layer.loading();
      this.$http
        .get("nc/ztb/tea/getStudentZtbKh", {
          ztbid: _this.ztbId,
          userId: _this.userId,
        },$$webRoot.nc)
        .done((d) => {
          _this.dsf.layer.closeLoading(loader);
          if (d.success) {
            let _data = d.data.dyjdData;
            console.log(d.data);
            _this.asment = d.data;
            _this.pageExamine = _data;
          } else {
            _this.dsf.layer.message(d.message || "获取列表数据异常", false);
          }
        })
        .error((response) => {
          _this.dsf.layer.closeLoading(loader);
          _this.dsf.layer.message("服务器异常", false);
        });
    },
    //小结
    gotoSum(isSum) {
      if (isSum) {
        dsf.layer.message("小结请去PC端操作", false);
      }
    },
    gotoExam(item) {
      if (!item.jdsfwc) return;
      let that = this;
      let _params = {
        examId: item.ksid,
        userId: that.userId,
        deviceType: "mobile",
        businessId: that.ztbId,
        entryType: "1",
      };
      let loader = dsf.layer.loading();
      that.dsf.http
        .get("nc/mobile/tyzj/exam/url", _params,$$webRoot.nc)
        .done((d) => {
          dsf.layer.closeLoading(loader);
          if (d.success) {
            that.$router.push({
              name: "dsfIframe",
              params: { url: dsf.config.webRoot + d.data.relativePath },
            });
          } else {
            dsf.layer.message(d.message || "获取列表数据异常", false);
          }
        })
        .error((response) => {
          dsf.layer.closeLoading(loader);
          dsf.layer.message("服务器异常", false);
        });
    },
    getIcon(item) {
      let text = item.type_text;
      let type = item.kcflryvalue || "1";
      let iconArr = ["icon-sp", "icon-yp", "icon-wg", "icon-fmt", "icon-qt"];
      if (text == "课程") {
        let t = this.removeEmpty(type.split(","));
        if (t && t.length == 1) {
          return iconArr[t[0] - 1];
        } else {
          return iconArr[0];
        }
      }
    },
    removeEmpty(arr) {
      for (var i = 0; i < arr.length; i++) {
        if (arr[i] == "" || typeof arr[i] == "undefined") {
          arr.splice(i, 1);
          i = i - 1;
        }
      }
      return arr;
    },
  },
  components: {
    dsfPanel,
  },
});
</script>